import { Component, Output, EventEmitter, Input, SimpleChanges, OnChanges, OnDestroy } from "@angular/core";
import { ViewController, NavController, Events } from "ionic-angular";
import { AppConfig } from "@app/app.config";
import { AppMode } from "../../enums/app-mode.enum";
import moment from 'moment'
import { CONSTANTS } from "@common/constants";
import { TimeService } from "@providers/time.service";
import { AlertBoxComponent } from "../alert-box/alert-box";
import { AppEvent } from "@common/appEvent";
import { BluetoothService } from "@providers/bluetooth/bluetooth.service";
import { Resevervation } from "@models/reservation";
@Component({
    selector: 'card-resever',
    templateUrl: './card-resever.html'
})
export class CarResevervation implements OnChanges, OnDestroy {

    @Input() reservation: Resevervation
    @Input() mode: any
    @Input() modeDetail: any
    @Input() bluetooth: any = false

    @Output() event: EventEmitter<any> = new EventEmitter()
    @Output() rentalDetail: EventEmitter<any> = new EventEmitter()
    @Output() eventShowInfo: EventEmitter<any> = new EventEmitter()
    @Output() eventExtend: EventEmitter<any> = new EventEmitter()
    @Output() eventRental: EventEmitter<any> = new EventEmitter()
    @Output() eventLock: EventEmitter<any> = new EventEmitter()
    @Output() eventUnlock: EventEmitter<any> = new EventEmitter()
    @Output() eventBluetooth: EventEmitter<any> = new EventEmitter()
    @Output() eventChangeReservation: EventEmitter<any> = new EventEmitter()
    private appMode = AppMode
    private imageUrl: string
    position: any
    expiredAllowExtend : boolean;
    private startTime;
    private endTime;
    private isDisabled: boolean = false
    private isClick = false;
    message = '<p>This feature allows you to lock/unlock your car door directly from app.</p> Please make sure you have enabled mobile data or bluetooth.'
    constructor(public viewCtrl: ViewController,
        public appConfig: AppConfig,
        public navCtrl: NavController,
        public timeService: TimeService,
        public alertBoxComponent: AlertBoxComponent,
        public events: Events,
        public bluetoothService: BluetoothService) {
        events.subscribe(AppEvent.EventBluetooth, (result) => {
            this.bluetooth = result
        })
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.startTime = moment(this.reservation.start_time).format(CONSTANTS.RESERVATION_TIME_FORMAT)
        this.endTime = moment(this.reservation.end_time).format(CONSTANTS.RESERVATION_TIME_FORMAT)
        this.timeExpiredAllow();
        if (changes.reservation && changes.reservation.currentValue) {
            this.imageUrl = `${this.appConfig.carImageUrl}/${this.reservation.car_type_id ? this.reservation.car_type_id : null}/${this.reservation.photo ? this.reservation.photo : null}`
        }

        this.position = {
            lat: Number(this.reservation.building_lat),
            lng: Number(this.reservation.building_lng)
        }



    }
    showInfo(event) {
        event.stopPropagation()
        this.eventShowInfo.emit(this.reservation)

    }
    extend(event) {
        event.stopPropagation()
        this.eventExtend.emit(this.reservation)

    }
    rental(event) {
        event.stopPropagation()
        this.eventRental.emit(this.reservation)
    }
    lock(event) {
        if (this.isClick == false) {
            setTimeout(() => { this.isClick = false }, 5000);
            this.isClick = true;
            this.eventLock.emit(this.reservation)
        }
    }
    unlock(event) {
        if (this.isClick == false) {
            setTimeout(() => { this.isClick = false }, 5000);
            this.isClick = true;
            this.eventUnlock.emit(this.reservation)
        }
    }
    checkBluetooth(event) {
        this.eventBluetooth.emit(this.reservation)
    }
    smartKeyInfor() {
        this.alertBoxComponent.createInfoAlertInfor('SmartKey', null, this.message).present();
    }

    changeReservation() {
        if (this.mode != this.appMode.DETAIL) {
            this.eventChangeReservation.emit(this.reservation)
        }
    }

    ngOnDestroy() {
        this.events.unsubscribe(AppEvent.EventBluetooth)
    }

    get isCanChange() {
        let now = moment()
        let isAfter = moment(this.reservation.start_time).isAfter(now)
        let diff = moment(this.reservation.start_time).diff(now)
        let duration = moment.duration(diff).asMinutes()
        return isAfter && duration > 15 //15min
    }

    timeExpiredAllow() {
        let now = moment();
        let overTime = now.diff(moment(this.reservation.end_time))
        let duration = moment.duration(overTime).asHours()
        if (duration <= 5) {
            this.expiredAllowExtend = true;
        }
    }
}


