import { Component, OnDestroy } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Events } from 'ionic-angular/util/events';
import { ViewController, AlertController } from "ionic-angular";
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { EndReport } from '../end-report/end-report';
import { ReportDamagePage } from '../report-damage/report-damage';
import { ReservationExtendPage } from '../../resevation/reservation-extend/reservation-extend';
import { RentalDetailProvider } from '@providers/rental-detail/rental-detail';
import { CONSTANTS } from "@common/constants";
import * as moment from 'moment'
import { BluetoothService } from '@providers/bluetooth/bluetooth.service';
import { SmsService } from '@providers/sms/sms.provider';
import { ModalController } from "ionic-angular";
import * as _ from 'lodash';
import { GoCarToast } from '@shared/components/toast/gocar-toast';
import { DeviceState, GocarDevice, BleDevice } from '@providers/bluetooth/ble-device.service';
import { BluetoothLe } from 'ionic-native-bluetoothle';
import { GlobalService } from '@providers/global.provider';
import { Subscription } from 'rxjs';
import { ReservationMode } from '@shared/enums/reservation-mode.enum';


@Component({
  selector: 'during-trip',
  templateUrl: 'during-trip.html',
})
export class DuringTrip implements OnDestroy {
  value: any;
  step: any = 2;
  mode: ReservationMode
  reservation: any;
  buildingName: any;
  dateFrom: any;
  dateTo: any;
  service: any = this;
  fuelValue: any = 'start_fuel_level'
  startTime: any
  endTime: any
  pinCode: any
  isBluetooth = false;
  isFiring: boolean = false
  isDisabledBluetooth: boolean = false
  stateSubcription: Subscription
  deviceName: BleDevice = null;
  serviceReservation: any;
  private isClick = false;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public events: Events,
    public viewCtrl: ViewController,
    public goCarModal: GoCarModal,
    public gocarToast: GoCarToast,
    public ble: BluetoothLe,
    public rentalDetailProvider: RentalDetailProvider,
    private alertCtrl: AlertController,
    private globalService: GlobalService,
    private bluetoothService: BluetoothService,
    private smsService: SmsService,
    public modalCtrl: ModalController) {
    this.step = 2;
    this.reservation = navParams.get('data')
    this.serviceReservation = navParams.get('service')
    this.mode = navParams.get('mode') || ReservationMode.DRIVING
    this.deviceName = this.mode == ReservationMode.DRIVING ? this.globalService.getBLE() : this.globalService.getBLEExpired()
    this.buildingName = this.reservation.building_name;
    this.dateFrom = this.convertDate(moment.utc(this.reservation.start_time).format(CONSTANTS.RENTALDETAIL_TIME_FORMAT))
    this.dateTo = this.convertDate(moment.utc(this.reservation.end_time).format(CONSTANTS.RENTALDETAIL_TIME_FORMAT))
    this.startTime = moment.utc(this.reservation.start_time).format(CONSTANTS.RENTALDETAIL_TIME_FORMAT);
    this.endTime = moment.utc(this.reservation.end_time).format(CONSTANTS.RENTALDETAIL_TIME_FORMAT);
    this.pinCode = this.reservation.unlock_code_start_id;
    

    if(this.deviceName) {
      this.isBluetooth = this.deviceName.currentState == DeviceState.DISCOVERED
      this.subcribeStateChange()
    }
  }

  ionViewDidLoad() {

  }

  ngOnDestroy() {
    this.unsubcribeStateChange()
  }

  unsubcribeStateChange() {
    if(this.stateSubcription) {
      this.stateSubcription.unsubscribe()
    }
  }
  close() {
    this.serviceReservation.initData()
    this.viewCtrl.dismiss(this.reservation)
  }

  back() {
    this.goCarModal.showModalV2(ReportDamagePage, { data: this.reservation, service: this.serviceReservation })
  }

  endTrip() {
    if (_.isNull(this.reservation.start_fuel_level) || this.reservation.start_fuel_level == 0) {
      this.alertError();
    } else {
      this.goCarModal.showModalV2(EndReport, { data: this.reservation, service: this.serviceReservation })
    }
  }

  alertError() {
    let alert = this.alertCtrl.create({
      title: 'Invalid',
      message: 'Please confirm starting fuel level!',
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }

  alertMoreInfor() {
    let alert = this.alertCtrl.create({
      title: 'Bluetooth SmartKey',
      cssClass: 'title-moreinfo',
      message: '<p>This allows you to lock and unlock the car directly from our app.</p>'
        + '<p>Please make sure you have enabled mobile data or Bluetooth.</p>',
      buttons: [
        {
          text: 'Okay',
          cssClass: 'more-infor-text',
          role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }

  convertDate(data) {
    let date = new Date(data)
    let dataLst = String(date).split(" ");
    return " " + dataLst[2] + " " + dataLst[1] + " " + dataLst[3] + ", " + this.addZero(date.getHours()) + ":" + this.addZero(date.getMinutes());
  }

  extend(event) {
    this.goCarModal.showModalWithoutDismiss(ReservationExtendPage, { data: this.reservation })
    this.goCarModal.currentModal.onDidDismiss((data) => {
    if(data) {
      this.endTime = moment.utc(data).format(CONSTANTS.RENTALDETAIL_TIME_FORMAT);
      this.dateTo = this.convertDate(moment.utc(data).format(CONSTANTS.RENTALDETAIL_TIME_FORMAT))
    }
    })
    // this.goCarModal.currentModal.onDidDismiss(data => {
    //   this.navCtrl.setRoot(DuringTrip, { data: this.reservation, service: this.serviceReservation})
    // })
  }
  addZero(data) {
    if (data < 10) {
      return "0" + data;
    } else {
      return data;
    }
  }

  updateFuelLevel(reservation) {
    this.rentalDetailProvider.updateRentalDetail(reservation, new Date(), 'DURING_TRIP').subscribe(rs => {
      this.rentalDetailProvider.getRentalDetailById(this.reservation.id).subscribe(rs => {
        this.reservation = rs.data;
      })
    })
  }

  eventLock() {
    if(!this.isFiring) {
      this.isFiring = true
      setTimeout(() => { this.isFiring = false }, 5000);
      if (this.deviceName && this.deviceName.currentState == DeviceState.DISCOVERED) {
        this.deviceName.lockDevice()
      } else {
        this.smsService.lockCar(this.reservation.id).subscribe(val => {
          console.log(val)
        }, err => {
          console.log(err)
        })
      }
    }
  }

  eventUnlock() {
    if(!this.isFiring) {
      this.isFiring = true
    setTimeout(() => { this.isFiring = false }, 5000);
    if (this.deviceName && this.deviceName.currentState == DeviceState.DISCOVERED) {
      this.deviceName.unlockDevice()
     } else {
       this.smsService.unlockCar(this.reservation.id).subscribe(val => {
         console.log(val)
       }, err => {
         console.log(err)
       })
     }
    }
  }

  initialize() {
    this.ble.initialize().take(1).subscribe(result => {
      console.log(`bluetooth status: ${result.status}`)
      this.scan()
    })
  }

  scan() {
    let scanSubscription = this.ble.startScan().subscribe(result => {
      console.log(JSON.stringify(result))
      if (result.status === 'scanResult') {
        if (result.name == this.reservation.bluetooth_address) {
            clearTimeout(scanTimeout)
            this.ble.stopScan().then(() => {
              console.log('scanStopped')
              let device : GocarDevice = {
                address: result.address,
                characteristic: this.reservation.bluetooth_characteristic,
                service: this.reservation.bluetooth_service 
              }
              this.connect2(device)
              scanSubscription.unsubscribe()
            })
        }
      } else if (result.status === 'scanStarted') {
        console.log('scan started')
      } else {
        // TODO handle error
        this.isDisabledBluetooth
        console.log('start scan error: ' + JSON.stringify(result))
      }
    }, err => {
      console.log('start scan error 1', err)
      this.gocarToast.showToast({message: err.message})
      this.isDisabledBluetooth = false
      this.isBluetooth = false
      setTimeout(() => {
        this.isBluetooth = false
      })
      clearTimeout(scanTimeout) 
    })
  let scanTimeout =  setTimeout(() => {
    this.smsService.resetCar(this.reservation.id).subscribe((data) => {
      console.log('Car is unlocked', data)
    }, err => {
      console.log('cannot unlocked', err)
    })
      this.ble.stopScan().then(() => {
        console.log('scanStopped')
        scanSubscription.unsubscribe()
        this.isDisabledBluetooth = false
        this.gocarToast.showToast({message: 'Cannot find your car'})
        this.isBluetooth = false
      })
    }, 5000)
  }

  eventBluetooth() {
   if(!this.isClick) {
     this.isClick = true // to prevent event onchange from check bluetooth
      this.isDisabledBluetooth = true
      if (this.isBluetooth) { 
        this.initialize()
      } else {
        this.disconnect2()
      }
   }
  }

  disconnect2() {
    return this.deviceName.disconnect().then(()=> {
      this.isBluetooth = false
      this.isClick = false
    }).catch((error) => { 
      console.log(JSON.stringify(error))
       this.isBluetooth = false
       this.isClick = false
      })
  }

  connect2(device: GocarDevice) {
    this.deviceName =  new BleDevice(this.ble, null, device)
    if(this.mode == ReservationMode.DRIVING) {
      this.globalService.setBLE(this.deviceName)
    } else {
      this.globalService.setBLEExpired(this.deviceName)
    }
    this.subcribeStateChange()
    this.deviceName.isconnected().then((result) => {
      console.log('reservation-driving isconnected.then', result)
      if(result.isConnected == false) {
        this.deviceName.reconnect()
      }
    }).catch((err) => {
      console.log('reservation-driving isconnected.catch', err)
       this.deviceName.connect()
    })
  }

  subcribeStateChange() {
    this.stateSubcription = this.deviceName.stateChangeEvent.subscribe((data) => {
      console.log('state changed', data)
      if(data.newState == DeviceState.DISCOVERED) {
        this.gocarToast.showToast({message: 'Connected'})
        this.isDisabledBluetooth = false;
        this.isClick = false
      } else if(data.newState == DeviceState.DISCONNECTED) {
        // this.gocarToast.showToast({message: 'Disconnected'})
        if(this.isClick) {
          this.isDisabledBluetooth = false;
          this.isBluetooth = false
          this.isClick = false
        }
      } else if(data.oldState != DeviceState.DISCONNECTED && data.newState == DeviceState.CLOSED) {
        // this.gocarToast.showToast({message: 'Disconnected'})
        if(this.isClick) {
          this.isBluetooth = false
          this.isDisabledBluetooth = false;
          this.isClick = false
        }
      }
    }, (err) => {
      console.log('state changed err', err)
    })
  }

}



