import { Component, OnInit, ViewChild, AfterViewInit, ElementRef } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConfig } from '@app/app.config';
import { AngularFireDatabase } from 'angularfire2/database';
import {Observable} from 'rxjs/Observable';
import {PaymentMethodsService} from '@providers/paymentMethods/paymentMethods.provider';
import { GoCarToast } from '@shared/components/toast/gocar-toast';

/**
 * Generated class for the AddCardPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-charge-card',
  templateUrl: 'charge-card.html',
})

export class ChargeCardPage implements OnInit ,AfterViewInit  {
  
  @ViewChild('form') postForm: ElementRef;

  private endpoint: any;
  public userData : any;
  //public transactionStatus: AngularFireDatabase<any>;
  // public items: Observable<any[]>;
  public items: Observable<any[]>;
  public refNo : String;


  constructor(private http: Http ,public navCtrl: NavController, public viewCtrl: ViewController, private appConfig :AppConfig,
     public navParams: NavParams , public afDatabase: AngularFireDatabase , public paymentMethodsService : PaymentMethodsService,
     public gocarToast : GoCarToast
    ) {

      this.endpoint = this.appConfig.ipayChargePaymentMethodsUrl; 

      this.userData = this.navParams.get('userData');
      console.log(this.userData);
      //this.userData = null;

      this.afDatabase.object('/ipay88Transaction/'+this.userData.refNo).snapshotChanges().subscribe((snapshot) =>{
        console.log("snapshot change" , snapshot.payload.val() );
        if(snapshot.payload.val() == "DONE"){
          this.viewCtrl.dismiss(true);
          //Payment SUCCESS
        }else if(snapshot.payload.val() == "ERROR"){
          // Payment FAILED
          this.viewCtrl.dismiss(false);
          //gocarToast.showToast({ duration: 4000, message:"Failed to make payment. Please try again." })

        }

      });

  }
    
  ngOnInit() {
    console.log('ngOnInit triggered.');
    console.log(this.navParams.get('userData'));

    
  }

  ngOnChanges(userData) {
    console.log(userData);
}

  ngAfterViewInit() {
      console.log('ngAfterViewInit triggered.');
      
      this.postForm.nativeElement.submit();
  }

  
  submitForm($event): boolean {
   // $event.stopPropagation();
    console.log("submit form");
    return false;
  }

  onLoad() {
    console.log('onLoad triggered.');
    //this.postForm.nativeElement.submit();

  }

  close() {
    this.viewCtrl.dismiss(false);
  }

  
}



