import { Injectable } from "@angular/core";
import { BaseService } from "../base.service";
import { Http, Response } from "@angular/http";
import { AppConfig } from "@app/app.config";
import { Storage } from '@ionic/storage';
import { Observable } from "rxjs/Observable";
import { AuthProvider } from "../auth/auth";
import { PayMentMethod } from "@models/payment-method";

@Injectable()
export class PaymentMethodsService extends BaseService {
    constructor(public http: Http, 
                public authService: AuthProvider,
                protected appConfig: AppConfig, 
                protected storage: Storage) {
        super(http, appConfig);
    }

    public generateRefNo() {
        return this.getData('payment_methods/generate-ref-no').catch((err) => {
            return Observable.throw(err);
          });
    }

    // public getUserPaymentMethods(){
    //     return this.getData('payment_methods/get-payment-methods')
    //     .catch((err)=>{
    //         return Observable.throw(err);
    //     })   
    // }
    public getUserPaymentMethods(){
        return this.postData('payment_methods/get-payment-methods')
        .map((res: Response) => {
            const result = res.json()
            let data = result.data
            let paymentMethods: PayMentMethod[] = []
            for(let item of data) {
                paymentMethods.push(new PayMentMethod(item))
            }
            return paymentMethods
        }).catch((err)=>{
            return Observable.throw(err);
        })   
    }

    public remove(id): Observable<any> {
        let body = {
            methodId: id,
            userId: this.authService.userId
        }
        return this.putData('payment_methods/delete', body).catch(err => {
            return Observable.throw(err)
        })
    }

    
}


