import { Component, ViewChild, NgZone, Output, EventEmitter, Renderer, AfterViewInit, trigger, state, OnDestroy } from '@angular/core';
import { NavController, Events, Content, Platform, Slides } from 'ionic-angular';
import { AppEvent } from '@common/appEvent';
import { CONSTANTS } from "@common/constants";
import * as _ from 'lodash';
import { CarsProvider } from '@providers/cars/cars';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { ViewInfoComponent } from '@shared/components/view-info/view-info';
import { GlobalService } from "@providers/global.provider";
import { GoCarToast } from '@shared/components/toast/gocar-toast';
import { AuthProvider } from '@providers/auth/auth';
import { BookCarPage } from '../book-car/book-car';
import { UtilsService } from '@providers/utils.service';
import { UserPage } from '../user/user';
import { FiltersProvider } from '@providers/fiters/filters';
import { LoadingComponent } from '@shared/components/loading/loading';

import {
  GoogleMaps,
  GoogleMap,
  GoogleMapsEvent,
  ILatLng,
  MyLocation
} from '@ionic-native/google-maps';
import { BuidingsProvider } from '@providers/building/building';
import { style, transition, animate } from '@angular/core';
import { Building } from '@models/building';

@Component({
  selector: 'page-test-map',
  templateUrl: 'test-map.html',
  animations: [
    trigger('openClose', [
      state('true', style({ height: '*' })),
      state('false', style({ height: '0px' })),
      transition('false <=> true', animate(300))
    ])
  ]
})
export class TestMapPage implements AfterViewInit, OnDestroy {

  @Output() markerClick: EventEmitter<any> = new EventEmitter()
  @ViewChild(Content) content: Content;
  @ViewChild('map_canvas') map_canvas: any
  @ViewChild('list') list: any
  @ViewChild('carfilter') carfilter: any
  @ViewChild('slides') slides: Slides;

  building;
  open: boolean = false
  map: GoogleMap;
  mapReady: boolean = false;
  listBuilding: any;
  points: any[] = [];
  markersInit: boolean = false
  ZOOM_LEVEL: number = 13
  public cars = [];
  public currentBuildingName: string = ''
  public currentBuildingId: string = ''
  public isOpen: boolean = false
  public isLoadingCars = false
  public pageInfo: any = {
    pagIndex: 0,
    itemsPerPage: 4
  }
  // for hub2hub
  dropoff: Building
  center: ILatLng
  drawerOptions: any
  markers = []
  selectedMarker = ''
  selectedMarkerState = ''
  showCarFilter = false;
  slideOptions: {}
  showBtnPrev = false
  showBtnNext = false
  loadNewCars = false
  isHub2Hub;
  constructor(public events: Events,
    public renderer: Renderer,
    public navCtrl: NavController,
    public buildingsProvider: BuidingsProvider,
    private readonly ngZone: NgZone,
    private _global: GlobalService,
    private carsProvider: CarsProvider,
    public _platform: Platform,
    public utilService: UtilsService,
    public authServie: AuthProvider,
    public gocarToast: GoCarToast,
    public filterProvider: FiltersProvider,
    public gocarModal: GoCarModal,
    public loading: LoadingComponent

  ) {
    this.center = this._global.defaultLocation

    events.subscribe(AppEvent.TimeChanged, () => {
      this.loadBuilding()
      if(this.isOpen){
        this.loadCars(this.currentBuildingId, 0)
      }
    })

    this.drawerOptions = {
      handleHeight: 1,
      thresholdFromBottom: 200,
      thresholdFromTop: 0,
      bounceBack: false
    };

    this.slideOptions = {
      pagination: true,
      loop: true
    }

    this._platform.ready().then((readySource) => {
      this.renderer.setElementStyle(this.carfilter.nativeElement, "webkitTransition", "class 500ms");
      this.loadMap()  
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad');
  }
  
  ngAfterViewInit() {
    // this._platform.ready().then((readySource) => {
    //   this.renderer.setElementStyle(this.carfilter.nativeElement, "webkitTransition", "class 500ms");
    //   this.loadMap()  
    // })
  }

  get networkStatus() {
    return navigator.onLine
  }

  ionViewDidEnter() {
    console.log('ionViewDidEnter');
    //this.loadBuilding();
  }

  loadBuilding() {
    console.log("loadBuilding");
    this.loading.showLoading();
    this.buildingsProvider.getListBuilding().subscribe((result) => {
      let abc = _.find(result, o => { return o.hub2hub == 1 })
      this.isLoadingCars = false
      this.listBuilding = result;
      this.loading.hideLoading();
      this.points = []
      if (this.markersInit == true) {
        _.forEach(this.listBuilding, item => {
          let icon
          if (this.markers[item.building_id] != undefined) {
            if (item.hub2hub == 0) {
              if (item.available) {
                if(item.icon_available !== null && item.icon_not_available  !=null && item.icon_selected !=null )
                {
                  let i = JSON.parse(item.icon_available);
                  icon = i
                }else{
                  icon = CONSTANTS.MARKER
                }
                
              } else {

                if(item.icon_available !== null && item.icon_not_available  !=null && item.icon_selected !=null )
                {
                  let i = JSON.parse(item.icon_not_available);
                  icon = i
                }else{
                  icon = CONSTANTS.MARKER_GREY
                }

                //icon = CONSTANTS.MARKER_GREY
              }
            } else {
              if (item.available) {
                icon = CONSTANTS.MARKER_H2H
              } else {
                icon = CONSTANTS.MARKER_GREY_H2H
              }
            }

            this.selectedMarker = '';

            this.markers[item.building_id].get('iconData').available = item.available;
            this.markers[item.building_id].setIcon(icon);
          }
        })

      }
      else {
        _.forEach(this.listBuilding, item => {
          let position = { lat: item.lat, lng: item.lng }
          let icon
          if (item.hub2hub == 0) {
            if (item.available) {
              if(item.icon_available !== null && item.icon_not_available  !=null && item.icon_selected !=null )
              {
                let i = JSON.parse(item.icon_available);
                icon = i
              }
              else{
                icon = CONSTANTS.MARKER
              }
            } else {

              if(item.icon_available !== null && item.icon_not_available  !=null && item.icon_selected !=null )
              {
                let i = JSON.parse(item.icon_not_available);
                icon = i
              }
              else{
                icon = CONSTANTS.MARKER_GREY
              }
            }
          } else {
            if (item.available) {
              icon = CONSTANTS.MARKER_H2H
            } else {
              icon = CONSTANTS.MARKER_GREY_H2H
            }
          }

          let iconData = item
          this.points.push({ position, iconData, icon })
        })

        this.markersInit = true;

        if (this.mapReady) {
          this.setMarker()
        }
      }

      // this.loading.hideLoading();

    }, err => {
      console.log(err)
      // this.loading.hideLoading();

      //this.loading.hideLoading()
    })

  }

  onDropoffChanged(dropoff) {
    console.log('dropoff', dropoff)
    this.dropoff = dropoff
  }


  onMarkerClick(building) {
    this.isHub2Hub = building.hub2hub
    let buildingId = building.building_id
    this.building = building
    this.currentBuildingName = building.name
    this.currentBuildingId = buildingId
    if (!this.isOpen) {
      this.isOpen = true
      this.renderer.setElementClass(this.list.nativeElement, 'show', true)
      this.renderer.setElementClass(this.list.nativeElement, 'hide', false)
    }

    this.selectedMarker = building.building_id;
    //this.selectedMarkerState = this.markers[building.building_id];

    this.loadCars(buildingId, 0)
  }

  private loadCars(buildingId, pagIndex?, limit?) {
    this.isLoadingCars = true
    this.cars = []
    this.carsProvider.getCars(null, null, buildingId).subscribe((result) => {
      this.cars = result.cars
      this.isLoadingCars = false
      this.loadNewCars = true;
      this.slides.update();
      this.slides.slideTo(0);

      // this.slides.update();
      // this.toggleNav();
      if (this.cars.length > 1) {
        this.showBtnNext = true;
        this.showBtnPrev = false;

      } else {
        this.showBtnNext = false;
        this.showBtnPrev = false;
      }
    }, err => {
      this.isLoadingCars = false
      console.log(err)
    })
  }

  eventBuilding(car) {
    this.gocarModal.showModal(ViewInfoComponent, { data: car })
  }

  onMapClick() {
    this.open = false
    this.closeList()
  }

  closeList() {
    if (this.isOpen) {
      this.isOpen = false
      this.renderer.setElementClass(this.list.nativeElement, 'hide', true)
      this.renderer.setElementClass(this.list.nativeElement, 'show', false)
    }
    if (this.showCarFilter) {
      this.showCarFilter = false
      this.open = false
      this.renderer.setElementClass(this.carfilter.nativeElement, 'show', false)
      this.renderer.setElementClass(this.carfilter.nativeElement, 'hide', true)
    }
  }

  loadMap() {
    this.map = GoogleMaps.create('map_canvas', {
      controls: {
        myLocationButton: true,
        myLocation: true
      },
      camera: {
        target: this.center,
        zoom: 12
      }
    });
    this.loadLocationView();
    this.map.one(GoogleMapsEvent.MAP_READY).then(() => {
      this.mapReady = true;
      this.map.on(GoogleMapsEvent.MAP_CLICK).subscribe(() => {
        this.onMapClick();
      })
      this.loadBuilding();

    });
  }

  loadLocationView() {
    this.map.getMyLocation().then((location: MyLocation) => {
      this.map.animateCamera({ target: location.latLng, duration: 100 })
    }).catch(err => {
    })
  }

  setMarker() {
    this.markersInit = false;
    this.markers = [];
    this.selectedMarker = '';
    console.debug('1111', this.points[0].icon.size)
    this.map.addMarkerSync({position: this.points[0].position, icon: {
      url: 'assets/imgs/G-icon-grey.png',
      size: { width: 32, height: 45 }
  }})
    // this.map.clear().then(() => {
    //   //console.log(4444, this.points)
    //   this.points.forEach((data) => {
    //     data.disableAutoPan = true;
    //     //console.log(data);
    //     try {
    //       let marker = this.map.addMarkerSync(data)
    //     } catch (error) {
    //       throw error
    //     }
    //   })
    // })
  }

  private bookNow(car) {
    if (!this.utilService.isValidBookingTime()) {
      this.gocarToast.showToast({ duration: 2000, message: 'Invalid DateTime' })
    } else if (this.authServie.isAuthenticated) {
      this.carsProvider.pickCar(car.building_id, car.carTypeId).subscribe((picked) => {
        this.navCtrl.push(BookCarPage, { car: picked, dropoff: this.dropoff });
      }, err => {
        console.log(err);
        this.gocarToast.showToast({ message: err.message })
      }
    )
    } else {
      this.gocarModal.showModal(UserPage)
    }
  }

  toggleFilter(hide) {
    this.closeList()
    if (hide) {
      this.showCarFilter = true
    }
    if (this.showCarFilter) {
      this.showCarFilter = false
      this.open = false
      this.renderer.setElementClass(this.carfilter.nativeElement, 'show', false)
      this.renderer.setElementClass(this.carfilter.nativeElement, 'hide', true)
    }
    else {
      this.showCarFilter = true
      this.open = true
      this.renderer.setElementClass(this.carfilter.nativeElement, 'show', true)
      this.renderer.setElementClass(this.carfilter.nativeElement, 'hide', false)
    }
  }

  changeCarType(event) {
    console.log('changeCarType', event);
    this.markersInit = false;
    this.loadBuilding()
  }

  next() {
    try {
      this.slides.slideNext();
    } catch (c) {
      console.log("next exception ", c);
    }
  }

  prev() {
    console.log("isBegining : ", this.slides.isBeginning())
    try {
      this.slides.slidePrev();
    } catch (c) {
      console.log("next exception ", c);
    }

  }

  slideReachStart() {
    this.showBtnPrev = false;
    if (this.loadNewCars) {
      console.log("slideReachStart", this.slides)
      this.slides.slideTo(0);
      this.loadNewCars = false
    }
  }

  slideReachEnd() {
    this.showBtnNext = false;
    console.log("slideReachEnd");
    this.toggleNav();
  }

  slideChanged() {
    this.toggleNav();
  }

  toggleNav() {

    if (this.slides.length() > 1) {
      if (this.slides.isEnd()) {
        console.log("isEnd");
        this.showBtnNext = false
        this.showBtnPrev = true;

      }
      else if (this.slides.isBeginning()) {
        console.log("isBeginning");
        this.showBtnPrev = false;
        this.showBtnNext = true;

      }
      else {
        console.log("isMiddle");

        this.showBtnNext = true
        this.showBtnPrev = true
      }
    }
    else {
      this.showBtnNext = false
      this.showBtnPrev = false
    }
  }

  ngOnDestroy() {
    this.events.unsubscribe(AppEvent.TimeChanged)
  }

}



