export class CarType {
    private _id: string
    private _make: string
    private _model: string
    private _seat: number
    private _photo: string
    private _photo_dir: string
    private _type: string
    private _daily_rate: number
    private _hourly_rate: number
    private _monthly_rate: number
    private _icon_dir: string

    constructor(cartype?) {
        cartype = cartype || {}
        this.id = cartype.id
        this.make = cartype.make
        this.model = cartype.model
        this.seat = cartype.seat
        this.photo = cartype.photo
        this.type = cartype.type
        this.dailyRate = cartype.daily_rate
        this.hourlyRate = cartype.hourly_rate
        this.monthlyRate = cartype.monthly_rate
        this.iconDir = cartype.icon_dir
    }

    public set id(id) {
        this._id = id
    }

    public set make(make) {
        this._make = make
    }

    public set model(model) {
        this._model = model
    }

    public set seat(seat) {
        this._seat = seat
    }

    public set photo(photo) {
        this._photo = photo
    }

    public set iconDir(iconDir) {
        this._icon_dir = iconDir
    }

    public set type(type) {
        this._type = type
    }

    public set dailyRate(dailyRate) {
        this._daily_rate = dailyRate
    }

    public set monthlyRate(monthlyRate) {
        this._monthly_rate = monthlyRate
    }

    public set hourlyRate(hourlyRate) {
        this._hourly_rate = hourlyRate
    }

    public get id() {
        return this._id
    }

    public get make() {
        return this._make
    }

    public get model() {
        return this._model
    }

    public get seat() {
        return this._seat
    }

    public get photo() {
        return this._photo
    }

    public get iconDir() {
        return this._icon_dir
    }

    public get type() {
        return this._type
    }

    public get dailyRate() {
        return this._daily_rate
    }

    public get monthlyRate() {
        return this._monthly_rate
    }

    public get hourlyRate() {
        return this._hourly_rate
    }
}


