import { Component, Output, EventEmitter, Input, OnChanges, SimpleChanges, OnInit } from "@angular/core";
import { NavParams, Events } from 'ionic-angular';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import {PaymentMethodsService} from '@providers/paymentMethods/paymentMethods.provider';
import { AppEvent } from "@common/appEvent";
import { AddCardPage } from '@pages/wallet/add-card/add-card';

import { AuthProvider } from '@providers/auth/auth';
import { GoCarToast } from "../toast/gocar-toast";
import { PayMentMethod } from "@models/payment-method";


@Component({
    selector: 'ipay88-payment-methods',
    templateUrl: './ipay88-payment-methods.html'
})
export class Ipay88PaymentMethodsComponent implements OnChanges, OnInit{
    @Output() onSelectPaymentMethod: EventEmitter<any> = new EventEmitter()
    paymentMethods : PayMentMethod[] = [];
    userData : any ={};
    selectedCard : any ;
    listviewSettings: any = {
        onItemTap: (event, inst) => {
            this.select(this.paymentMethods[event.index])
        },
        theme: 'mobiscroll',
        swipe: 'left',
        stages: [{
            percent: -25,
            color: 'red',
            text: 'Delete',
            confirm: true,
            action: (event, inst) => {
                // this.todo.splice(event.index, 1);
                
                let paymentMethod = this.paymentMethods[event.index]
                if(paymentMethod) {
                    this.removeCredit(paymentMethod.id)
                }
            }
        }]
    };
    @Input() selectable:boolean;

    constructor(public paymentMethodsService:PaymentMethodsService , 
                public navParams: NavParams , 
                private events:Events , 
                public gocarModal: GoCarModal , 
                public gocarToast: GoCarToast,
                public authService : AuthProvider){
    }

    ngOnInit() {
        this.onPaymentMethodsAdded();
        this.getPaymentMethods();
        this.getUsetData();
    }

    ngOnChanges(changes: SimpleChanges): void {

    }

    removeCredit(id) {
        this.paymentMethodsService.remove(id).subscribe(() => {
            this.gocarToast.showToast({message: 'Deleted'})
            this.getPaymentMethods()
        }, err => {
            this.gocarToast.showToast({message: 'Server Error'})
        })
    }

    addCredit() {

        this.paymentMethodsService.generateRefNo().subscribe(
          (refNo)=>{
            this.userData.refNo = refNo.data;
          },(err)=>{
            console.log(err)
          } , () =>{ //finally
            console.log("addCredit userData : " , this.userData);
            //this.gocarModal.showModal(AddCardPage , {userData : this.userData});
            this.gocarModal.showModalWithoutDismiss(AddCardPage , {userData : this.userData});
            this.gocarModal.currentModal.onDidDismiss((isAdded) => {
              this.events.publish(AppEvent.PaymentMethodsAdded, isAdded)
            });
    
          } )
    }

    getUsetData(){
        this.authService.getUserDatasByToken().subscribe((result) => {
            this.userData = result.data;
        });
    }

    getPaymentMethods(){
        this.paymentMethodsService.getUserPaymentMethods().subscribe((result)=>{
           this.paymentMethods = result;

           if(this.paymentMethods && this.paymentMethods.length>0 && this.selectable == true){
               this.selectedCard = this.paymentMethods[0].id;
               this.onSelectPaymentMethod.emit(this.paymentMethods[0].id)
           }

        }, (err)=>{
          console.log(err);
        }) 
      }
    
    onPaymentMethodsAdded(){
        this.events.subscribe(AppEvent.PaymentMethodsAdded ,(isAdded)=>{
          this.getPaymentMethods();
        })
    }

    select(paymentMethod){
        if(this.selectable == true)
        {
            this.selectedCard = paymentMethod.id;
            this.onSelectPaymentMethod.emit(paymentMethod.id)
        }
    }

    isSelected(paymentMethodId){
        if(paymentMethodId == this.selectedCard)
            return true;
    }


}


