import { Component } from "@angular/core";
import { Camera, CameraOptions } from '@ionic-native/camera'
import { CONSTANTS } from "@common/constants";

@Component({
    selector: 'gocar-camera',
    templateUrl: './gocar-camera.html'
})
export class GoCarCamera {
    public options: CameraOptions = {
        quality: CONSTANTS.IMAGE_QUALITY,
        destinationType: this.camera.DestinationType.FILE_URI,
        encodingType: this.camera.EncodingType.JPEG,
        correctOrientation: true,
        saveToPhotoAlbum: true,
        mediaType: this.camera.MediaType.PICTURE,
        targetHeight: CONSTANTS.IMAGE_HEIGHT,
    }
    constructor(public camera: Camera) {

    }
    /**
     * 
     * @param sourceType 0 = photo library, 1 = camera
     */
    initCamera(sourceType?: number) {
        this.options.sourceType = sourceType
        return this.camera.getPicture(this.options)
    }
}


