import { Component, Output, EventEmitter } from "@angular/core";

@Component({
    selector: 'no-internet-connection',
    templateUrl: 'no-internet-connection.html'
})

export class NoInternetConnectionComponent {
    @Output() onRetry: EventEmitter<any> = new EventEmitter()

    constructor() {}

    tryAgain() {
        this.onRetry.emit()
    }
}


