import { Component } from '@angular/core';
import { NavController, NavParams, Events } from 'ionic-angular';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { PaymentHistoryPage } from '../payment-history/payment-history';
import { CreditHistoryPage } from '../credit-history/credit-history';
import { WalletProvider } from '@providers/wallet/wallet';
import { PaymentMethodsService } from '@providers/paymentMethods/paymentMethods.provider';
import { QRScannerService } from '@providers/qa-scanner/qa.scanner.provider';
import { QRScanner } from '@ionic-native/qr-scanner';
import { LoadingComponent } from '@shared/components/loading/loading';
import { ScannerPage } from '../scanner/scanner';
import { AlertBoxComponent } from '@shared/components/alert-box/alert-box';

/**
 * Generated class for the WalletPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-wallet',
  templateUrl: 'wallet.html',
})
export class WalletPage {

  walletData: any = {};
  userData: any = {};
  paymentMethods: any;
  scannerData;
  constructor(public navCtrl: NavController, public navParams: NavParams, public gocarModal: GoCarModal,
    private walletProvider: WalletProvider, private gocarLoading: LoadingComponent,
    private paymentMethodsService: PaymentMethodsService, private events: Events, private QRScannerService: QRScannerService,
    private qrScanner: QRScanner, private alertBoxComponent: AlertBoxComponent) {
    // this.onPaymentMethodsAdded();
    // this.getPaymentMethods();
  }

  ionViewDidLoad() {
    this.gocarLoading.showLoading("Loading...");
    this.walletProvider.getWalletInfoByUser().subscribe(result => {
      this.gocarLoading.hideLoading();
      this.walletData = result.data || {};
    }, err => this.gocarLoading.hideLoading());
  }

  public ionViewWillEnter() {
    this.scannerData = this.navParams.get('result') || null;
    if (this.scannerData) {
      let data = {
        'scannerData': this.scannerData
      }
      this.walletProvider.qrScanner(data).subscribe(result => {
        this.alertBoxComponent.createInfoAlert('QR Code', null, result.data).present()
      }, err => console.log('err', err))
    }
  }

  // addCredit() {
  //   this.userData = this.navParams.get('user') || {};

  //   this.paymentMethodsService.generateRefNo().subscribe(
  //     (refNo)=>{
  //       this.userData.refNo = refNo.data;
  //     },(err)=>{
  //       console.log(err)
  //     } , () =>{ //finally
  //       this.gocarModal.showModal(AddCardPage , {userData : this.userData});

  //       this.gocarModal.currentModal.onDidDismiss((isAdded) => {
  //         this.events.publish(AppEvent.PaymentMethodsAdded, isAdded)
  //       });

  //     } )
  // }

  // getPaymentMethods(){
  //   this.paymentMethodsService.getUserPaymentMethods().subscribe((result)=>{
  //      this.paymentMethods = result.data;
  //   }, (err)=>{
  //     console.log(err);
  //   }) 
  // }

  // onPaymentMethodsAdded(){
  //   this.events.subscribe(AppEvent.PaymentMethodsAdded ,(isAdded)=>{
  //     console.log("onPaymentMethodsAdded : ", isAdded);
  //     this.getPaymentMethods();
  //   })
  // }


  paymentHistory() {
    this.navCtrl.push(PaymentHistoryPage);
  }

  creditHistory() {
    this.navCtrl.push(CreditHistoryPage);
  }
  scanner() {
    this.navCtrl.push(ScannerPage)
  }


}



