import { Http } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { BaseService } from '../base.service';
import { AppConfig } from '@app/app.config';

/*
  Generated class for the TransactionProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class TransactionProvider extends BaseService {

  constructor(public http: Http, protected appConfig: AppConfig) {
    super(http, appConfig);
    console.log('WalletProvider Provider');
  }

  getTransactionInfoByUser() {
    return this.getData(`transaction/list`)
      .catch((err) => {
        console.log(err);
        return Observable.throw(false);
      });
  }

}



