import { Component, OnDestroy } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { AlertBoxComponent } from '@shared/components/alert-box/alert-box';
import { AppMode } from '@shared/enums/app-mode.enum';
import { BookCarPayment } from './payment/payment';
import { ReservationsProvider } from '@providers/reservations/reservations';
import { AuthProvider } from '@providers/auth/auth';
import { FiltersProvider } from '@providers/fiters/filters';
import { Resevervation } from '@models/reservation';
import { GoCarToast } from '@shared/components/toast/gocar-toast';
import { PayMentType } from '@shared/enums/payment-type.enum';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { AppEvent } from '@common/appEvent';
import { ViewInfoComponent } from '@shared/components/view-info/view-info';
import { Building } from '@models/building';

/**
 * Generated class for the BookCarPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-book-car',
  templateUrl: 'book-car.html',
})
export class BookCarPage implements OnDestroy{
  promoCode;
  car: any
  position: any
  reservation: Resevervation
  dropoff: Building
  // used in html template
  private appMode = AppMode

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public authService: AuthProvider,
              public filterProvider: FiltersProvider,
              public gocarToast: GoCarToast,
              public events: Events,
              public reservationService: ReservationsProvider, 
              public alertCtr: AlertBoxComponent,
              public goCarModal: GoCarModal) {
                this.car = navParams.get('car')
                this.dropoff = navParams.get('dropoff')
                 console.log("car",this.car)
                this.position = {lat: Number(this.car.building_lat), 
                                 lng: Number(this.car.building_lng)}
                this.initReservation()
                // this.events.subscribe(AppEvent.PaymentDone, () => {

                // })

                // this.events.subscribe(AppEvent.PaymentFailed,()=>{
                //   this.gotoPayment();
                // })
  }

  initReservation() {
    this.reservation = new Resevervation()
    this.reservation.userId = this.authService.userId
        this.reservation.start_time = this.filterProvider.getBookingTimeStart(),
        this.reservation.end_time = this.filterProvider.getBookingTimeEnd()
        this.reservation.carId = this.car.id
        this.reservation.buildingId = this.car.building_id
        this.reservation.amount = this.car.pricing.total_price
        this.reservation.building_city = this.car.building_city
        this.reservation.building_name = this.car.building_name
        this.reservation.braintree_traxid = '1'
        this.reservation.braintree_card_token = '1'
        this.reservation.make = this.car.make
        this.reservation.model = this.car.model,
        this.reservation.pricing = this.car.pricing
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad BookCarPage');
    console.log( this.navParams.get('user'));
    
  }

  gotoPayment() {
    //this.navCtrl.push(BookCarPayment, { user:{} ,  total_price: this.car.pricing.total_price, mode: PayMentType.BOOK_CAR, reservation: this.reservation});
    this.goCarModal.showModalWithoutDismiss(BookCarPayment, { mode: PayMentType.BOOK_CAR, reservation: this.reservation, dropoff: this.dropoff})
  }
  

  showLocation(car) {
    this.goCarModal.showModal(ViewInfoComponent, { data: car })
  }

  ngOnDestroy() {
    //this.events.unsubscribe(AppEvent.PaymentFailed);
    //this.events.unsubscribe(AppEvent.PaymentDone);
  }
}



