import { Http, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { BaseService } from '../base.service';
import { AppConfig } from '@app/app.config';
import { Observable } from 'rxjs';
import { GlobalService } from '../global.provider';
import { AngularFireStorage } from 'angularfire2/storage';
import { Events } from 'ionic-angular';
import { AlertBoxComponent } from '@shared/components/alert-box/alert-box';
/*
  Generated class for the ReservationsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class RentalDetailProvider extends BaseService {

    constructor(public http: Http,
        public global: GlobalService,
        protected appConfig: AppConfig,
        public events: Events,
        public alertBox: AlertBoxComponent,
        private storage: AngularFireStorage) {
        super(http, appConfig, alertBox, events, global);
        console.log('Hello RentalDetailProvider Provider');
    }

    updateRentalDetail(body, time, status): Observable<any> {
        return this.postData(`reservations/update`, { reservation: body, time: time, status: status })
            .map((res: Response) => res.json())
            .catch((err) => {
                console.log(err);
                return Observable.throw(false);
            });
    }

    updateStatusRentalDetail(body, status): Observable<any> {
        return this.postData(`reservations/update-status`, { reservation: body, status: status })
            .map((res: Response) => res.json())
            .catch((err) => {
                console.log(err);
                return Observable.throw(false);
            });
    }

    getRentalDetailById(id): Observable<any> {
        return this.getData(`reservations/reservation-by-user/${id}`)
            .catch((err) => {
                console.log(err);
                return Observable.throw(false);
            });
    }

    getUrlImage(url) {
        let ref = this.storage.storage.ref(url);
        return ref.getDownloadURL();
    }

}



