import { Component } from '@angular/core';
import { ViewController, IonicPage, NavController, NavParams } from 'ionic-angular';
import { SocialSharing } from '@ionic-native/social-sharing';
import { AppConfig } from "@app/app.config";
import { Clipboard } from '@ionic-native/clipboard';
import { HomePage } from '../home/home';


/**
 * Generated class for the ReferralPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-referral',
  templateUrl: 'referral.html',
  providers: [SocialSharing, Clipboard]
})
export class ReferralPage {
  deepLink: any;
  referenceCode: any;
  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private socialSharing: SocialSharing,
              private clipboard: Clipboard,
              private appConfig: AppConfig,
              public viewCtrl: ViewController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ReferralPage');
    this.referenceCode = this.navParams.get('user').referenceCode;
    this.createLink(this.referenceCode)
  }

  shareFaceBook() {
    this.socialSharing.shareViaFacebook('', '', this.deepLink)
  }

  shareWhatsapp() {
    this.socialSharing.shareViaWhatsApp('', '', this.deepLink)
  }

  shareSMS() {
    this.socialSharing.shareViaSMS(this.deepLink, '');
  }

  copyLink() {
    this.clipboard.copy(this.deepLink)
  }

  createLink(referenceCode) {
    this.deepLink = this.appConfig.firebaseConfig.dynamicLink + '?referenceCode='
                  + referenceCode + '&apn='
                  + this.appConfig.firebaseConfig.packageName;
  }

  back() {
    this.navCtrl.setRoot(HomePage)
  }
}



