import { Injectable } from "@angular/core";
import { GoCarFileTransfer } from "@shared/components/file-transfer/gocar-file-transfer";

@Injectable()
export class UploadImage {
    constructor(private gocarFileTransfer: GoCarFileTransfer) {

    }

    doUpload(images: any[], url, headers?) {
        let promises = []
        for(let img of images) {
            headers.type = img.type
            let imgPromise = this.gocarFileTransfer.upload(img.image, url, headers)
            promises.push(imgPromise)
        }
       return Promise.all(promises)
    }

}


