import { Component } from "@angular/core";
import { ModalController } from "ionic-angular";

@Component({
    selector: 'modal',
    templateUrl: 'gocar-modal.html'
})
export class GoCarModal {
    private modal: any
    constructor(public modalCtrl: ModalController) { }

    public showModal(component, data?) {
        if (this.modal) {
            this.modal.dismiss()
        }
        this.modal = this.modalCtrl.create(component, data)
        this.modal.present()
    }

    public showModalV2(component, data?) {
        let modal;
        if (this.modal) {
            modal = this.modal;
        }
        this.modal = this.modalCtrl.create(component, data)
        this.modal.present()
        if (modal) {
            modal.dismiss()
        }
    }

    public showModalWithoutDismiss(component, data?) {
        let modal;
        if (this.modal) {
            modal = this.modal;
        }
        this.modal = this.modalCtrl.create(component, data)
        this.modal.present()
    }

    public get currentModal() {
        return this.modal
    }

    public hideModal() {
        if (this.modal) {
            this.modal.dismiss()
            this.modal = null
        }
    }
}


