import { Component, Input } from "@angular/core";
import { CarTypeEnum } from "../../enums/car-type.enum";
import { CarTypeImageEnum } from "../../enums/car-type-image.enum";
import { ViewController, NavParams, ActionSheetController, App } from "ionic-angular";
import { GoCarModal } from "../modal/gocar-modal";
import { AfterTrip } from '@pages/rental-detail/after-trip/after-trip';
import { BeforeTrip } from "@pages/rental-detail/before-trip/before-trip";
import { LaunchNavigator, LaunchNavigatorOptions } from '@ionic-native/launch-navigator';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Clipboard } from "@ionic-native/clipboard";
import { GoCarToast } from "@shared/components/toast/gocar-toast";

@Component({
    selector: 'view-info',
    templateUrl: './view-info.html'
})
export class ViewInfoComponent {
    @Input() type: string
    reservation;
    public imgSrc: string = './assets/imgs/car_type/'
    position: any
    componentPrevious: any
    service: any
    imgBuildings = [{ a: '1' }, { a: '2' }, { a: '3' }, { a: '4' }, { a: '5' }];
    constructor(public viewCtrl: ViewController,
        public navParams: NavParams,
        public goCarModal: GoCarModal,
        private launchNavigator: LaunchNavigator,
        public actionSheetCtrl: ActionSheetController,
        public app: App,
        private iab: InAppBrowser,
        public clipboard: Clipboard,
        public gocarToast: GoCarToast) {
        this.reservation = this.navParams.get('data');
        this.service = navParams.get('service')
        this.componentPrevious = this.navParams.get('componentPrevious');
        this.position = {
            lat: Number(this.reservation.building_lat),
            lng: Number(this.reservation.building_lng)
        }
    }

    copy() {
        let message = Number(this.reservation.building_lat) + ',' + Number(this.reservation.building_lng)
        this.clipboard.copy(message).then(() => {
            this.gocarToast.showToast({ position: 'bottom', message: 'Address copied , GPS copied' });
        })
    }

    get image() {
        switch (this.type) {
            case CarTypeEnum.MPV:
                return `${this.imgSrc}${CarTypeImageEnum.MPV}`
            case CarTypeEnum.SEDAN:
                return `${this.imgSrc}${CarTypeImageEnum.SEDAN}`
            case CarTypeEnum.SUV:
                return `${this.imgSrc}${CarTypeImageEnum.SUV}`
            default:
                return `${this.imgSrc}${CarTypeImageEnum.SUV}`
        }
    }
    close() {
        if (this.componentPrevious == 'AfterTrip') {
            this.goCarModal.showModalV2(AfterTrip, { data: this.reservation, service: this.service })
        } else if (this.componentPrevious == 'BeforeTrip') {
            this.goCarModal.showModalV2(BeforeTrip, { data: this.reservation, service: this.service })
        } else {
            this.viewCtrl.dismiss()
        }
    }

    navigateGoogleMap() {
        let options: LaunchNavigatorOptions = {
            start: [Number(this.reservation.building_lat), Number(this.reservation.building_lng)],
            app: this.launchNavigator.APP.GOOGLE_MAPS
        };
        this.launchNavigator.navigate(options.start, options)
            .then(
                success => console.log('Launched navigator'),
                error => console.log('Error launching navigator', error)
            );
    }

    navigateWazeMap() {
        let options: LaunchNavigatorOptions = {
            start: [Number(this.reservation.building_lat), Number(this.reservation.building_lng)],
            app: this.launchNavigator.APP.WAZE
        };
        this.launchNavigator.navigate(options.start, options)
            .then(
                success => console.log('Launched navigator'),
                error => console.log('Error launching navigator', error)
            );
    }

    navigateGrab() {
        let _this = this;
        let url = "https://grab.onelink.me/2695613898?pid=KBwsWv56Kc&af_dp=grab%3A%2F%2Fopen%3FscreenType%3DBOOKING%26dropOffAddress%3D" + encodeURI(this.reservation.building_name) + "%26dropOffLatitude%3D" + Number(this.reservation.building_lat) + "%26dropOffLongitude%3D" + Number(this.reservation.building_lng);
        window.open(url, '_system');
    }

    presentActionSheet() {
        let actionSheet = this.actionSheetCtrl.create({
            title: 'Open With',
            cssClass: 'building-infor',
            buttons: [
                {
                    text: 'Google Map',
                    handler: () => {
                        this.navigateGoogleMap();
                        console.log('Google map clicked');
                    }
                },
                {
                    text: 'Waze Map',
                    handler: () => {
                        this.navigateWazeMap();
                        console.log('Waze map clicked');
                    }
                },
                {
                    text: 'Grab',
                    handler: () => {
                        this.navigateGrab();
                        console.log('Grab clicked');
                    }
                },
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: () => {
                        console.log('Cancel clicked');
                    }
                }
            ]
        });

        actionSheet.present();
    }
}


