import { Component, Input, OnChanges, SimpleChanges } from "@angular/core";
import { SharingButtonsEnum, SharingIcons } from "../../enums/sharing-button-icon.enum";
import { DomSanitizer } from "@angular/platform-browser";

@Component({
    selector: 'sharing-button',
    templateUrl: 'sharing-button.html'
})

export class SharingButton implements OnChanges {
    @Input() type: number
    @Input() text: string
    svgIcon: any
    buttonName: string
    dataNetwork: string
    constructor(private domSantitizer: DomSanitizer){}

    ngOnChanges(changes: SimpleChanges) {
        if (changes.type && changes.type.currentValue) {
            this.initIcon()
        }

        if (changes.text && changes.text.currentValue) {
            this.buttonName = this.text
        }
    }

    initIcon() {
        let icon;
        switch (this.type) {
            case SharingButtonsEnum.FACEBOOK:
                icon = SharingIcons.FACEBOOK;
                this.dataNetwork = 'facebook'
                break;
            case SharingButtonsEnum.WHATSAPP:
                icon = SharingIcons.WHATSAPP;
                this.dataNetwork = 'whatsapp'
                break;
            case SharingButtonsEnum.SMS:
                icon = SharingIcons.SMS;
                this.dataNetwork = 'sms'
                break;
            case SharingButtonsEnum.SHARE:
                icon = SharingIcons.SHARE;
                this.dataNetwork = 'share'
                break;
        }
        this.svgIcon = this.domSantitizer.bypassSecurityTrustHtml(`<path d="${icon}"></path>`)
    } 
}   


