import { Injectable } from "@angular/core";
import { BaseService } from "../base.service";
import { Http } from "@angular/http";
import { AppConfig } from "@app/app.config";
import { Storage } from '@ionic/storage';
import { Observable } from "rxjs/Observable";

@Injectable()
export class SmsService extends BaseService {
    constructor(public http: Http, protected appConfig: AppConfig, protected storage: Storage) {
        super(http, appConfig);
    }

    public generateOPT(phone) {
        return this.getData(`user/otp/generate/${phone}`).catch((err) => {
            return Observable.throw(err);
        });
    }

    public verifyOPT(phone, pincode) {
        return this.getData(`user/otp/verify/${phone}/${pincode}`).catch((err) => {
            return Observable.throw(err);
        });
    }
    public unlockCar(id) {
        return this.getData(`reservations/unlock-car/${id}`)
            .catch((err) => {
                console.log(err)
                return Observable.throw(err)
            })
    }
    public lockCar(id) {
        return this.getData(`reservations/lock-car/${id}`)
            .catch((err) => {
                console.log(err)
                return Observable.throw(err)
            })
    }
    public resetCar(id) {
        return this.getData(`reservations/reset-car/${id}`)
            .catch((err) => {
                console.log(err)
                return Observable.throw(err)
            })
    }
}


