import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { TransactionProvider } from '@providers/transaction/transaction';
import * as _ from 'lodash';
import { LoadingComponent } from '@shared/components/loading/loading';

/**
 * Generated class for the PaymentHistoryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-payment-history',
  templateUrl: 'payment-history.html',
})
export class PaymentHistoryPage {
  
  paymentHistory: any = [];

  constructor(public navCtrl: NavController, public navParams: NavParams,
    private transactionProvider: TransactionProvider, private gocarLoading: LoadingComponent) {
  }

  ionViewDidLoad() {
    this.gocarLoading.showLoading("Loading...");

    this.transactionProvider.getTransactionInfoByUser().subscribe(result => {

//      this.paymentHistory = result.data || [];
      this.paymentHistory  =  _.sortBy(result.data, 'created').reverse(); 
      console.log(this.paymentHistory);

      this.gocarLoading.hideLoading();
      
    }, err => this.gocarLoading.hideLoading())
  }

}



