import { NgModule } from '@angular/core';
import { IonicPageModule, IonicModule } from 'ionic-angular';
import { ComponentsModule } from '../../shared/components/components.module';
import { ReportDamagePage } from './report-damage/report-damage';
import { BeforeTrip } from './before-trip/before-trip';
import { DuringTrip } from './during-trip/during-trip';
import { EndReport } from './end-report/end-report';
import { AfterTrip } from './after-trip/after-trip';
import { RentalDetailPage } from './rental-detail';
import { Ionic2RatingModule } from 'ionic2-rating';

@NgModule({
  declarations: [
    RentalDetailPage,
    ReportDamagePage,
    BeforeTrip,
    DuringTrip,
    EndReport,
    AfterTrip
  ],
  imports: [
    IonicModule,
    IonicPageModule.forChild(RentalDetailPage),
    ComponentsModule,
    Ionic2RatingModule
  ],
  entryComponents: [
    ReportDamagePage,
    BeforeTrip,
    DuringTrip,
    EndReport,
    AfterTrip,
    RentalDetailPage
  ]
})
export class RentalDetailPageModule {}



