import { Component, Input, OnInit, OnChanges, SimpleChanges } from "@angular/core";
import { ViewController } from "ionic-angular";


@Component({
    selector: 'semi-circle',
    templateUrl: 'semi-circle.html'
})
export class SemiCircleComponent  implements OnInit, OnChanges {
    @Input() start: any
    @Input() end: any
    maxTime: any
    remainTime: any
    remain: any = '';
    constructor(public viewCtrl: ViewController) {
        
    }

    ngOnChanges(changes: SimpleChanges) {
        if(changes.end && changes.end.currentValue) {
            this.getTimeRange()
        }
    }

    ngOnInit() {
        // this.getTimeRange()
    }

    getTimeRange() {
        console.log('changed', this.end)
        let dateStart = new Date(this.start);
        let dateEnd = new Date(this.end);
        let dateNow = new Date();
        
        if (dateStart > dateNow) {
            dateNow = dateStart;
        }
        this.maxTime = dateEnd.getTime() - dateStart.getTime()
        this.remainTime = this.maxTime - (dateNow.getTime() - dateStart.getTime())
        this.calculateTime();
    }

    calculateTime() {
        let remain = this.remainTime;
        let day = 0;
        let hour = 0;
        let minute = 0;
        // let sec = 0;
        if (remain >= 86400000) {
            day = Math.floor(remain / 86400000)
            this.remain += day + 'd ';
        } else {
            this.remain += '0d ';
        }
        remain = remain - day * 86400000;
        if (remain >= 3600000) {
            hour = Math.floor(remain / 3600000)
            this.remain += hour + 'h ';
        } else {
            this.remain += '0h ';
        }
        remain = remain - hour * 3600000;
        if (remain >= 60000) {
            minute = Math.floor(remain / 60000)
            this.remain += minute + 'm ';
        } else {
            this.remain += '0m ';
        }
    }
}


