import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';

import { NavController, NavParams, ViewController, AlertController, Platform } from 'ionic-angular';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { AuthProvider } from '@providers/auth/auth';
import { Events } from 'ionic-angular/util/events';
import { UserDetail } from '../register/user-detail/user-detail';
import { AppEvent } from '@common/appEvent';
import { CustomValidator } from '@shared/validator/validator';
import { CONSTANTS } from "@common/constants";
import { GoCarToast } from '@shared/components/toast/gocar-toast';
import { Keyboard } from '@ionic-native/keyboard';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { LoadingComponent } from '@shared/components/loading/loading';
/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
  providers: [AuthProvider]
})
export class LoginPage implements OnInit {
  signUp: FormGroup;
  public isKeyBroad = false;
  constructor(public navCtrl: NavController,
    // private keybaordShowSub: Subscription,
    //	private keyboardHideSub: Subscription,

    public navParams: NavParams,
    public viewCtrl: ViewController,
    public events: Events,
    private gocarLoading: LoadingComponent,
    private gocarToast: GoCarToast,
    private formBuilder: FormBuilder,
    private alertCtr: AlertController,
    private authProvider: AuthProvider,
    public validator: CustomValidator,
    // public keyboard : Keyboard
    public platform: Platform,
    public keyboard: Keyboard,
    public iab: InAppBrowser
  ) {

    this.platform.ready().then(() => {

      Observable.fromEvent(window, 'keyboardWillShow').subscribe(() => {
        console.log('Keyboard is now open');
      });

      Observable.fromEvent(window, 'keyboardDidShow').subscribe(() => {
        console.log('Keyboard is now open');

      });

      ////
      // console.log("platform ready");
      // document.addEventListener('keyboardDidHide', (ev) => {
      //   // Describe your logic which will be run each time when keyboard is about to be shown.
      //   console.log("keyboardDidHide 2");
      // });

      // window.addEventListener('keyboardDidHide', (ev) => {
      //   // Describe your logic which will be run each time when keyboard is about to be shown.
      //   console.log("keyboardDidHide");
      // });


      // window.addEventListener('keyboardDidShow', (ev) => {
      //   // Describe your logic which will be run each time when keyboard is about to be shown.
      //   console.log("keyboard did show");
      // });

      ///



    });

    // document.addEventListener('keyboardDidHide', (ev) => {
    //   // Describe your logic which will be run each time when keyboard is about to be shown.
    //   console.log("keyboardDidHide 2");
    // });

    // window.addEventListener('keyboardDidHide', (ev) => {
    //   // Describe your logic which will be run each time when keyboard is about to be shown.
    //   console.log("keyboardDidHide");
    // });


    // window.addEventListener('keyboardDidShow', (ev) => {
    //   // Describe your logic which will be run each time when keyboard is about to be shown.
    //   console.log("keyboard did show");
    // });

  }




  ngOnInit() {
    this.keyboard.onKeyboardShow().subscribe(() => {
      this.isKeyBroad = true;
    });
    this.keyboard.onKeyboardHide().subscribe(() => {
      this.isKeyBroad = false;
    });
    this.initialForm()
    console.log("init");




  }
  initialForm() {
    this.signUp = this.formBuilder.group({
      email: ['', [this.validator.required, Validators.pattern(CONSTANTS.EMAIL_REGEX)]],
      password: ['', [this.validator.required]],
    });
  }

  ionViewDidLoad() {
    // document.body.classList.add('keyboard-is-open');
  }

  ionViewWillEnter() {
    this.addKeyboardListeners();
  }

  ionViewWillLeave() {
    this.removeKeyboardListeners();
  }


  private addKeyboardListeners() {
    Observable.fromEvent(window, 'keyboardWillShow').subscribe(e => {
      // Code here
      console.log("keyboardWillShow");
      document.body.classList.add('keyboard-is-open');
    });

    Observable.fromEvent(window, 'keyboardWillHide').subscribe(e => {
      // Code here
      console.log("keyboardWillHide");
      document.body.classList.remove('keyboard-is-open');


    });
  }

  private removeKeyboardListeners() {
    //if (this.keybaordShowSub) this.keybaordShowSub.unsubscribe();
    //if (this.keyboardHideSub) this.keyboardHideSub.unsubscribe();
  }

  login() {
    this.gocarLoading.showLoading("Logging...")
    let email = this.signUp.value.email;
    let password = this.signUp.value.password;
    this.authProvider.loginWithEmailAndPassword(email, password).subscribe(result => {
      this.close()
      this.gocarLoading.hideLoading()
    }, (err) => {
      this.gocarLoading.hideLoading()
      this.showMessage(err.message);
    })
  }

  register() {
    this.navCtrl.push(UserDetail)
  }

  close() {
    this.events.publish(AppEvent.UserDismiss, 'dismiss')
    // this.navCtrl.parent.dismiss()
  }

  showMessage(message: any) {
    console.log(message)
    this.gocarToast.showToast({ position: 'bottom', message: message, duration: 2000 })
  }

  getControl(ctrlName) {
    return this.signUp.get(ctrlName)
  }

  forgotPass() {
    let browser = this.iab.create('https://www.gocar.my/reset_password', '_blank');
  }


}



