import { Component } from "@angular/core";
import { ToastController, ToastOptions, Toast } from "ionic-angular";

@Component({
    selector: 'gocar-toast',
    templateUrl: 'gocar-toast.html'
})

export class GoCarToast {
    private toast: Toast
    constructor(private toastCtrl: ToastController) {

    }

    showToast(options: ToastOptions) {
        if(!options.showCloseButton)
            options.duration = 5000
        
            
        options.cssClass = 'gocar-toast'
        if (this.toast) {
            this.toast.dismiss()
        }
        this.toast = this.toastCtrl.create(options)
        this.toast.present()
    }

    hideToast() {
        if (this.toast) {
            this.toast.dismiss()
        }
    }
}


