import { ReportOptionProvider } from './../../../providers/report-option/report-option';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Events } from 'ionic-angular/util/events';
import { ViewController, AlertController } from "ionic-angular";
import { AfterTrip } from '../after-trip/after-trip';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { DuringTrip } from "../during-trip/during-trip";
import { RentalDetailProvider } from '@providers/rental-detail/rental-detail';
import { GoCarPopover } from "@shared/components/popover/gocar-popover";
import { SelectImage } from "@shared/components/select-image/select-image";
import { UtilsService } from '@providers/utils.service';
import { CONSTANTS } from "@common/constants";
import { UploadImage } from '@providers/upload/upload';
import { AppConfig } from '@app/app.config';
import * as _ from 'lodash';
import { Observable } from 'rxjs';
import { LoadingComponent } from '@shared/components/loading/loading';

/**
 * Generated class for the RentalDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'end-report',
  templateUrl: 'end-report.html',
})
export class EndReport {
  public leftSide: any = 'assets/imgs/left-side.png';
  public rightSide: any = 'assets/imgs/right-side.png';
  public frontSide: any = 'assets/imgs/front-side.png';
  public backSide: any = 'assets/imgs/back-side.png';
  public other: any = 'assets/imgs/other.png';
  public reportImage: any
  public images: any = []
  public carExDirty = false;
  public carInDirty = false;
  public dent = false;
  public cigarette = false;
  public remarks: any = ''
  public additionalImage: any = []
  listOpt;
  reservation: any
  service: any = this;
  serviceReservation: any
  fuelValue: any = 'end_fuel_level'
  public uploadUrl: any = this.appConfig.apiUrl + '/reservations/upload'
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public events: Events,
    public viewCtrl: ViewController,
    public goCarModal: GoCarModal,
    public rentalDetailProvider: RentalDetailProvider,
    private popover: GoCarPopover,
    private utilsService: UtilsService,
    private uploadImage: UploadImage,
    private appConfig: AppConfig,
    private gocarLoading: LoadingComponent,
    private alertCtrl: AlertController,
    private reportOptProvider: ReportOptionProvider) {
    this.reservation = this.navParams.get('data')
    this.serviceReservation = navParams.get('service')

  }

  ionViewDidLoad() {
    this.listOption();
    this.addKeyboardListeners()
    if (this.reservation.status == 'DURING_TRIP' || this.reservation.status == 'END_TRIP') {
      // this.setupComment();
      this.reloadImage();
      if (!_.isUndefined(this.reservation.end_fuel_level)) {
        this.reservation.end_fuel_level_origin = this.reservation.end_fuel_level;
      }
    }
  }

  listOption() {
    this.listOpt = [];
    this.reportOptProvider.listReportOption().subscribe(result => {
      _.forEach(result.data, val => {
        val['isCheck'] = false;
        this.listOpt.push(val);
      })
    });
  }

  clickOption(option, isCheck) {
    _.forEach(this.listOpt, (val) => {
      if (val.name == option) {
        val.isCheck = isCheck;
      }
    })
  }

  private addKeyboardListeners() {
    Observable.fromEvent(window, 'keyboardWillShow').subscribe(e => {
      document.body.classList.add('keyboard-is-open');
    });

    Observable.fromEvent(window, 'keyboardWillHide').subscribe(e => {
      document.body.classList.remove('keyboard-is-open');
    });
  }

  back() {
    this.goCarModal.showModalV2(DuringTrip, { data: this.reservation, service: this.serviceReservation })
  }

  close() {
    this.viewCtrl.dismiss(this.reservation)
  }

  endTrip() {
    if (_.isNull(this.reservation.end_fuel_level) || this.reservation.end_fuel_level == 0) {
      this.alertFuelError();
    } else {
      this.updateStatus();
    }
  }

  updateFuelLevel(reservation) {
    this.rentalDetailProvider.updateRentalDetail(reservation, new Date(), 'DURING_TRIP').subscribe(rs => {
      this.rentalDetailProvider.getRentalDetailById(this.reservation.id).subscribe(rs => {
        this.reservation = rs.data;
      })
    })
  }

  // attachImage(side) {
  //   this.popover.show(SelectImage).then(data => {
  //     if (data) {
  //       this.reportImage = this.utilsService.thumbnailify(data, CONSTANTS.THUMBNAIL_SIZE, (thumbnail) => {
  //         this[side] = thumbnail;
  //       })
  //       let item = { image: data, type: side }
  //       this.images.push(item);
  //       this.reservation.imagesEnd = this.images;
  //     }
  //   })
  // }

  attachImage(side) {
    this.popover.show(SelectImage, {}, { cssClass: "popover-class" }).then(data => {
      if (data) {
        if (side != 'other') {
          this.reportImage = this.utilsService.thumbnailify(data, CONSTANTS.THUMBNAIL_SIZE, (thumbnail) => {
            this[side] = thumbnail;
          })
        } else {
          this.reportImage = this.utilsService.thumbnailify(data, CONSTANTS.THUMBNAIL_SIZE, (thumbnail) => {
            //this[side] = thumbnail;
            this.additionalImage.push(thumbnail);
            console.log(this.additionalImage);
          })
        }
        let item = { image: data, type: side }
        this.images.push(item);
        this.reservation.imagesDamage = this.images;
      }
    })
  }


  reloadImage() {
    if (!_.isUndefined(this.reservation.imagesEnd) && this.reservation.imagesEnd.length > 0) {
      this.reservation.imagesEnd.forEach(element => {
        this.reportImage = this.utilsService.thumbnailify(element.image, CONSTANTS.THUMBNAIL_SIZE, (thumbnail) => {
          this[element.type] = thumbnail;
          this.images.push(element);
        })
      });
    }
  }

  uploadReport() {
    // let time = new Date(this.reservation.end_time).getTime();
    delete this.reservation.imagesEnd
    if (this.images.length > 0) {
      this.uploadImage.doUpload(this.images, this.uploadUrl, { reservationId: this.reservation.id, status: 'END_TRIP' }).then((data) => {
        this.gocarLoading.hideLoading()
        this.goCarModal.showModalV2(AfterTrip, { data: this.reservation, service: this.serviceReservation })
      }).catch(err => {
        this.gocarLoading.hideLoading()
      })
    } else {
      this.gocarLoading.hideLoading()
      this.goCarModal.showModalV2(AfterTrip, { data: this.reservation, service: this.serviceReservation })
    }
  }

  updateStatus() {
    this.gocarLoading.showLoading('Uploading')
    this.reservation.comment = this.intOption();
    this.reservation.remark = this.remarks;
    if (this.images.length >= 4) {
      this.rentalDetailProvider.updateStatusRentalDetail(this.reservation, 'DURING_TRIP').subscribe(rs => {
        this.uploadReport()
      })
    } else {
      this.alertError();
    }
    // if (!_.isEmpty(this.reservation.comment) && !_.isEmpty(this.reservation.comment.split(',')[1])) {
    //   // this.reservation.comment = this.concatComment();
    //   this.rentalDetailProvider.updateStatusRentalDetail(this.reservation, new Date(this.reservation.inspection_time), 'DURING_TRIP').subscribe(rs => {
    //     this.uploadReport()
    //   })
    // } else if ((this.images.length >= 4 && this.other.includes('assets/imgs/other.png')) || (this.images.length >= 5 && !this.other.includes('assets/imgs/other.png'))) {
    //   // this.reservation.comment = this.concatComment();
    //   this.rentalDetailProvider.updateStatusRentalDetail(this.reservation, new Date(this.reservation.inspection_time), 'DURING_TRIP').subscribe(rs => {
    //     this.rentalDetailProvider.getRentalDetailById(this.reservation.id).subscribe(rs => {
    //       this.reservation = rs.data;
    //       this.uploadReport()
    //     })
    //   })
    // } else {
    //   this.alertError();
    // }

  }

  intOption() {
    let option = [];
    _.forEach(this.listOpt, (o) => {
      if (o.isCheck) {
        let data = {
          reservation_id: this.reservation.id,
          option: o.name,
          status: 'END_TRIP',
          date: new Date()
        }
        option.push(data);
      }
    })
    return option
  }

  // setupComment() {
  //   if (!_.isEmpty(this.reservation.comment)) {
  //     let after = this.reservation.comment.split(',')[1];
  //     if (!_.isEmpty(after)) {
  //       let comment = after.split(':')[1];
  //       let comlst = comment.split('|')
  //       if (!_.isEmpty(comlst[0])) {
  //         this.carExDirty = true;
  //       }
  //       if (!_.isEmpty(comlst[1])) {
  //         this.carInDirty = true;
  //       }
  //       if (!_.isEmpty(comlst[2])) {
  //         this.dent = true;
  //       }
  //       if (!_.isEmpty(comlst[3])) {
  //         this.cigarette = true;
  //       }
  //       if (!_.isEmpty(comlst[4])) {
  //         this.remarks = comlst[4];
  //       }
  //     }
  //   }
  // }

  alertError() {
    let alert = this.alertCtrl.create({
      title: 'Error',
      message: 'Please upload photos of the left, right, front, and back of the car as stated',
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    this.gocarLoading.hideLoading()
    alert.present();
  }

  alertFuelError() {
    let alert = this.alertCtrl.create({
      title: 'Error',
      message: 'Please confirm end fuel level!',
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }

  // choiceOption(data) {
  //   this[data] = !this[data];
  // }

  // concatComment() {
  //   let comment = ', after:';
  //   if (this.carExDirty) {
  //     comment += 'Car exterior dirty'
  //   }
  //   comment += '|'
  //   if (this.carInDirty) {
  //     comment += 'Car interior dirty'
  //   }
  //   comment += '|'
  //   if (this.dent) {
  //     comment += 'Dent'
  //   }
  //   comment += '|'
  //   if (this.cigarette) {
  //     comment += 'Cigarette smell'
  //   }
  //   comment += '|' + this.remarks;
  //   return this.reservation.comment.split(',')[0] + comment;
  // }
}



