import { Component } from '@angular/core';
import { AlertController, Alert } from 'ionic-angular';

/*
  Generated class for the AlertBox component.

  See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
  for more info on Angular 2 Components.
*/
@Component({
  selector: 'alert-box',
  templateUrl: 'alert-box.html'
})

export class AlertBoxComponent {
  
  constructor(private alertCtrl: AlertController) {
    console.log('Hello AlertBox Component');
  }

  createInfoAlert(title?: string, subTitle?: string, message?: string): Alert {
    return this.alertCtrl.create(
      {
        title: title,
        subTitle: subTitle,
        message: message,
        buttons: ['OK'],
        enableBackdropDismiss: false
      }
    );
  }

  createInfoAlertInfor(title?: string, subTitle?: string, message?: string): Alert {
    return this.alertCtrl.create(
      {
        title: title,
        subTitle: subTitle,
        message: message,
        buttons: ['OKay'],
        enableBackdropDismiss: false
      }
    );
  }

  createConfirmAlert(okCallback, title?: string, subTitle?: string, message?: string) {
    return this.alertCtrl.create(
      {
        title: title,
        subTitle: subTitle,
        message: message,
        inputs: [
          {
            name: 'promo',
            placeholder: 'promo'
          }
        ],
        buttons: [
          {
            text: 'Cancel'
          },
          {
            text: 'Save',
            handler: data => {
              if (data.promo) {
                okCallback(data.promo);
              } else {
                return false;
              }
            }
          }
        ]
      }
    );
  }

  createPromptAlert(title, message, input: {name: string, placeholder: string}, callback?) {
    return this.alertCtrl.create({
      title: title,
      message: message,
      inputs: [
        {
          name: input.name,
          placeholder: input.placeholder
        },
      ],
      buttons: [
        {
          text: 'Cancel'
        },
        {
          text: 'Submit',
          handler: data => {
              if(data[input.name]) {
                return callback(data)
              } else {
                return false
              }
          }
        }
      ]
    });
  }
}


