export abstract class Pricing {
    day?: number
    month?: number
    hour?: number
    total_price?: number
}

export class Car {
    bluetooth: number
    building_city: string
    building_id: string
    building_lat: string
    building_lng: string
    building_name: string
    building_street: string
    built_in_gps: number
    carTypeId: string
    daily_rate: number
    doors: number
    fuel: number
    hourly_rate: number
    id: string
    lock: any
    make: string
    model: string
    monthly_rate: number
    numOfReserve: number
    numOfCarType: number
    photo: string
    photo_dir: string
    pricing: Pricing
    seat: number
    transmission: number
    type: string
    year: string
    distance: Number
    building_instruction:string
    hub2hub:number

    constructor(car?) {
        car = car || {}
        this.carTypeId = car.carTypeId || null
        this.bluetooth = car.bluetooth || 0
        this.building_city = car.building_city || null
        this.building_id = car.building_id || null
        this.building_lat = car.building_lat || null
        this.building_lng = car.building_lng || null
        this.building_name = car.building_name || null
        this.building_street = car.building_street || null
        this.building_instruction = car.building_instruction || null
        this.built_in_gps = car.built_in_gps || 0
        this.daily_rate = car.daily_rate || 0
        this.doors = car.doors || 0
        this.fuel = car.fuel || 0
        this.hourly_rate = car.hourly_rate || 0
        this.id = car.id || null
        this.lock = car.lock || 0
        this.make = car.make || null
        this.model = car.model || null
        this.monthly_rate = car.monthly_rate || 0
        this.numOfReserve = car.numOfReserve || 0
        this.numOfCarType = car.numOfCarType || 0
        this.photo = car.photo || null
        this.photo_dir = car.photo_dir || null
        this.pricing = car.pricing || null
        this.seat = car.seat || 0
        this.transmission = car.transmission || 0
        this.type = car.type || null
        this.year = car.year || null
        this.distance = car.distance || 0
        this.hub2hub = car.hub2hub || 0
    }
}


