import { VerifyStatus } from "../shared/enums/verify-status.enum";
import { BlockStatus } from "../shared/enums/block-status.enum";
import { UserRole } from "../shared/enums/user-role.enum";

export class User {
    id: string
    firstName: string
    lastName: string
    email: string
    telephone: string
    role: string
    buildingId: string
    passport: string
    selfie: string
    licence: string
    isVerified: boolean
    isBlock: boolean
    area: string
    birthday: string
    password: string
    postCode: string
    address: string
    referenceCode: string
    icNumber:string

    constructor(user?) {
        user = user || {}
        this.id = user.id || null
        this.firstName = user.firstName || null
        this.lastName = user.lastName || null
        this.email = user.email || null
        this.telephone = user.telephone || null
        this.role = user.role || UserRole.USER
        this.buildingId = user.buildingId || null
        this.isVerified = user.isVerified || VerifyStatus.UNVERIFED
        this.isBlock = user.isBlock || BlockStatus.UNBLOCKED
        this.address = user.address || ''
        this.area = user.area || ''
        this.birthday = user.birthday || null
        this.referenceCode = user.referenceCode || ''
        this.icNumber = user.icNumber || ''
    }

    set _firstName(_firstname) {
        this.firstName = _firstname
    }

    set _lastName(_lastName) {
        this.lastName = _lastName
    }
    
    set _email(_email) {
        this.email = _email
    }

    set _telephone(_telephone) {
        this.telephone = _telephone
    }

    set _birthday(_birthday) {
        this.birthday = _birthday
    }

    set _postCode(_postCode) {
        this.postCode = _postCode
    }

    set _area(_area) {
        this.area = _area
    }

    set _referenceCode (_referenceCode) {
        this.referenceCode = _referenceCode;
    }

}


