import { Component } from "@angular/core";
import { GoCarCamera } from "../camera/gocar-camera";
import { ViewController } from "ionic-angular";

@Component({
    selector: 'select-image',
    templateUrl: 'select-image.html'
})
export class SelectImage {
    constructor(private gocarCamera: GoCarCamera, private viewCtrl: ViewController) {}

    /**
     * 
     * @param sourceType 0 = gallery, 1 = camera
     */
    selectImage(sourceType) {
        this.gocarCamera.initCamera(sourceType).then(img => {
            let data = {}
            this.close(img)
        }).catch(err => {
            this.close
        })
    }

    close(data?) {
        this.viewCtrl.dismiss(data)
    } 
}


