import { Http, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { BaseService } from '../base.service';
import { AppConfig } from '@app/app.config';
import { Observable } from 'rxjs';
import { Storage } from '@ionic/storage';
import { FiltersProvider } from '../fiters/filters';
import { NativeStorageService } from '../storage/native-storage.service';
import { TimeService } from '@providers/time.service';
import * as moment from "moment"
/*
  Generated class for the CarsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class BuidingsProvider extends BaseService {

  constructor(public http: Http, protected appConfig: AppConfig, protected storage: Storage, public filterProviders: FiltersProvider, private nativeStorageService: NativeStorageService, private timeService: TimeService) {
    super(http, appConfig);
  }

  getListBuilding(): Observable<any> {
    let startTime = this.filterProviders.getBookingTimeStart()
    let endTime = this.filterProviders.getBookingTimeEnd()
    let timeStart = moment(startTime)
    if (timeStart.isBefore(this.timeService.getNow())) {
      this.filterProviders.validateTime()
      startTime = this.filterProviders.getBookingTimeStart()
      endTime = this.filterProviders.getBookingTimeEnd()
    }
    let carType = this.filterProviders.getCarType()
    let body = {
      start_time: startTime,
      end_time: endTime,
      filter: {
        models: carType
      }
    }
    return this.postData('buildings/list', body)
      .map((res: Response) => {
        let resData = res.json()
        console.log('resData', resData)
        let buildings = resData.data
        buildings.forEach(element => {
          element.available = (element.totalCars - element.totalReserve) > 0
        });
        return buildings
      })
      .catch((err) => {
        console.log(err);
        return Observable.throw(false);
      });
  }

  getNearestBuilding(lat, lng): Observable<any> {
    return this.getData('buildings/getNearest/' + lat + '/' + lng)
      .catch((err) => {
        console.log(err);
        return Observable.throw(false);
      });
  }

  getListBuildingName(): Observable<any> {
    return Observable.fromPromise(this.cacheBuilding()).map((result) => {
      return result.data
    })
  }

  async cacheBuilding() {
    // let exists = await this.nativeStorageService.getKey('buildings')
    // if (exists) {
    //   let data = await this.nativeStorageService.getData('buildings')
    //   return JSON.parse(data);
    // }
    // let buildings = await this.getData('buildings/list/name').toPromise()
    // await this.nativeStorageService.setData('buildings', buildings)
    // return buildings

    
    let buildings = await this.getData('buildings/list/name').toPromise()
    return buildings
  }

}



