import { Injectable } from "@angular/core";
import { AbstractControl } from "@angular/forms";
import moment from 'moment'

@Injectable()
export class CustomValidator {
    required(form) {
        return form.value.toString().trim() != '' ? null : {
            required: true
        }
    }

    minLength(min) {
        return 
    }

    matchPassword(ac: AbstractControl) {
        let password = ac.get('password').value
        let rePassword = ac.get('repassword').value

        if (password.trim() != rePassword.trim()) {
            ac.get('repassword').setErrors({matchPassword: true})
        } else {
            ac.get('repassword').setErrors(null)
        }
    }

    birthDate(form) {
        return moment(form.value).isBefore(moment().format('YYYY-MM-DD')) ? null : {
            invalid: true
        }
    }
}


