import { ViewController } from 'ionic-angular';
import { DropOffCard } from './../../shared/components/drop-off-card/drop-off-card';
import { BuidingsProvider } from '@providers/building/building';
import { Component, OnDestroy, OnInit, style } from '@angular/core';
import { NavController, Events, App, NavParams, Nav } from 'ionic-angular';
import { AuthProvider } from '@providers/auth/auth';
import { UserPage } from '@pages/user/user';
import * as _ from 'lodash'
import { Hub2hubProvider } from "@providers/hub2hub/hub2hub";

@Component({
  selector: 'page-drop-location',
  templateUrl: 'drop-location.html'
})
export class DropLocationPage {
  listDropOffLocation = [];
  itemCheck = [];
  constructor(private navCtrl: NavController,
    private viewCtrl: ViewController,
    private authServie: AuthProvider,
    public buidingsProvider: BuidingsProvider,
    public hub2hubProvider: Hub2hubProvider,
    public navParams: NavParams) {
    this.getListHub2Hub();
  }

  itemSelected(item) {
    this.itemCheck = [];
    this.itemCheck.push(item)
    this.viewCtrl.dismiss({ building: item })
  }
  close() {
    this.viewCtrl.dismiss({ data:  this.itemCheck[0] })
  }
  dropOffLocationSelected(location) {
    return _.find(this.itemCheck, loc => {
      return loc.id == location.id
    })
  }
  getListHub2Hub() {
    let pickupId = this.navParams.get('pickupId')
    this.itemCheck = [this.navParams.get('dropOffLocation')];
    this.hub2hubProvider.listHub2Hub(pickupId).subscribe(result => {
      this.listDropOffLocation = result.data;
    });
  }
}



