import { CorporateProvider } from '@providers/corporate/corporate';
import { Component, OnInit } from "@angular/core";
import { Events, NavController, Platform } from "ionic-angular";
import { UserIdentify } from "../user-identify/user-identify";
import { User } from "@models/user";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { CustomValidator } from "@shared/validator/validator";
import { GoCarCamera } from "@shared/components/camera/gocar-camera";
import { GoCarPopover } from "@shared/components/popover/gocar-popover";
import { CONSTANTS } from "@common/constants";
import { UserRegisterProvider } from "../user.provider";
import { UtilsService } from "@providers/utils.service";
import { Keyboard } from '@ionic-native/keyboard';
import { MbscDatetimeOptions } from '@lib/mobiscroll/js/mobiscroll.angular.min';
// import { FirebaseDynamicLinks } from '@ionic-native/firebase-dynamic-links';
import { AuthProvider } from "@providers/auth/auth";
import { Observable } from "../../../../../node_modules/rxjs";
import $ from 'jquery'
@Component({
    selector: 'user-detail',
    templateUrl: './user-detail.html',
    // providers: [FirebaseDynamicLinks]
})

export class UserDetail implements OnInit {
    isKeyBoard: boolean;
    listCompany;
    companyChecked = '';
    showCompanyList = false;
    visibleFormCompany = false;
    public user: User
    public telephone: any
    public isValidPhone: any = true
    public isExistPhone: any = false
    public images: any[] = []
    public formRegister: FormGroup
    public telInput: any
    public currentDate: string
    public referenceCode: any;
    public BirthDateOptions: MbscDatetimeOptions = {
        theme: 'gocar',
        display: 'bottom'
    }
    constructor(public events: Events,
        public _fb: FormBuilder,
        public popover: GoCarPopover,
        public authService: AuthProvider,
        public userProvider: UserRegisterProvider,
        public gocarCamera: GoCarCamera,
        public utilsService: UtilsService,
        public validator: CustomValidator,
        public navCtrl: NavController,
        public keyboard: Keyboard,
        public corporateProvider:CorporateProvider,

        // private firebaseDynamicLinks: FirebaseDynamicLinks,
        public platform: Platform) {
        this.initialForm()
        let date = new Date()
        this.currentDate = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
        // BEGIN: Retrieve list coporate, open when the coporate is ready
        // this.listCorporate();
        // END: Retrieve list coporate, open when the coporate is ready
    }

    ngOnInit() {
        if (!this.platform.is('webmobile')) {
            this.keyboard.onKeyboardShow().subscribe(() => {
                this.isKeyBoard = true;
            });
            this.keyboard.onKeyboardHide().subscribe(() => {
                this.isKeyBoard = false;
            });
        }
    }

    listCorporate(){
        this.corporateProvider.listCorporate().subscribe(result=>{
            console.log(result)
            this.listCompany = result.data;
        })
    }

    companyCheck() {
        this.visibleFormCompany = !this.visibleFormCompany;
    }

    isChecked(event) {
        this.companyChecked = event
        this.showCompanyList = !this.showCompanyList;
    }

    onFocus() {
        this.showCompanyList = !this.showCompanyList;
    }


    ionViewWillEnter() {
        this.addKeyboardListeners();
    }

    private addKeyboardListeners() {
        Observable.fromEvent(window, 'keyboardWillShow').subscribe(e => {
            // Code here
            console.log("keyboardWillShow");
            document.body.classList.add('keyboard-is-open');
        });

        Observable.fromEvent(window, 'keyboardWillHide').subscribe(e => {
            // Code here
            console.log("keyboardWillHide");
            document.body.classList.remove('keyboard-is-open');


        });
    }

    initialForm() {
        this.formRegister = this._fb.group({
            firstName: ['', [this.validator.required]],
            lastName: ['', [this.validator.required]],
            repassword: ['', [this.validator.required]],
            password: ['', [this.validator.required,
            Validators.pattern(CONSTANTS.PASSWORD_REGEX),
            Validators.minLength(CONSTANTS.PASSWORD_MINLENGTH)]],
            birthday: ['', [this.validator.required, this.validator.birthDate]],
            address: ['', [this.validator.required]],
            postCode: ['', [Validators.required]],
            icNumber: ['', [Validators.required]],
            email: ['', [this.validator.required]],
            referenceCode: ['']
        }, { validator: this.validator.matchPassword })
    }

    next() {
        this.formRegister.value.referenceCode = this.referenceCode;
        let data = this.formRegister.value
        data['telephone'] = this.buildTelephone()
        this.userProvider.setUser(data)
        this.navCtrl.push(UserIdentify)
    }

    onPhoneChanged(data) {
        this.telInput = data.phone
        this.isExistPhone = false
        if (this.telInput) {
            this.isValidPhone = CONSTANTS.ONLY_NUMBER_REGEX.test(this.telInput)
        } else {
            this.isValidPhone = true
        }
    }

    buildTelephone() {
        if (!this.telephone) return null
        let phone = this.telephone.phone
        if (phone.charAt(0) == '0') phone = phone.substring(1)
        return '+' + this.telephone.dialCode + phone
    }

    onLostFocus() {
        let phone = this.buildTelephone()
        if (!phone) return
        this.authService.checkExist({ telephone: phone }).subscribe((res) => {
            this.isExistPhone = res.data
        })
    }

    onEmailLostFocus() {
        let email = this.getControl('email')
        if (!email.value || email.invalid) return
        if (!CONSTANTS.EMAIL_REGEX.test(email.value)) {
            return email.setErrors({ pattern: true })
        }
        this.authService.checkExist({ email: email.value }).subscribe((res) => {
            if (res.data) {
                email.setErrors({ exists: true })
            }
        })
    }

    getControl(ctrlName) {
        return this.formRegister.get(ctrlName)
    }
}


