export class PayMentMethod {
    cardType: any
    ccName: any
    ccNo: any
    displayNo: any
    expiryDate: any
    id: any
    s_bankname: any
    s_country: any
    status: any
    tokenId: any
    tranDate: any
    transId: any
    user_id: any

    constructor(method) {
        this.cardType = method.cardType || null
        this.ccName = method.ccName || null
        this.ccNo = method.ccNo || null
        this.displayNo = method.displayNo || null
        this.expiryDate = method.expiryDate || null
        this.id = method.id || null
        this.s_bankname = method.s_bankname || null
        this.s_country = method.s_country || null
        this.tokenId = method.tokenId || null
        this.tranDate = method.tranDate || null
        this.transId = method.transId || null
        this.user_id = method.user_id || null
    }
}


