import { Injectable } from '@angular/core';
import { BaseService } from '../base.service';
import { Http, Response } from '@angular/http';
import { AppConfig } from '@app/app.config';
import { Observable } from 'rxjs';
import { CONSTANTS } from '@common/constants';
import { GoCarStorageService } from '../storage/localstorage.service';
import {JwtHelper} from 'angular2-jwt'
import { GlobalService } from '../global.provider';
import { Events } from 'ionic-angular';
import { AppEvent } from '@common/appEvent';
import { AlertBoxComponent } from '@shared/components/alert-box/alert-box';
/*
  Generated class for the AuthProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthProvider extends BaseService {

  constructor(public http: Http, 
              public gocarStorage: GoCarStorageService,
              public jwtHelper: JwtHelper,
              public events: Events,
              public alertBox: AlertBoxComponent,
              public global: GlobalService,
              protected appConfig: AppConfig) {
    super(http, appConfig, alertBox, events, global);
    console.log('Hello AuthProvider Provider');
  }

  loginWithEmailAndPassword(email: string, password: string): Observable<any> {
    console.log('loginWithEmailAndPassword');
    let authentication = email + ':' + password;
    return this.loginApiCall(authentication);
  }

  private loginApiCall(authentication: any): Observable<any> {
    if (authentication) {
      let authHeader = { 'gocar-authentication': authentication };
      return this.postData('auth/login', null, authHeader)
        .map(this.handleAuthResponse.bind(this))
        .catch((err) => {
          console.log(err);
          return Observable.throw(err);
        });
    } else { return Observable.of(false); }
  }

  public registerUser(user: any): Observable<any> {
    return this.postData('user/register', user)
               .map(res => res.json())
               .catch(err => Observable.throw(err))
  }

  public checkExist(data): Observable<any> {
    return this.postData('user/exists', data)
               .map((res: Response) => res.json())
               .catch(err => Observable.throw(err))
  }

  public updateRequestDoccument(data): Observable<any> {
    return this.postData('user/update-request-document', data)
               .map((res: Response) => res.json())
               .catch(err => Observable.throw(err))
  }

  private handleAuthResponse(res: Response): string {
    let returndata = res.json().data
    let token = returndata.token;
    this.global.sethasReservations(returndata.isHasReservations)
    this.gocarStorage.setData(CONSTANTS.GOCAR_TOKEN, token);
    return token;
  }

  public doLogOut() {
    return new Promise((resolve, reject) => {
      console.log('logged out')
        this.gocarStorage.cleardata(CONSTANTS.GOCAR_TOKEN)
        this.gocarStorage.cleardata(CONSTANTS.RESERVATION_KEY)
        this.gocarStorage.cleardata(CONSTANTS.RESERVATION_RETURN_KEY)
        this.global.sethasReservations(false)
        // this.events.publish(AppEvent.AppLoggedOut)
        return resolve()
    })
  }

  public get userId() {
      let info = this.jwtHelper.decodeToken(this.getCredentials()) || {}
      return info.uid
  }

  public getCredentials() {
    return this.gocarStorage.getData(CONSTANTS.GOCAR_TOKEN)
  }

  public get isAuthenticated() {
    return this.getCredentials()
  }

  public getUserDatasByToken() {
    return this.getData('user/verify/token')
            .map(res => res)
            .catch(err => Observable.throw(err));
  }

}



