import { Reservation } from './../../shared/enums/tab-reservation.enum';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavController, Events, App } from 'ionic-angular';
import { BookCarPage } from '@pages/book-car/book-car';
import { CarsProvider } from '@providers/cars/cars';
import { LoadingComponent } from '@shared/components/loading/loading';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { CarFilterModal } from '@pages/car-filter/car-filter';
import { AuthProvider } from '@providers/auth/auth';
import { UserPage } from '@pages/user/user';
import { CarFilterMode } from '@shared/enums/car-filter-mode.enum';
import { FiltersProvider } from '@providers/fiters/filters';
import { AppEvent } from '@common/appEvent';
import { UtilsService } from '@providers/utils.service';
import { GoCarToast } from '@shared/components/toast/gocar-toast';
import { ViewInfoComponent } from '@shared/components/view-info/view-info';
import { ReservationsProvider } from '@providers/reservations/reservations';
import { ReservationPage } from '@pages/resevation/reservation/reservation';
import { GlobalService } from '@providers/global.provider';

@Component({
  selector: 'page-list',
  templateUrl: 'list.html'
})
export class ListPage implements  OnDestroy , OnInit {
  cars = [];
  filterData: any
  isLoadingCar: any = false
  listExpired = []
  listInfo: { page: number, itemsPerPage: number, totalItems: number, totalPages: number }
  position:any
  reservation = Reservation
  constructor(private navCtrl: NavController,
    private authServie: AuthProvider,
    private filterProvider: FiltersProvider,
    private events: Events,
    private gocarToast: GoCarToast,
    private utilService: UtilsService,
    private gocarModal: GoCarModal,
    private carsProvider: CarsProvider,
    private loading: LoadingComponent,
    private reservationsProvider: ReservationsProvider,
    private app: App,
    private globalService: GlobalService) {
      // using for load more
    this.listInfo = {
      page: 0,
      itemsPerPage: 4,
      totalItems: 0,
      totalPages: 0
    }
    this.onTimeChange()
    this.getFilterData()
    this.position =  {lat: Number(3.172322), lng:Number( 101.70818)};
    this.events.subscribe(AppEvent.AppGoOnline, () => {
      this.loadCars()
    })
  }
  ionViewDidEnter() {
    this.getListExpired();
    this.loadCars(0)
  }

  getFilterData() {
    this.filterData = {
      cities: this.filterProvider.getCities(),
      models: this.filterProvider.getCarType()
    }

    console.log(this.filterData);
  }

  ngOnInit(){
    // this.filterProvider.validateTime();
  }

  loadCars(pageIndex?, limit?) {
    if(!this.networkStatus) return false
    this.loading.showLoading();
    this.isLoadingCar = true;
    console.log(this.filterData);
    this.carsProvider.getCars(pageIndex, this.filterData, null, limit).subscribe((result) => {
      this.loading.hideLoading();
      this.cars = result.cars;
      this.isLoadingCar = false
      this.listInfo.totalPages = result.totalPages
    }, err => {
      this.loading.hideLoading()
      this.isLoadingCar = false
      this.gocarToast.showToast({ duration: 2000, message: err.message })
    })
  }

  get networkStatus() {
    return navigator.onLine
  }

  // called
  private bookNow(car) {
    if (!this.utilService.isValidBookingTime()) {
      this.gocarToast.showToast({ duration: 2000, message: 'Invalid DateTime' })
    } else if (this.authServie.isAuthenticated) {
      this.loading.showLoading('Loading...')
      this.carsProvider.pickCar(car.building_id, car.carTypeId).subscribe((picked) => {
        this.loading.hideLoading()
        this.navCtrl.push(BookCarPage, { car: picked });
      }, err => {
        this.gocarToast.showToast({ message: err.message })
        this.loading.hideLoading()
      })
    } else {
      this.gocarModal.showModal(UserPage)
    }
  }

  eventBuilding(car) {
    this.gocarModal.showModal(ViewInfoComponent, { data: car })
  }

  onTimeChange() {
    this.events.subscribe(AppEvent.TimeChanged, (isChanged) => {
      if (isChanged) {
        this.loadCars(0, (this.listInfo.page + 1) * this.listInfo.itemsPerPage)
      }
    })
  }

  doInfinite(infiniteScroll) {
    this.listInfo.page++
    this.carsProvider.getCars(this.listInfo.page, this.filterData).subscribe((result) => {
      let cars = result.cars
      this.listInfo.totalPages = result.totalPages
      for (let item of cars) {
        this.cars.push(item)
      }
      infiniteScroll.complete()
    })
  }

  private filter() {
    this.gocarModal.showModal(CarFilterModal, { mode: CarFilterMode.LIST })
    this.gocarModal.currentModal.onDidDismiss((isFilter) => {
      if (isFilter) {
        this.getFilterData()
        this.loadCars(0)
      }
    });
  }

  ngOnDestroy() {
    this.events.unsubscribe(AppEvent.TimeChanged)
    this.events.unsubscribe(AppEvent.AppGoOnline)
  }

  getListExpired() {
    if (this.authServie.isAuthenticated) {
      this.reservationsProvider.getListExpired(this.authServie.userId).subscribe((result) => {
        if (result.data) {
          this.listExpired = result.data
        }
      }, err =>
          console.log(err)
      )
    }
  }

  eventExpired() {
    this.globalService.setReservation(this.listExpired);
    this.app.getRootNav().setRoot(ReservationPage, {pageType:this.reservation.EXPIRED})
  }

}



