import { Component, Input, OnInit } from "@angular/core";
import { ViewController, AlertController } from "ionic-angular";
import { GoCarPopover } from "@shared/components/popover/gocar-popover";
import { FuelReportPopupComponent } from '../fuel-report-popup/fuel-report-popup';
import * as _ from 'lodash';



@Component({
    selector: 'fuel-report',
    templateUrl: 'fuel-report.html'
})
export class FuelReportComponent implements OnInit {
    @Input() reservation: any
    @Input() service: any
    @Input() fieldValue: any
    data = [
        {index: 8, value: false},
        {index: 7, value: false},
        {index: 6, value: false},
        {index: 5, value: false},
        {index: 4, value: false},
        {index: 3, value: false},
        {index: 2, value: false},
        {index: 1, value: false}
    ]
    constructor(public viewCtrl: ViewController,
                private alertCtrl: AlertController,
                private popover: GoCarPopover,) {
    }

    ngOnInit() {
        this.data.forEach(element => {
            if (element.index <= this.reservation[this.fieldValue]) {
                element.value = true;
            } else {
                element.value = false;
            }
        });
    }

    fillColor(obj) {
        if (_.isNull(this.reservation[this.fieldValue]) || this.reservation[this.fieldValue] == 0) {
            this.data.forEach(element => {
                if (element.index <= obj.index) {
                    element.value = true;
                } else {
                    element.value = false;
                }
            });
            this.confirmFuel(obj.index);
        }
    }

    presentConfirm(index) {
        let alert = this.alertCtrl.create({
          title: 'Confirm Fuel',
          message: 'Do you want to confirm fuel level?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
                this.data.forEach(element => {
                    if (element.index <= this.reservation[this.fieldValue]) {
                        element.value = true;
                    } else {
                        element.value = false;
                    }
                });
              }
            },
            {
              text: 'Agree',
              handler: () => {
                this.reservation[this.fieldValue] = index;
                this.service.updateFuelLevel(this.reservation)
                console.log('update fuel successful');
              }
            }
          ]
        });
        alert.present();
      }

      confirmFuel(index) {
       this.popover.show(FuelReportPopupComponent, {data: index}).then(data => {
            this.reservation[this.fieldValue] = data;
            this.data.forEach(element => {
                if (element.index <= this.reservation[this.fieldValue]) {
                    element.value = true;
                } else {
                    element.value = false;
                }
            });
            this.service.updateFuelLevel(this.reservation)
        })
      }
}


