import { Component } from "@angular/core";
import { Events, MenuController, IonicPage, NavParams } from "ionic-angular";

@IonicPage()
@Component({
    selector: 'register-done',
    templateUrl: './register-done.html'
})

export class RegisterDone {

    userData = [];
    
    constructor(public events: Events, public menuCtrl: MenuController, private navParams: NavParams) {
        this.userData = this.navParams.get('user') || {};
    }

}


