import { Http, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { BaseService } from '../base.service';
import { AppConfig } from '@app/app.config';
import { Observable } from 'rxjs';
import { AuthProvider } from '../auth/auth';
import { AlertBoxComponent } from '@shared/components/alert-box/alert-box';
import { Events } from 'ionic-angular';
import { GlobalService } from '../global.provider';
/*
  Generated class for the CarsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class PricingProvider extends BaseService {

    constructor(public http: Http,
        public auth: AuthProvider,
        public alertBox: AlertBoxComponent,
        public events: Events,
        public global: GlobalService,
        protected appConfig: AppConfig) {
            super(http, appConfig, alertBox, events, global);
        console.log('Hello CarsProvider Provider');
    }

    getPrice(body): Observable<any> {
        return this.postData(`pricing/calculate`, body)
            .map((res: Response) => res.json())
            .catch((err) => {
                return Observable.throw(err);
            });
    }

    getTax(price) {
        return this.postData('pricing/tax', {price})
            .map((res: Response) => res.json())
            .catch((err) => {
                return Observable.throw(err)
            })
    }

    getChangeReservationPrice(data): Observable<{current: any, previous: any}> {
        let data1 = {
            carId: data.carId,
            userId: data.userId,
            startTime: data.startTime,
            endTime: data.endTime,
            duration: null,
            isTax: false,
            buildingId: data.buildingId,
          }
      
          let data2 = {
            carId: data.carId,
            userId: data.userId,
            startTime: data.newStartTime,
            endTime: data.newEndTime,
            duration: null,
            isTax: false,
            buildingId: data.buildingId,
          }
          return Observable.forkJoin([
            this.getPrice(data1),
            this.getPrice(data2)
          ]).map((data) => {
            let previous: any = data[0].data
            let current: any = data[1].data
            return {
              previous,
              current
            }
          }).catch((err) => {
            return Observable.throw(err)
          })
    }
}



