import { Component, OnInit, OnDestroy } from "@angular/core";
import { ViewController, NavParams } from "ionic-angular";
import { SmsService } from "@providers/sms/sms.provider";
import { GoCarToast } from "../toast/gocar-toast";
import { SMSVERIFYSTATUS } from "../../enums/sms-verify-status.enum";
import { LoadingComponent } from "../loading/loading";

@Component({
    selector: 'check-pincode',
    templateUrl: './check-pincode.html',
})
export class CheckPinCode implements OnInit, OnDestroy {
    timeLeft: number = 30;
    timerId: any
    phoneNumber: any
    code: string
    constructor(private viewCtrl: ViewController, 
                private smsService: SmsService,
                private gocarToast: GoCarToast,
                private gocarLoading: LoadingComponent,
                private navParam: NavParams) {
        this.phoneNumber = this.navParam.get('phone')
    }

    ngOnInit() {
        this.initInterval()
    }

    initInterval() {
        this.timerId = setInterval(() => {
            this.countdown()
        }, 1000)
    }

    verify() {
        if(!this.phoneNumber) return;
        this.gocarLoading.showLoading('Loading...')
        this.smsService.verifyOPT(this.phoneNumber, this.code).subscribe(result => {
            if (result.data != SMSVERIFYSTATUS.UNVERIFY) {
                this.close(true)
            } else {
                this.gocarToast.showToast({message: 'Invalid pincode'})
            }
            this.gocarLoading.hideLoading()
        }, err => {
            this.gocarToast.showToast({message: 'Server Error'})
            this.gocarLoading.hideLoading()
        })
    }

    requestNewOne() {
        this.gocarLoading.showLoading('Loading...')
        this.smsService.generateOPT(this.phoneNumber).subscribe(ret => {
            this.gocarLoading.hideLoading()
            this.timeLeft = 30
            this.initInterval()
        }, err => {
            console.log(err)
            this.gocarLoading.hideLoading()
            this.gocarToast.showToast({message: err.message})
        })
    }

    countdown() {
        if (this.timeLeft == -1) {
            clearInterval(this.timerId);
        } else {
            this.timeLeft--;
        }
    }

    close(status) {
        this.viewCtrl.dismiss(status)
    }

    ngOnDestroy() {
        clearInterval(this.timerId)
    }
}


