import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import _ from 'lodash';

import { WalletProvider } from '@providers/wallet/wallet';
import { CreditType } from '@shared/enums/credit-type.enum';
import { LoadingComponent } from '@shared/components/loading/loading';

/**
 * Generated class for the CreditHistoryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-credit-history',
  templateUrl: 'credit-history.html',
})
export class CreditHistoryPage {
  
  tabsCreditHistory: string = "earn";
  creditEarn: any = [];
  creditRedeem: any = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, private walletProvider: WalletProvider, private gocarLoading: LoadingComponent) {
  }

  ionViewDidLoad() {
    
    this.gocarLoading.showLoading("Loading...");

    this.walletProvider.getWalletTransactionInfoByUser().subscribe(result => {

      _.forEach(result.data, (item) => {
        //credit_type = 4: tab credit redeemed
        if(item.credit_type === CreditType.PAYMENT) {
          this.creditRedeem.push(item);
        } else {
          this.creditEarn.push(item);
        }
      });
      this.gocarLoading.hideLoading();
      
    }, err => this.gocarLoading.hideLoading())
  }

}



