import { Reservation } from './../../../shared/enums/tab-reservation.enum';
import { OnDestroy } from '@angular/core';
// import { Events } from 'ionic-angular';
import { ReservationExpiredPage } from '../reservation-expired/reservation-expired';
import { Component, Input } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { ReservationDrivingPage } from '../reservation-driving/reservation-driving';
import { ReservationReturnedPage } from '../reservation-returned/reservation-returned';

/**
 * Generated class for the ReservationTabsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-reservation-tabs',
  templateUrl: 'reservation-tabs.html',
})
export class ReservationTabsPage {
  tab1Root: any = ReservationDrivingPage;
  tab2Root: any = ReservationReturnedPage;
  tab3Root: any = ReservationExpiredPage;
  index = 0;
  reservation = Reservation
  constructor(public navCtrl: NavController,
  public navParams: NavParams) {
    this.checkFromExpired()
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ReservationTabsPage');
  }

  checkFromExpired() {
    let pageType = this.navParams.get('pageType')
    if (pageType == this.reservation.EXPIRED) {
      this.index = 2;
    }
  }
}



