import moment from 'moment';
import { Injectable } from "@angular/core";
import * as _ from 'lodash'
import { CONSTANTS } from "@common/constants";
import { FiltersProvider } from './fiters/filters';
@Injectable()
export class UtilsService {

    constructor(private filterProvider: FiltersProvider) { }
    /**
     * function to add an object to array without dupplication
     * @param arr destination array
     * @param dest obj to add to arr
     * @param field field to compare, not required
     */
    public addItemInArray(arr, dest, field?) {
        let item = _.find(arr, obj => {
            if (field) {
                return obj[field] == dest[field]
            }
            return obj == dest
        })
        if (item) {
            item = dest
        } else {
            arr.push(dest)
        }
    }
    /**
     * Get distance from point to point by kilometers
     * @param point1 lat: number, lng: number
     * @param point2 lat: number, lng: number
     */
    public getDistanceFromPointToPoint(point1, point2): any {
        var lat = [point1.lat, point2.lat]
        var lng = [point1.lng, point2.lng]
        var R = 6378137;
        var dLat = (lat[1] - lat[0]) * Math.PI / 180;
        var dLng = (lng[1] - lng[0]) * Math.PI / 180;
        var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
            Math.cos(lat[0] * Math.PI / 180) * Math.cos(lat[1] * Math.PI / 180) *
            Math.sin(dLng / 2) * Math.sin(dLng / 2);
        var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
        var d = R * c;
        return Math.round(d).toFixed(2);
    }

    public getPrice(priceHour, priceDay, priceMonth, startTime, endTime, hours?) {
        let month = 0;
        let day = 0;
        let hour = 0;
        let time = hours ? hours : this.getHoursBetweenTwoDate(startTime, endTime);
        let totalPrice = 0;
        if ((time / CONSTANTS.DAY) < 30) {
            day = _.toInteger(time / CONSTANTS.DAY);
            hour = time % CONSTANTS.DAY;
            totalPrice = day * priceDay + hour * priceHour;
        } else {
            month = _.toInteger(time / CONSTANTS.MONTH);
            day = _.toInteger((time % CONSTANTS.MONTH) / CONSTANTS.DAY);
            hour = (time - (month * CONSTANTS.MONTH) - (day * CONSTANTS.DAY));
            totalPrice = month * priceMonth + day * priceDay + hour * priceHour;
        }
        let data = {
            month: month,
            day: day,
            hour: hour,
            total_price: totalPrice
        }
        return data;
    }

    public isValidBookingTime() {
        let start = this.filterProvider.getBookingTimeStart()
        let end = this.filterProvider.getBookingTimeEnd()
        return !(moment(start).isSame(end) || moment(start).isAfter(end))
    }

    public getHoursBetweenTwoDate(startTime, endTime) {
        if (!startTime || !endTime) {
            return 0;
        }
        let a = moment(endTime, CONSTANTS.BOOKING_TIME_FORMAT)
        let b = moment(startTime, CONSTANTS.BOOKING_TIME_FORMAT)
        return a.diff(b, 'hours');
    }

    public thumbnailify(base64Image, targetSize, callback) {
        var img = new Image();

        img.onload = function () {
            var width = img.width,
                height = img.height,
                canvas = document.createElement('canvas'),
                ctx = canvas.getContext("2d");

            canvas.width = canvas.height = targetSize;

            ctx.drawImage(
                img,
                width > height ? (width - height) / 2 : 0,
                height > width ? (height - width) / 2 : 0,
                width > height ? height : width,
                width > height ? height : width,
                0, 0,
                targetSize, targetSize
            );

            callback(canvas.toDataURL());
        };

        img.src = base64Image;
    }

    public sortCars(cars: any[]) {
        cars.sort((a, b) => {
            if(a.distance) return a.distance > b.distance ? 1 : -1
            return a.building_city < b.building_city ? 1 : -1
        })
    }
}


