import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { Hub2hubProvider } from '@providers/hub2hub/hub2hub';
import { DropLocationPage } from './../../../pages/drop-location/drop-location';
import { Component, Output, EventEmitter, Input, ViewChild, SimpleChanges, OnChanges } from "@angular/core";
import { ViewController, Nav, NavParams, NavController } from "ionic-angular";
import { AppMode } from "../../enums/app-mode.enum";
import { Building } from '@models/building';

@Component({
    selector: 'drop-off-card',
    templateUrl: './drop-off-card.html'
})
export class DropOffCard implements OnChanges{
    @Input() pickUpLocation;
    @Output() onDropoff: EventEmitter<any> = new EventEmitter()
    currentDropoff: Building
    constructor(public viewCtrl: ViewController,
        public hub2hubProvider: Hub2hubProvider,
        public gocarModal: GoCarModal,
        public navCtrl: NavController,
        public navParams: NavParams
    ) {
    }

    ngOnChanges(changes: SimpleChanges) {
        if(changes.pickUpLocation && changes.pickUpLocation.currentValue) {
            this.getListHub2Hub();
        }
    }

    ngAfterViewInit() {
        
    }

    dropOff() {
        this.gocarModal.showModal(DropLocationPage, { pickupId: this.pickUpLocation.building_id, dropOffLocation: this.currentDropoff })
        this.gocarModal.currentModal.onDidDismiss((data) => {
            this.currentDropoff = data.building;
            this.onDropoff.emit(this.currentDropoff)
        })

    }
    getListHub2Hub() {
        this.hub2hubProvider.listHub2Hub(this.pickUpLocation.building_id).subscribe(result => {
            this.currentDropoff = result.data[0]
            this.onDropoff.emit(result.data[0])
        });
    }
}



