import { Component, OnInit } from "@angular/core";
import { ViewController, NavParams } from 'ionic-angular';
import * as _ from 'lodash'

import { CarTypesProvider } from '@providers/carTypes/cartypes.provider';
import { CarType } from "@models/cartype";
import { GoCarToast } from "@shared/components/toast/gocar-toast";
import { Building } from "@models/building";
import { BuidingsProvider } from "@providers/building/building";
import { CarFilterMode } from "@shared/enums/car-filter-mode.enum";
import { FiltersProvider } from "@providers/fiters/filters";

@Component({
    selector: 'car-filter',
    templateUrl: 'car-filter.html'
})

export class CarFilterModal implements OnInit {
    private modes = CarFilterMode
    private carTypes: CarType[] = []
    private buildings = [];
    //call in html template
    private mode: any = this.modes.MAP
    private isLoadingCarType: boolean = false
    private roundTrips = [{ trip: 'Round trip' }, { trip: 'Round trip & one-way' }]
    private isCheck: boolean = false;
    private isLoadingLocations: boolean = false
    private filter: { models: any[], distance: any, locations: any[], hideAvailbleCar: any, trips: any[] }
    constructor(private viewCtrl: ViewController,
        private navParams: NavParams,
        private filterProvider: FiltersProvider,
        private buildingService: BuidingsProvider,
        private gocarToast: GoCarToast,
        private cartypeProvider: CarTypesProvider) {
        this.filter = {
            models: Object.assign([], filterProvider.getCarType()),
            distance: 5,
            hideAvailbleCar: false,
            locations: Object.assign([], filterProvider.getCities()),
            trips: Object.assign([], null)
        }
        this.mode = this.navParams.get('mode')
    }

    ngOnInit() {
        this.loadCartype()
        this.loadBuilding()
    }

    loadBuilding() {
        this.buildings = [];
        this.isLoadingLocations = true
        this.buildingService.getListBuildingName().subscribe(result => {
            let group = _.groupBy(result, 'state')
            this.groupBuilding(group);
            this.isLoadingLocations = false
        }, err => this.isLoadingLocations = false)
    }

    groupBuilding(group) {
        _.forEach(group, (val, key) => {
            val['state'] = key;
            this.buildings.push(val)
        })
    }

    loadCartype() {
        this.carTypes = []
        this.isLoadingCarType = true
        this.carTypes.push(new CarType({ model: 'All' }))
        this.cartypeProvider.getListTypes().subscribe(result => {
            for (let cartype of result) {
                this.carTypes.push(new CarType(cartype))
            }
            if (this.filter.models.length == 0) this.filter.models.push(this.carTypes[0].type)
            this.isLoadingCarType = true
        }, err => {
            this.showToast(err.message)
            this.isLoadingCarType = true
        })
    }

    showToast(message: string) {
        this.gocarToast.showToast({ position: 'bottom', message: message })
    }

    closeFilter(data?) {
        this.viewCtrl.dismiss(data)
    }

    changeCarType(typeName) {
        this.filter.models[0] = typeName
    }

    sortBuildings() {
        this.buildings = _.sortBy(this.buildings, item => {
            return item.city
        })
    }

    moveToTop(building) {
        this.sortBuildings()
        let index = _.findIndex(this.buildings, (b) => {
            return building.city == b.city
        })
        if (index != -1) this.buildings.splice(index, 1)
        this.buildings.unshift(building)
    }

    selectLocation(building) {
        this.filter.locations = []
        this.filter.locations.push(building.city)
        //this.moveToTop(building)
    }

    selectRoundTrip(data) {
        this.filter.trips = []
        this.filter.trips.push(data.trip)
    }

    isLocationSelected(city) {
        return _.find(this.filter.locations, loc => {
            return loc == city
        })
    }

    isRoundTripSelected(trip) {
        return _.find(this.filter.trips, val => {
            return val == trip;
        })
    }

    submit() {
        this.filterProvider.setCarType(this.filter.models)
        this.filterProvider.setCities(this.filter.locations)
        // do filter
        this.closeFilter(true)
    }

    isCarTypeSelected(typeName) {
        return _.find(this.filter.models, type => {
            return type == typeName
        })
    }
}


