import { Component, ViewChild, ElementRef, OnDestroy, Input, Output, EventEmitter, OnChanges, SimpleChanges, OnInit, NgZone } from "@angular/core";
import { Geolocation } from '@ionic-native/geolocation';
import { Platform } from "ionic-angular";
import * as _ from 'lodash';
import { CONSTANTS } from "@common/constants";
import { GlobalService } from "@providers/global.provider";

import {
  GoogleMaps,
  GoogleMap,
  GoogleMapsEvent,
  GoogleMapsAnimation,
  MyLocation,
  ILatLng,
  Circle,
  Marker,
  Spherical,
  BaseArrayClass
} from '@ionic-native/google-maps';

let options = {
  enableHighAccuracy: true,
  timeout: 1000,
  maximumAge: 0
};
@Component({
  selector: 'gocar-map',
  templateUrl: './map.html'
})
export class GoCarMap implements OnDestroy, OnInit, OnChanges {
  @ViewChild('map') mapElement: ElementRef;
  @Input() listBuilding
  @Output() markerClick: EventEmitter<any> = new EventEmitter()
  @Output() mapClick: EventEmitter<any> = new EventEmitter()
  mapReady: boolean = false;
  map: GoogleMap;
  center: ILatLng;
  points: any[] = [];
  markersInit: boolean = false
  ZOOM_LEVEL: number = 15
  constructor(private _platform: Platform,
    private _global: GlobalService,
    private readonly ngZone: NgZone,
    private _geolocation: Geolocation) {
    this.center = _global.defaultLocation
  }

  ngOnInit() {
    this._platform.ready().then(() => {
      this.loadMap()
    })  
  }



  ngOnChanges(changes: SimpleChanges) {
    console.log("ngOnChanges");
    if (changes.listBuilding && changes.listBuilding.currentValue && !this._platform.is('mobileweb')) {
      this.points = []
      _.forEach(changes.listBuilding.currentValue, item => {
        let position = { lat: item.lat, lng: item.lng }
        let icon
        if (item.available) {
          icon = CONSTANTS.MARKER
        } else {
          icon = CONSTANTS.MARKER_GREY
        }
        let iconData = item
        this.points.push({ position, iconData, icon })
      })

      if (this.mapReady) {
        this.setMarker()
      }

    }
  }

  loadMap() {

    this.map = GoogleMaps.create('map_canvas', {
      controls: {
        myLocationButton: true,
        myLocation: true
      },
      camera: {
        target: this.center,
        zoom: 10
      }
    });

    this.map.one(GoogleMapsEvent.MAP_READY).then(() => {
      this.mapReady = true
      if(!this.markersInit) {
        this.setMarker()
      }
      let pos = this._global.getUserLocation()
      this.map.setCameraTarget({ lat: pos.lat, lng: pos.lng })
    })
  }

  setMapClick() {
    this.map.on(GoogleMapsEvent.MAP_CLICK).subscribe(() => {
      this.mapClick.emit()
    })
  }

  setMarker() {
    this.map.clear().then(() => {
      this.markersInit = true
      this.points.forEach((data) => {
        data.disableAutoPan = true;
        this.map.addMarker(data).then((marker) => {
          //https://stackoverflow.com/questions/47352048/ionic3-angular4-two-way-databinding-stops-working-after-click-event-on-google-m
          marker.on(GoogleMapsEvent.MARKER_CLICK).subscribe(() => {
            this.ngZone.run(() => {
              let iconData: any = marker.get('iconData');
              this.map.animateCamera({
                target: marker.getPosition(),
                duration: 300
              }).then(() => {
                if (this.map.getCameraZoom() < this.ZOOM_LEVEL) {
                  this.map.setCameraZoom(this.ZOOM_LEVEL)
                }
              })
              this.markerClick.emit(iconData)
            })
          })
        })
      })
    })
  }

  ngOnDestroy() {
    console.log("onDestroy");
    this.map.remove();
  }

}


