import { Injectable } from "@angular/core";
import { User } from "@models/user";
import { UtilsService } from "@providers/utils.service";
@Injectable()
export class UserRegisterProvider {
    public user: User = new User()
    public images:any[] = []

    constructor(private utilsService: UtilsService) {}

    public setUser(user) {
        this.user.email = user.email || this.user.email
        this.user.firstName = user.firstName || this.user.firstName
        this.user.address = user.address || this.user.address
        this.user.birthday = user.birthday || this.user.birthday
        this.user.lastName = user.lastName || this.user.lastName
        this.user.postCode = user.postCode || this.user.postCode
        this.user.telephone = user.telephone || this.user.telephone
        this.user.password = user.password || this.user.password
        this.user.referenceCode = user.referenceCode || this.user.referenceCode
        this.user.icNumber = user.icNumber || this.user.icNumber
    }

    public setImage(images) {
        for (let img of images) {
           if (img) this.utilsService.addItemInArray(this.images, img, 'type')
        }
    }

    public getImage() {
        return this.images
    }

    public getUser() {
        return this.user
    }

    public clearImages() {
        this.images = []
    }

}


