import { Component, OnInit } from "@angular/core";
import { ViewController, NavParams } from "ionic-angular";



@Component({
    selector: 'fuel-report-pupup',
    templateUrl: 'fuel-report-popup.html'
})
export class FuelReportPopupComponent implements OnInit {
    index: any;
    data = [
        {index: 8, value: false},
        {index: 7, value: false},
        {index: 6, value: false},
        {index: 5, value: false},
        {index: 4, value: false},
        {index: 3, value: false},
        {index: 2, value: false},
        {index: 1, value: false}
    ]
    constructor(public viewCtrl: ViewController,
                public navParams: NavParams) {
    }

    ngOnInit() {
        this.index = this.navParams.get('data')
        // let service = this.navParams.get('service')
        // let fieldValue = this.navParams.get('fieldValue')
        this.data.forEach(element => {
            if (element.index <= this.index) {
                element.value = true;
            } else {
                element.value = false;
            }
        });
    }

    confirm() {
        this.viewCtrl.dismiss(this.index)
    }

    cancel() {
        this.viewCtrl.dismiss('0')
    }

    fillColor(obj) {
        this.index = obj.index;
        this.data.forEach(element => {
            if (element.index <= obj.index) {
                element.value = true;
            } else {
                element.value = false;
            }
        });
    }
}


