import { Component, Input, OnChanges, SimpleChanges } from "@angular/core";
import { GoCarModal } from "../modal/gocar-modal";
import { NavController, Events , AlertController } from "ionic-angular";
import { GocarCalendar } from "../calendar/gocar-calendar";
import { GlobalService } from "@providers/global.provider";
import { AppEvent } from "@common/appEvent";
import { AppMode } from "../../enums/app-mode.enum";
import { ReservationsProvider } from "@providers/reservations/reservations";
// import { NO_COMPLETE_CHILD_SOURCE } from "@firebase/database/dist/src/core/view/CompleteChildSource";
import { CallNumber } from '@ionic-native/call-number';

@Component({
    selector: 'header-car',
    templateUrl: './header-car.html'
})
export class HeaderCar implements OnChanges{
    @Input() mode: any
    @Input() hideTime : any = false;
    bookingTime: any
    isHasReservations: boolean = false
    public appMode = AppMode
    constructor(public navCtrl: NavController,
        private alertCtrl: AlertController,
        private callNumber: CallNumber,
        public global: GlobalService,
        public events: Events,
        public reservation: ReservationsProvider,
        public gocarModal: GoCarModal){
        this.bookingTime = global.bookingTime
        console.log(this.bookingTime)
            this.countReservations()
    }

    countReservations() {
        this.reservation.count().subscribe((res) => {
            if(res.data > 0) this.isHasReservations = true
            else this.isHasReservations = false
            this.global.sethasReservations(this.isHasReservations)
        }, err => {
            this.isHasReservations = false
            this.global.sethasReservations(this.isHasReservations)
        })
    }

    ngOnChanges(changes: SimpleChanges): void {
    }

    changeTime() {
        console.log('Booking time')
        this.gocarModal.showModal(GocarCalendar)
        this.gocarModal.currentModal.onDidDismiss((isChanged) => {
            if(isChanged) {
                this.events.publish(AppEvent.TimeChanged, isChanged)
            }
        });
    }

    gotoReservation() {
        this.events.publish(AppEvent.GoToReservation)
    }

    call(){

        let alertbox = this.alertCtrl
            .create({
                title: 'GoCar Hotline',
                message: "For emergencies, please call: <ul><li style='text-align: left; margin-left: 10px;'> 1-300-88-4622</li><li style='text-align: left!important; margin-left: 10px;'>+60 3 4047 5231</li> </ul>" ,
                buttons: [
                    { text: 'Close' },
                    {
                        text: 'Call Now', handler: () => {
                                this.dial()
                        }
                    }

                ],
                cssClass: 'call' 
            })
        alertbox.present()   
    }

    dial(){
        this.callNumber.callNumber("1300884622", false)
        .then(res => console.log('Launched dialer!', res))
        .catch(err => console.log('Error launching dialer', err));
    }

    
}


