import { Injectable } from "@angular/core";

@Injectable()
export class GoCarStorageService {
    public setData(key, data) {
        localStorage.setItem(key, data)
    }

    public getData(key) {
        return localStorage.getItem(key)
    }

    public cleardata(key) {
        localStorage.removeItem(key)
    }
}


