import { Component } from "@angular/core";
import { ViewController, NavParams } from "ionic-angular";
import moment from 'moment'
import { Resevervation } from "@models/reservation";
import { TimeService } from "@providers/time.service";
import { UtilsService } from "@providers/utils.service";

@Component({
    selector: 'confirm-change-reservation',
    templateUrl: './confirm-change-reservation.html'
})

export class ConfirmChangeReservation {
    startTime: any
    endTime: any
    bookingPaid: any = 0
    newChange: any = 0
    totalPay: any = 0
    data: any
    constructor(private viewCtrl: ViewController,
                private timeService: TimeService,
                private utilsService: UtilsService,
                private navParams: NavParams) {
                this.startTime = navParams.get('startTime')
                this.endTime = navParams.get('endTime')
                this.data = this.navParams.get('data')
                this.calculateRangeTime()
    }

    calculateRangeTime() {
        this.bookingPaid = this.data.previous.finalPrice
        this.newChange = this.data.current.finalPrice
        this.totalPay = this.newChange >  this.bookingPaid ?  this.newChange - this.bookingPaid : 0
    }

      close(data?) {
          this.viewCtrl.dismiss(data)
      }

      confirm() {
          this.close({confirmed: true, price: this.totalPay})
      }
}


