import { Component } from "@angular/core";
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';

@Component({
    selector: 'gocar-file-transfer',
    templateUrl: 'gocar-file-transfer.html'
})

export class GoCarFileTransfer {
    private options: FileUploadOptions = {
        fileKey: 'image',
        fileName: 'ionicfile',
        chunkedMode: false,
        mimeType: "multipart/form-data",
    }
    constructor(private transfer: FileTransfer) {}

    upload(image, url, headers:any = {}) {
        console.log(222, url, headers)
        this.options.headers = headers
        const fileTransfer: FileTransferObject = this.transfer.create()
        return fileTransfer.upload(image, url, this.options)
    }
}


