import { Component } from "@angular/core";
import { NavParams, ViewController, AlertController, Events , NavController } from "ionic-angular";
import moment from 'moment'
import { Resevervation } from "@models/reservation";
import { AuthProvider } from "@providers/auth/auth";
import { ReservationsProvider } from "@providers/reservations/reservations";
import { GoCarToast } from "@shared/components/toast/gocar-toast";
import { AlertBoxComponent } from "@shared/components/alert-box/alert-box";
import { PricingProvider } from "@providers/pricing/pricing.provider";
import { AppEvent } from "@common/appEvent";
import { WalletProvider } from "@providers/wallet/wallet";
import { PayMentType } from "@shared/enums/payment-type.enum";
import { CONSTANTS } from "@common/constants";
import { LoadingComponent } from "@shared/components/loading/loading";
import { ChargeCardPage } from "@pages/wallet/charge-card/charge-card";
import {PaymentMethodsService} from '@providers/paymentMethods/paymentMethods.provider';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { AngularFireDatabase } from 'angularfire2/database';
import { Building } from "@models/building";



export abstract class Pricing {
    [x: string]: any;
    memberFee?: any
    discount?: any
    wallet?: MyWallet
    bookingFee?: any
    finalPrice?: any
    tax?: Tax
}

export abstract class Tax {
    rate?: number
    amount?: number
}

export abstract class MyWallet {
    creditRefund?: number
    creditUsed?: number
    total?: number
}

@Component({
    selector: 'bookcar-payment',
    templateUrl: 'payment.html'
})
export class BookCarPayment {
    public promoCode: any = null;
    public isLoading: boolean = false
    public startDate: any
    public endDate: any
    public time: any
    public alertPromoCode
    public wallet: any
    public total_price: any
    public pricing: Pricing
    public formatTime: string = 'ddd DD MMM HH:mm'
    public useGocarCredit: boolean = false
    public reservation: Resevervation
    public paymentType: any
    public extendFrom: any
    public extendTo: any
    public paymentMethod: any = null
    public mode: any

    // used for hub2hub
    public dropoff: Building

    //for change reservation
    public params: any
    public changeReservationPrice: any

    userData : any ={}
    public getPriceType: {Promo:any, Wallet: any} = {Promo: 1, Wallet: 2}
    constructor(private navParams: NavParams,
        public paymentMethodsService:PaymentMethodsService , 
        public gocarModal: GoCarModal ,
        private authService: AuthProvider,
        private nav : NavController,
        //private chargeCardPage : ChargeCardPage,
        private gocarToast: GoCarToast,
        private alertCtrl: AlertController,
        private alertBox: AlertBoxComponent,
        private gocarLoading: LoadingComponent,
        private walletProvider: WalletProvider,
        private event: Events,
        private reservationService: ReservationsProvider,
        private pricingService: PricingProvider,
        private viewCtrl: ViewController) {
        // used for change reservation
        this.params = navParams.get('params')
        
        //used for hub2hub
        this.dropoff = navParams.get('dropoff')

        this.changeReservationPrice = {}

        this.pricing = {
            tax: {
                amount: 0,
                rate: 0
            },
            discount: 0,
            bookingFee: 0,
            finalPrice: 0,
            memberFee: 0
        }
        this.mode = navParams.get('mode')
        this.paymentType = PayMentType
        this.reservation = this.navParams.get('reservation')

        if (!this.reservation) {
            this.viewCtrl.dismiss()
        }

        if (this.mode == PayMentType.EXTEND_TRIP) {
            this.extendFrom = this.reservation.end_time
            this.extendTo = moment(this.reservation.end_time).add(this.reservation.extend_time, 'h').format(CONSTANTS.DATE_TIME_FORMAT)
            this.time = `${moment(this.extendFrom).format(this.formatTime).toString()} to ${moment(this.extendTo).format(this.formatTime).toString()}`
        } else if(this.mode == PayMentType.BOOK_CAR) {
            this.startDate = this.reservation.start_time
            this.endDate = this.reservation.end_time
            this.time = `${moment(this.startDate).format(this.formatTime).toString()} to ${moment(this.endDate).format(this.formatTime).toString()}`
        } else if(this.mode == PayMentType.CHANGE_RESERVATION) {
            this.startDate = this.reservation.newStartDate
            this.endDate = this.reservation.newEndDate
            this.time = `${moment(this.startDate).format(this.formatTime).toString()} to ${moment(this.endDate).format(this.formatTime).toString()}`
        }

        if(this.mode == PayMentType.CHANGE_RESERVATION) {
            this.getChangeReservationPricing()
        } else {
            this.getPrice()
        }
        this.getWallet()
        this.getUsetData()
    }

    onSelectPaymentMethod(paymentMethodId) {
        console.log(paymentMethodId)
        this.paymentMethod = paymentMethodId
    }

    private getWallet() {
        this.walletProvider.getWalletInfoByUser().subscribe((result) => {
            this.wallet = result.data
        }, err => {
            this.wallet = {credit: 0}
        })
    }

    get isHub2Hub() {
        return this.dropoff != null && this.dropoff != undefined
    }

    private getChangeReservationPricing() {
        this.isLoading = true
        this.pricingService.getChangeReservationPrice(this.params).subscribe(data => {
            this.changeReservationPrice.bookingPaid = data.previous.finalPrice
            this.changeReservationPrice.currentChange = data.current.finalPrice
            let totalPay = this.changeReservationPrice.currentChange - this.changeReservationPrice.bookingPaid
            this.changeReservationPrice.bookingFee = Number(totalPay).toFixed(2)
            this.pricingService.getTax(totalPay).subscribe((tax) => {
                this.changeReservationPrice.tax = tax.data.amount
                this.changeReservationPrice.taxRate = tax.data.rate
                this.changeReservationPrice.totalPay = tax.data.finalPrice
                this.isLoading = false
            })
        }, err => this.isLoading = false)
    }

    private getPrice(type?) {
        let startTime;
        let endTime;
        let duration;
        if(this.mode == PayMentType.CHANGE_RESERVATION) {
            startTime = null
            endTime = null
            let prevDurations = moment(this.reservation.end_time).diff(this.reservation.start_time)
            let lastDurations = moment(this.endDate).diff(this.startDate)
            let diff = prevDurations - lastDurations
            if(diff < 0) diff *= -1
            duration = Math.round(moment.duration(diff).asHours())
        } else if(this.mode == PayMentType.BOOK_CAR) {
            startTime = this.reservation.start_time
            endTime = this.reservation.end_time
        } else {
            startTime = this.extendFrom
            endTime = this.extendTo
        }
        let data = {
            carId: this.reservation.carId,
            userId: this.authService.userId,
            startTime,
            endTime,
            duration,
            isTax: true,
            isHub2Hub: this.isHub2Hub ,
            buildingId: this.reservation.buildingId,
            promoCode: this.promoCode,
            useGocarCredit: this.useGocarCredit
        }
        this.isLoading = true
        this.pricingService.getPrice(data).subscribe((result) => {
            this.isLoading = false
            this.pricing = result.data
            this.reservation.pricing = this.pricing
            if(this.useGocarCredit && Number(this.pricing.wallet.creditUsed) <= 0) {
                let alert = this.alertBox.createInfoAlert('Wallet is Empty', 'Your wallet currently is empty.')
                alert.present()
                this.useGocarCredit = false
            }
            if (this.alertPromoCode) {
                this.alertPromoCode.dismiss()
            }
        }, err => {
            this.isLoading = false
            this.promoCode = null
            if(type == this.getPriceType.Wallet) {
                this.useGocarCredit = false
            }
            this.gocarToast.showToast({ duration: 5000, message: err.message })
        })
    }

    // called in template
    private applyPromoCode() {
        this.alertPromoCode = this.alertCtrl.create({
            title: 'Promo Code',
            message: `Enter your promo code`,
            inputs: [
                {
                    name: 'promocode',
                    placeholder: 'Promo Code'
                }
            ],
            buttons: [
                {
                    text: 'Cancel'
                },
                {
                    text: 'Submit',
                    handler: data => {
                        if (data.promocode && !this.isLoading) {
                            this.promoCode = data.promocode
                            this.getPrice(this.getPriceType.Promo)
                        }
                        return false
                    }
                }
            ]
        })
        this.alertPromoCode.present()
    }

    private get isValidPayment() {
        // if(Number(this.pricing.finalPrice) == 0) return true
        return this.paymentMethod
    }


    // called in template
    private confirmPayment() {
        if (!this.isValidPayment) {
            return this.gocarToast.showToast({ duration: 5000, message: 'Please choose your payment method.' })
        }
        let alertbox = this.alertCtrl
            .create({
                title: 'Confirm Payment',
                message: "Are you sure you want to proceed with this payment?",
                buttons: [
                    { text: 'Cancel' },
                    {
                        text: 'Yes', handler: () => {
                            if (this.mode == PayMentType.BOOK_CAR) {
                                this.doPayment()
                            } else if(this.mode == PayMentType.EXTEND_TRIP) {
                                this.doExtendPayment()
                            } else {
                                this.doChangeReservation()
                            }
                        }
                    }

                ]
            })
        alertbox.present()
    }

    private doExtendPayment() {
        let reservationRequest = this.buildRequestData()
        reservationRequest.type='extend'
        this.gocarLoading.showLoading('Proceeding')

        this.paymentMethodsService.generateRefNo().subscribe(
            (refNo)=>{
              console.log("refNo :" ,refNo);
              this.userData.refNo = refNo.data;
               this.userData.paymentMethod = this.paymentMethod;
               reservationRequest.refNo = refNo.data
               this.holdReservation(reservationRequest).then((data)=>{
                    this.gocarModal.showModalWithoutDismiss(ChargeCardPage , {userData : this.userData});
                    this.gocarModal.currentModal.onDidDismiss((charged) => {
                        console.log("charge successfully ?" , charged);
                        if(charged == true){
                            this.event.publish(AppEvent.ExtendDone, this.extendTo)
                            this.viewCtrl.dismiss();
                        }
                        else{
                            this.event.publish(AppEvent.PaymentFailed) 
                        }    
                    });
               })
            },(err)=>{
              console.log(err)
              this.event.publish(AppEvent.PaymentFailed)
            }
        )   
    }

    private doExtendPayment_old() {
        let reservationRequest = this.buildRequestData()
        this.gocarLoading.showLoading('Proceeding')
        this.reservationService.updateReservation(reservationRequest).subscribe(() => {
            this.event.publish(AppEvent.ExtendDone, this.extendTo)
            this.viewCtrl.dismiss()
            this.gocarLoading.hideLoading()
        }, err => {
            this.gocarLoading.hideLoading()
            this.gocarToast.showToast({ duration: 5000, message: err.message })
        })
    }

    private buildChangeReservation() {
        let startTime = moment(this.startDate).format(CONSTANTS.DATE_TIME_FORMAT).toString()
        let endTime = moment(this.endDate).seconds(0).format(CONSTANTS.DATE_TIME_FORMAT).toString()
        return {
        userId: this.reservation.userId,
        carId: this.reservation.carId,
        buildingId: this.reservation.buildingId,
        oldStartTime: this.reservation.start_time,
        oldEndTime: this.reservation.end_time,
        startTime,
        endTime,
        paymentMethod: this.paymentMethod,
        reservationId: this.reservation.id,
        useGocarCredit: this.useGocarCredit,
        type : null, 
        refNo: null    
        }
    }

    private doChangeReservation() {
        let reservationRequest = this.buildChangeReservation()
        reservationRequest.type='change'

        this.gocarLoading.showLoading()
        this.paymentMethodsService.generateRefNo().subscribe(
            (refNo)=>{
              console.log("refNo :" ,refNo);
              this.userData.refNo = refNo.data;
               this.userData.paymentMethod = this.paymentMethod;
               reservationRequest.refNo = refNo.data
               this.holdReservation(reservationRequest).then((data)=>{
                    this.gocarModal.showModalWithoutDismiss(ChargeCardPage , {userData : this.userData});
                    this.gocarModal.currentModal.onDidDismiss((charged) => {
                        console.log("charge successfully ?" , charged);
                        if(charged == true){
                            this.event.publish(AppEvent.ExtendDone, this.extendTo)
                            this.viewCtrl.dismiss();
                        }
                        else{
                            this.event.publish(AppEvent.PaymentFailed) 
                        }    
                    });
               })
            },(err)=>{
              console.log(err)
              this.event.publish(AppEvent.PaymentFailed)
            }
        )   
    }

    private doChangeReservation_old() {
        let reservationRequest = this.buildChangeReservation()
        this.gocarLoading.showLoading()
        this.reservationService.changeReservation(reservationRequest).subscribe(() => {
            this.gocarLoading.hideLoading()
            this.viewCtrl.dismiss(true)
        }, err => {
            this.gocarLoading.hideLoading()
            this.gocarToast.showToast({ duration: 5000, message: err.message })
        })
    }

    private buildRequestData() {
        return {
            reservationId: this.reservation.id,
            userId: this.reservation.userId,
            startTime: this.mode == PayMentType.BOOK_CAR ? this.reservation.start_time : this.extendFrom,
            endTime: this.mode == PayMentType.BOOK_CAR ? this.reservation.end_time : this.extendTo,
            carId: this.reservation.carId,
            buildingId: this.reservation.buildingId,
            promoCode: this.promoCode,
            paymentMethod: this.paymentMethod,
            isHub2Hub: this.isHub2Hub,
            hub2hubId: this.dropoff ? this.dropoff.id : null,
            useGocarCredit: this.useGocarCredit,
            refNo : null,
            type : null
        }
    }

    private doPayment() {
        let reservationRequest = this.buildRequestData()
        reservationRequest.type='new'
        this.gocarLoading.showLoading('Proceeding')
        this.paymentMethodsService.generateRefNo().subscribe(
            (refNo)=>{
              console.log("refNo :" ,refNo);
              this.userData.refNo = refNo.data;
              this.userData.paymentMethod = this.paymentMethod;
              
               reservationRequest.refNo = refNo.data
               this.holdReservation(reservationRequest).then((data)=>{
                    this.gocarModal.showModalWithoutDismiss(ChargeCardPage , {userData : this.userData});
                    this.gocarModal.currentModal.onDidDismiss((charged) => {
                        console.log("charge successfully ?" , charged);
                        if(charged == true)
                        {
                            this.event.publish(AppEvent.PaymentDone)
                            this.viewCtrl.dismiss();
                        }
                        else{
                            this.event.publish(AppEvent.PaymentFailed) 
                        }    
                    });
               })
            },(err)=>{
              console.log(err)
              this.event.publish(AppEvent.PaymentFailed)
            }
        )        
    }

    private doPayment_old() {
        let reservationRequest = this.buildRequestData()
        this.gocarLoading.showLoading('Proceeding')
        this.reservationService.saveReservation(reservationRequest).subscribe(() => {
            this.event.publish(AppEvent.PaymentDone)
            this.gocarLoading.hideLoading()
            this.viewCtrl.dismiss()
        }, err => {
            this.gocarLoading.hideLoading()
            this.gocarToast.showToast({ duration: 5000, message: err.message })
        })
    }

    back() {
        this.viewCtrl.dismiss()
    }

    getUsetData(){
        this.authService.getUserDatasByToken().subscribe((result) => {
            this.userData = result.data;
        });
    }

    holdReservation(reservationRequest){
        return new Promise((resolve, reject) => {
            this.reservationService.holdReservation(reservationRequest).subscribe((data)=>{
                console.log(data);
                resolve(data);
                
            }, err=>{
                console.log(err);
                this.gocarLoading.hideLoading();
                this.gocarToast.showToast({ duration: 5000, message: err.message })
            })
        })
    }
}


