import { Component, EventEmitter } from "@angular/core";
import { PopoverController } from "ionic-angular";

@Component({
    selector: 'gocar-popover',
    templateUrl: 'gocar-popover.html'
})
export class GoCarPopover {
    public dismissEvent: EventEmitter<any> = new EventEmitter()
    constructor(public popoverCtrl: PopoverController) {
    }

    public show(component, data? , opt?): Promise<any> {
       return new Promise((resolve, reject) => {
           
            let popover = this.popoverCtrl.create(component, data, opt)
            popover.present()
            popover.onDidDismiss(data => {
                resolve(data)
            })
       })
    }
}


