import { Component, Input, ViewChild, ElementRef, OnChanges, SimpleChanges } from "@angular/core";
declare var google;
@Component({
    selector: 'gocar-js-map',
    templateUrl: './js-map.html'
})

export class GoCarJSMap implements OnChanges{
    @ViewChild('jsmap') mapElement: ElementRef;
    @Input() position: any
    
    constructor( ) {
        
    }

    ngOnChanges(changes: SimpleChanges) {
        if(changes.position && changes.position.currentValue) {
            console.log(this.position)
            this.buildMap()
        }
    }

    ionViewDidLoad() {
        // this.buildMap()
    }

    buildMap() {
        let  map = new google.maps.Map(this.mapElement.nativeElement, {
          zoom: 15,
          center: this.position,
          disableDefaultUI: true
        })
        var image = {
            url: './assets/imgs/G-icon.png',
            scaledSize: new google.maps.Size(32, 45),
           }

        let marker = new google.maps.Marker({
            position: this.position,
            map,
            icon: image,

          });
          marker.setMap(map)
      }
}


