import { Component, OnInit } from "@angular/core";
import { Events, ViewController, PopoverController } from "ionic-angular";
import { AppEvent } from "@common/appEvent";
import { User } from "@models/user";
import { SelectImage } from "@shared/components/select-image/select-image";
import { GoCarPopover } from "@shared/components/popover/gocar-popover";
import { AuthProvider } from "@providers/auth/auth";
import { GoCarToast } from "@shared/components/toast/gocar-toast";
import { AppConfig } from "@app/app.config";
import { UploadImage } from "@providers/upload/upload";
import { ImageType } from "@shared/enums/image-type.enum";
import { SmsService } from "@providers/sms/sms.provider";
import { SMSVERIFYSTATUS } from "@shared/enums/sms-verify-status.enum";
import { AlertBoxComponent } from "@shared/components/alert-box/alert-box";
import { UserRegisterProvider } from "../user.provider";
import { UtilsService } from "@providers/utils.service";
import { CONSTANTS } from "@common/constants";
import { CheckPinCode } from "@shared/components/check-pincode/check-pincode";
import { InAppBrowser } from "@ionic-native/in-app-browser";
import { GoCarStorageService } from "@providers/storage/localstorage.service";
import { LoadingComponent } from "@shared/components/loading/loading";
@Component({
    selector: 'user-identify',
    templateUrl: './user-identify.html',
    providers: []
})

export class UserIdentify implements OnInit {
    public user: User
    public selfie: any
    public images: any[]
    public licence: any
    public licenceRaw: any
    public passport: any
    public passportRaw: any
    public alertConfirmTel: any
    public uploadUrl: any = this.appConfig.apiUrl + '/user/upload'
    constructor(public viewCtrl: ViewController,
        private popoverCtrl: PopoverController,
        private popover: GoCarPopover,
        private gocarToast: GoCarToast,
        private gocarLoading: LoadingComponent,
        private alertBox: AlertBoxComponent,
        private uploadImage: UploadImage,
        private authService: AuthProvider,
        private userProvider: UserRegisterProvider,
        private smsService: SmsService,
        private utilsService: UtilsService,
        private appConfig: AppConfig,
        private events: Events,
        private iab: InAppBrowser,
        private gocarStorage: GoCarStorageService) {
        this.user = this.userProvider.getUser()
        this.retrieveImages()
    }

    private retrieveImages() {
        this.images = this.userProvider.getImage()
        this.images.forEach((img) => {
            if (img.type == ImageType.LICENCE) {
                this.utilsService.thumbnailify(img.image, CONSTANTS.THUMBNAIL_SIZE, (thb) => {
                    this.licence = thb
                })
            }
            if (img.type == ImageType.PASSPORT) {
                this.utilsService.thumbnailify(img.image, CONSTANTS.THUMBNAIL_SIZE, (thb) => {
                    this.passport = thb
                })
            }
            if (img.type == ImageType.SELFIE) {
                this.utilsService.thumbnailify(img.image, CONSTANTS.THUMBNAIL_SIZE, (thb) => {
                    this.selfie = thb
                })
            }
        })
    }

    ngOnInit() {

    }

    private get isvalid() {
        return this.images.length == 3
    }

    private submit() {
        if (!this.isvalid) {
            let alert = this.alertBox.createInfoAlert('Incomplete Info', null, 'Please attach your selfie as required and photos of your driving license and passport (if applicable)')
            alert.present()
            return;
        }
        this.generateOTP()
    }

    private builData() {
        return {
            address: this.user.address,
            birthday: this.user.birthday,
            email: this.user.email,
            firstName: this.user.firstName,
            lastName: this.user.lastName,
            password: this.user.password,
            telephone: this.user.telephone,
            referenceCode: this.user.referenceCode,
            role: this.user.role,
            postCode: this.user.postCode,
            ic_number: this.user.icNumber,
            license: '',
            licenseDir: '',
            passport: '',
            passportDir: '',
            selfie: '',
            selfieDir: '',
        }
    }

    private checkValidIdentify() { }

    private submitUser() {
        let user = this.builData()
        this.gocarLoading.showLoading('Registering')
        this.authService.registerUser(user)
            .subscribe(res => {
                this.gocarStorage.setData(CONSTANTS.GOCAR_TOKEN, res.data.token);
                let userId = res.data.id
                this.uploadLicence(userId)
            }, err => {
                this.showToast(err.message)
                this.gocarLoading.hideLoading()
            })
    }

    private uploadLicence(userId) {
        this.uploadImage.doUpload(this.images, this.uploadUrl, { userId: userId }).then((data) => {
            this.gocarLoading.hideLoading()
            this.events.publish(AppEvent.RegisterEnd, this.user)
            this.events.publish(AppEvent.UserDismiss)
            this.images = []
            this.userProvider.clearImages()
            this.licence = null;
            this.passport = null;
            this.user = new User()
        }).catch(err => {
            this.gocarLoading.hideLoading()
            this.events.publish(AppEvent.RegisterEnd, this.user)
            this.events.publish(AppEvent.UserDismiss)
        })
    }

    private showToast(message) {
        this.gocarToast.showToast({ message: message, position: 'bottom', duration: 2000 })
    }

    private attachSelfie() {
        this.popover.show(SelectImage).then(data => {
            if (data) {
                this.utilsService.thumbnailify(data, 64, (thumbnail) => {
                    this.selfie = thumbnail
                })
                let item = { image: data, type: ImageType.SELFIE }
                console.log('attach selfie before', this.images)
                this.utilsService.addItemInArray(this.images, item, 'type')
                console.log('attach selfie after', this.images)
            }
        })
    }

    private generateOTP() {
        this.gocarLoading.showLoading('Loading...')
        this.smsService.generateOPT(this.user.telephone).subscribe(ret => {
            this.gocarLoading.hideLoading()
            if (ret.data == SMSVERIFYSTATUS.SENT) {
                this.showInputPincode()
            } else {
                this.showToast("Invalid Phone")
            }//ionic cordova run android 
        }, err => {
            console.log(err)
            this.gocarLoading.hideLoading()
            this.showToast(err.message)
        })
    }

    resend() {
        console.log('resending')
    }

    private showInputPincode() {
        let confirm = this.popoverCtrl.create(CheckPinCode, { phone: this.user.telephone }, { cssClass: 'popover-confirm-pincode' })
        confirm.present()
        confirm.onDidDismiss((isValid) => {
            if (isValid) {
                this.submitUser()
            }
        })
    }

    private attachLicence() {
        this.popover.show(SelectImage).then(data => {
            if (data) {
                 this.utilsService.thumbnailify(data, CONSTANTS.THUMBNAIL_SIZE, (thumbnail) => {
                    this.licence = thumbnail
                })
                let item = { image: data, type: ImageType.LICENCE }
                console.log('attach license before', this.images)
                this.utilsService.addItemInArray(this.images, item, 'type')
                console.log('attach license after', this.images)
            }
        })
    }

    private attachPassport() {
        this.popover.show(SelectImage).then(data => {
            if (data) {
                this.utilsService.thumbnailify(data, CONSTANTS.THUMBNAIL_SIZE, (thumbnail) => {
                    this.passport = thumbnail
                })
                console.log('attach passport before', this.images)
                let item = { image: data, type: ImageType.PASSPORT }
                this.utilsService.addItemInArray(this.images, item, 'type')
                console.log('attach passport after', this.images)
            }
        })
    }

    ionViewWillLeave() {
        // this.userProvider.setImage(this.images)
    }

    terms() {
        let browser = this.iab.create('https://gocarhelp.zendesk.com/hc/en-us/articles/212885207-Member-Terms-Conditions', '_blank');
    }
}


