import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { CarTypesProvider } from '@providers/carTypes/cartypes.provider';
import { CarType } from "@models/cartype";
import * as _ from 'lodash'
import { FiltersProvider } from "@providers/fiters/filters";
import { CarTypeImageEnum } from "../../enums/car-type-image.enum";
/**
 * Generated class for the SelectCarTypeComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'select-car-type',
  templateUrl: 'select-car-type.html'
})
export class SelectCarTypeComponent implements OnInit {
  @Output() selectEvent: EventEmitter<any> = new EventEmitter()
  text: string;
  carTypes = [];
  isLoadingCarType = false;
  private filter: { models: any[], distance: any, locations: any[], hideAvailbleCar: any }
  public imgSrc: string = './assets/imgs/car_type/'
  public cartypeOption: any

  constructor(
    private cartypeProvider: CarTypesProvider,
    private filterProvider: FiltersProvider,

  ) {
    this.filter = {
      models: Object.assign([], filterProvider.getCarType()),
      distance: 5,
      hideAvailbleCar: false,
      locations: Object.assign([], filterProvider.getCities())
    }



  }

  selectOptions: any = {
    theme: 'ios',
    select: 'multiple'
  }

  ngOnInit() {
    this.loadCartype();
  }

  loadCartype() {
    this.carTypes = []
    this.isLoadingCarType = true
    this.cartypeProvider.getListTypes().subscribe(result => {
      for (let cartype of result) {
        this.carTypes.push(new CarType(cartype))
      }
      this.isLoadingCarType = false
    }, err => {
      this.isLoadingCarType = false
    })
  }

  changeCarType(typeName) {
    this.filter.models[0] = typeName
    console.log(this.filter.models);


    this.filterProvider.setCarType(this.filter.models);
    this.selectEvent.emit();

  }

  isCarTypeSelected(typeName) {
    let index = _.findIndex(this.filter.models, type => {
      return type == typeName
    })


    if (index != -1)
      return true;
    else
      return false;

  }


  image(model, iconDir) {
    //console.log(model);
    switch (model) {
      // case "Almera":
      //   return iconDir
      // case 'Captur':
      //   return iconDir
      // case 'Captur Promo':
      //   return iconDir
      // case 'X-Gear':
      //   return iconDir
      // case 'Bezza':
      //   return iconDir
      // case 'Grand Livina':
      //   return iconDir
      // case 'Koleos':
      //   return iconDir
      // case 'Navara':
      //   return iconDir
      case 'All':
        return `${this.imgSrc}${CarTypeImageEnum.All}`
      default:
        return iconDir
    }
    // switch (type) {
    //     case CarTypeEnum.MPV:
    //         return `${this.imgSrc}${CarTypeImageEnum.MPV}`
    //     case CarTypeEnum.SEDAN:
    //         return `${this.imgSrc}${CarTypeImageEnum.SEDAN}`
    //     case CarTypeEnum.SUV:
    //         return `${this.imgSrc}${CarTypeImageEnum.SUV}`
    //     default:
    //         return `${this.imgSrc}${CarTypeImageEnum.SUV}`
    // }
  }

}



