import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { ReservationsProvider } from '@providers/reservations/reservations';
import { LoadingComponent } from '@shared/components/loading/loading';
import { AuthProvider } from '@providers/auth/auth';
import { UserPage } from '../../user/user';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { GoCarStorageService } from '@providers/storage/localstorage.service';
import { CONSTANTS } from '@common/constants';

/**
 * Generated class for the ReservationReturnedPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-reservation-returned',
  templateUrl: 'reservation-returned.html',
})
export class ReservationReturnedPage {
  listReservation = [];
  constructor(public navCtrl: NavController, 
    public navParams: NavParams, 
    private reservationsProvider: ReservationsProvider, 
    private loading: LoadingComponent,
    private gocarStorage: GoCarStorageService,
    private authProvider: AuthProvider, 
    private gocarModal: GoCarModal) {
  }

  ionViewDidEnter() {
    if (this.authProvider.isAuthenticated) {
      this.loading.showLoading();
      this.reservationsProvider.getListReservationsReturn(this.authProvider.userId).subscribe((result) => {
        this.loading.hideLoading();
        this.listReservation = result.data;
        this.gocarStorage.setData(CONSTANTS.RESERVATION_RETURN_KEY, JSON.stringify(this.listReservation))
      }, err => {
        this.listReservation = JSON.parse(this.gocarStorage.getData(CONSTANTS.RESERVATION_RETURN_KEY || '[]'))
        this.loading.hideLoading()
      })
    } else {
      this.gocarModal.showModal(UserPage)
    }
  }

}



