import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { WalletPage } from './wallet/wallet';
import { PaymentHistoryPage } from './payment-history/payment-history';
import { CreditHistoryPage } from './credit-history/credit-history';
import { AddCardPage } from './add-card/add-card';
import { ChargeCardPage } from './charge-card/charge-card';
import { GocarPipeModule } from '../../pipe/gocar-pipe.module';
import { ComponentsModule } from '@shared/components/components.module';
import { ScannerPage } from './scanner/scanner';

@NgModule({
  declarations: [
    WalletPage,
    PaymentHistoryPage,
    CreditHistoryPage,
    AddCardPage,
    ChargeCardPage,
    ScannerPage,
  ],
  imports: [
      ComponentsModule,
    IonicPageModule.forChild(WalletPage),
    GocarPipeModule,
  ],
  entryComponents: [
      WalletPage,
      PaymentHistoryPage,
      CreditHistoryPage,
      AddCardPage,
      ChargeCardPage,
      ScannerPage
  ]
})
export class WalletPageModule {}



