import { BluetoothLe } from "ionic-native-bluetoothle"
import { Subscription } from "rxjs/Subscription"
import { EventEmitter, Output } from '@angular/core'
import { Buffer } from 'buffer'
import { Observable } from "rxjs/Observable"
import { map } from 'rxjs/operators'

export enum DeviceState {
    NONE = 'none',
    CONNECTING = 'connecting',
    CONNECTED = 'connected',
    DISCOVERED = 'discovered',
    DISCONNECTED = 'disconnected',
    CLOSED = 'closed'
}

export enum OSType {
    IOS = 'ios',
    ANDROID = 'android',
    OTHER = 'other'
}

const CONNECTION_TIMEOUT = 15 * 1000
const CONNECTION_MAX_RETRY = 3

export abstract class GocarDevice {
    address?: string
    characteristic?: string
    service?: string
}

export class BleDevice {

    private connectionSubscription: Subscription
    private state: DeviceState
    private reconnectCount: number = 0
    private msg:string = ''
    public name: string

    @Output() stateChangeEvent: EventEmitter<{ oldState: DeviceState, newState: DeviceState }> = new EventEmitter();

    constructor(private ble: BluetoothLe,
        private os: string,
        private device: GocarDevice) {
        this.state = DeviceState.NONE
    }

    private setState(newState: DeviceState) {
        console.log(`State: ${this.state} -> ${newState}`)
        this.stateChangeEvent.emit({
            oldState: this.state,
            newState: newState
        })
        this.state = newState
        if (newState === DeviceState.CONNECTED) {
            this.reconnectCount = 0
        }
    }

    public get currentState() {
        return this.state
    }

    connect() {
        console.log('start connect')
        this.setState(DeviceState.CONNECTING)
        let cancelConnectionTask = setTimeout(() => {
            this.connectionSubscription.unsubscribe()
            this.close()
        }, CONNECTION_TIMEOUT)
        this.connectionSubscription = this.ble.connect({ address: this.device.address, autoConnect: true }).subscribe((result) => {
            console.log('connection sub: ' + JSON.stringify(result))
            clearTimeout(cancelConnectionTask)
            if (result.status === 'disconnected') {
                this.setState(DeviceState.DISCONNECTED)
            } else if (result.status === 'connected') {
                this.setState(DeviceState.CONNECTED)
                this.name = result.name
                // if(this.os == OSType.ANDROID) {
                //     this.discover()
                // } else {
                //     this.iosDiscover()
                // }
                this.discover()
            } else {
                console.log('illegal state')
            }
        }, (error) => {
            console.log('connect error: ' + JSON.stringify(error))
            this.close()
        })
    }

    discover() {
        console.log('start discover', this.device.address)
        this.ble.discover({ address: this.device.address }).then((result) => {
            this.setState(DeviceState.DISCOVERED)
            console.log('end discover success', result)
            this.subscribeHr().subscribe((result) => {
                console.log('subcribed', result)
            })
        }).catch((err) => {
            console.log('end discover error', err)
            this.disconnect()
        })
    }

    subcribe() {
        this.ble.subscribe({
            address: this.device.address,
            service: this.device.service,
            characteristic: this.device.characteristic
        }).subscribe((data) => {
            console.log('Subcribe event', data)
        }, err => {
            console.log('Subcribe event error', err)
        })
    }

    iosDiscover() {
        let services = this.ble.services({address: this.device.address, services: []})
        let characteristic = this.ble.characteristics({address: this.device.address, services: this.device.service, characteristics: []})
        let descriptors = this.ble.descriptors({address: this.device.address,service: this.device.service, characteristic: this.device.characteristic})
        let promisses : any[] = [services, characteristic, descriptors]
        Promise.all(promisses).then(result => {
            this.setState(DeviceState.DISCOVERED)
            console.log('end ios discover success', result)
        }).catch((err) => {
            console.log('end ios discover error', err)
            this.disconnect()
        })
    }

    services() {
        console.log('start services')
        this.ble.services({address: this.device.address, services: []}).then((result) => {
            // this.setState(DeviceState.DISCOVERED)
            console.log('end services success', result)
        }).catch((err) => {
            console.log('end services error', err)
            this.disconnect()
        })
    }

    characteristic() {
        console.log('start characteristic')
        this.ble.characteristics({address: this.device.address, services: this.device.service, characteristics: []}).then((result) => {
            this.setState(DeviceState.DISCOVERED)
            console.log('end characteristic success', result)
        }).catch((err) => {
            console.log('end characteristic error', err)
            this.disconnect()
        })
    }

    disconnect() {
        return this.ble.disconnect({ address: this.device.address }).then((result) => {
            console.log('disconnect.then: ' + JSON.stringify(result))
            this.setState(DeviceState.DISCONNECTED)
            return this.close()
        }).catch(() => {
            return this.close()
        })
    }

    reconnect() {
        this.ble.reconnect({ address: this.device.address }).then(result => {
            console.log('reconnect.then: ' + JSON.stringify(result))
            if (result.status === 'connected') {
                this.setState(DeviceState.DISCOVERED)
            } else if (result.status === 'disconnected') {
                this.reconnectCount++
            } 
        }).catch((err) => {
            console.log('reconnect.catch', err)
            this.close()
        })
    }

    isconnected() {
        return this.ble.isConnected({address: this.device.address}).then((result) => {
            console.log('isconnected.then', result)
            return result
        })
    }

    close() {
        return this.ble.close({ address: this.device.address }).then(() => {
            console.log('close.then')
            this.setState(DeviceState.CLOSED)
            return true
        })
    }

    bond() {
        return this.ble.bond({ address: this.device.address })
    }

    isBonded() {
        return this.ble.isBonded({ address: this.device.address })
    }

    getSerialNumber() {
        return this.ble.read({
            address: this.device.address,
            service: '1800',
            characteristic: '2a00'
        }).then((result) => {
            console.log(JSON.stringify(result))
            let array = this.ble.encodedStringToBytes(result.value)
            // console.log(Buffer.from(array).toString())
        })
    }

    readBatteryLevel() {
        console.log('batteryLevel')
        this.ble.read({
            address: this.device.address,
            service: '180f',
            characteristic: '2a19'
        }).then((result) => {
            console.log(JSON.stringify(result))
            let array = this.ble.encodedStringToBytes(result.value)
            console.log('battery: ' + array[0])
        })
    }

    subscribeHr() {
        let hrCharPath = {
            address: this.device.address,
            service: this.device.service,
            characteristic: this.device.characteristic
        }
        return this.ble.subscribe(hrCharPath).pipe(map(result => {
            console.log('subscribeHr', result)
            if (result.status === 'subscribedResult') {
                let obj
                let bytes = this.ble.encodedStringToBytes(result.value)
                let string = this.ble.bytesToEncodedString(bytes)
                
               console.log(string);
            //    obj.status == "msg";

            //    if (this.msg[this.msg.length - 2] === '\r' && this.msg[this.msg.length - 1] === '\n') {
            //        console.log(this.msg);
            //        if(this.msg.trim() =='Please input password!')
            //        {
            //            console.log("input password.....");
            //        }
            //        this.msg = '';
            //    }
                return {
                    status: 'subscribedResult',
                    value: string
                }
            } else {
                return result
            }
        }))
    }

    unlockDevice() {
        this.writeLogin().then(() => {
            return this.writePassword();
          }).then(() => {
            return this.writeUnlock();
          }).then(() => {
            return this.writeLogout();
          }).then(() => {
            console.log('Unlock successfully')
          }).catch(err => {
            console.log(err)
          });
    }

    lockDevice() {
        this.writeLogin().then(() => {
            return this.writePassword();
          }).then(() => {
            return this.writeLock();
          }).then(() => {
            return this.writeLogout();
          }).then(() => {
            console.log('Unlock successfully')
          }).catch(err => {
            console.log(err)
          });
    }

    async writeLogin() {
        try {
            let encodedString = this.ble.bytesToEncodedString(new Uint8Array([0x0E, 0x0E, 0x0E]));
            let params = this.getParams(encodedString)
            let writeRes = await this.ble.write({
                "type": "noResponse",
                "address": this.device.address,
                "service": this.device.service,
                "characteristic": this.device.characteristic,
                "value": encodedString,
            })
            console.log("writeLogin : ", writeRes);
            return writeRes
        } catch (error) {
            throw error
        }
    }

    async writeLogout() {
        try {
            let encodedString = this.ble.bytesToEncodedString(new Uint8Array([0x1b, 0x1b, 0x1b]));
            let params = this.getParams(encodedString)
            let writeRes = await this.ble.write({
                "type": "noResponse",
                "address": this.device.address,
                "service": this.device.service,
                "characteristic": this.device.characteristic,
                "value": encodedString,
            });
            console.log("writeLogout :", writeRes);
            return writeRes
        } catch (error) {
            throw error
        }
    }

    async writePassword() {
        try {
            let bytes = this.stringToBytes('677340\r\n');
            console.log("bytes : ", bytes);
            var encodedString = this.ble.bytesToEncodedString(bytes);
            console.log("encodedString : ", encodedString);

            let params = this.getParams(encodedString)
            let writeRes = await this.ble.write({
                "type": "noResponse",
                "address": this.device.address,
                "service": this.device.service,
                "characteristic": this.device.characteristic,
                "value": encodedString,
            });
            console.log("writePassword :", writeRes);
            return writeRes
        } catch (error) {
            throw error
        }
    }

    async writeLock() {
        try {
            let bytes = this.stringToBytes("LCK;1\r\n");
            var encodedString = this.ble.bytesToEncodedString(bytes);
            console.log(encodedString);

            let params = this.getParams(encodedString)
            let writeRes = await this.ble.write({
                "type": "noResponse",
                "address": this.device.address,
                "service": this.device.service,
                "characteristic": this.device.characteristic,
                "value": encodedString,
            })
            console.log("writeLock :", writeRes);
            return writeRes
        } catch (error) {
            throw error
        }
    }


    async writeUnlock() {
        try {
            let bytes = this.stringToBytes("LCK;0\r\n");
            var encodedString = this.ble.bytesToEncodedString(bytes)
            console.log(encodedString);

            let params = this.getParams(encodedString)
            let writeRes = await this.ble.write({
                "type": "noResponse",
                "address": this.device.address,
                "service": this.device.service,
                "characteristic": this.device.characteristic,
                "value": encodedString,
            })
            console.log("writeLock :", writeRes);
        } catch (error) {
            throw error
        }
    }

    stringToBytes(str) {
        var bytes = new ArrayBuffer(str.length * 2);
        var bytesUint16 = new Uint16Array(bytes);
        for (var i = 0; i < str.length; i++) {
            bytesUint16[i] = str.charCodeAt(i);
        }
        return new Uint8Array(bytesUint16);
    }

    bin2String(array) {
        var result = "";
        for (var i = 0; i < array.length; i++) {
          result += String.fromCharCode(parseInt(array[i], 2));
        }
        return result;
      }

    getParams(encodedString) {
        return {
            "type": "noResponse",
            "address": this.device.address,
            "service": this.device.service,
            "characteristic": this.device.characteristic,
            "value": encodedString,
        }
    }
}


