import { Injectable } from "@angular/core";
import { CONSTANTS } from "@common/constants";
import * as moment from "moment"
import { NetWorkStatus } from "@shared/enums/network-status.enum";
import { BleDevice } from "@providers/bluetooth/ble-device.service";

@Injectable()
export class GlobalService {
    public defaultCity: string = 'Kuala Lumpur'
    public userLocation: { lat: any, lng: any }
    public reservation;
    public bluetoothDevice: BleDevice
    public bluetoothDeviceExpired: BleDevice
    public netWorkStatus: Number = NetWorkStatus.OFFLINE
    public global: {isHasReservations: boolean} = {isHasReservations: false}
    public defaultLocation: { lat: any, lng: any } = { lat: CONSTANTS.DEFAULT_LAT, lng: CONSTANTS.DEFAULT_LNG }
    public bookingTime: { start: any, end: any }
        = { start: moment().minute(0).seconds(0), end: moment().add(1, 'h').minute(0).seconds(0) }
    constructor() {

    }

    setUserLocation(lat, lng) {
        this.userLocation = { lat, lng }
    }
    setReservation(reservation) {
        this.reservation = reservation;
    }
    getReservation() {
        return this.reservation
    }

    gethasReservations() {
        return this.global
    }

    sethasReservations(isHas: boolean) {
        this.global.isHasReservations = isHas
    }

    resetReservation() {
        this.reservation = [];
    }

    getUserLocation() {
        if (this.userLocation) {
            return this.userLocation
        }
        return this.defaultLocation
    }

    setBLE(ble) {
        this.bluetoothDevice = ble
    }

    getBLE() {
        return this.bluetoothDevice
    }

    setBLEExpired(ble) {
        this.bluetoothDeviceExpired = ble
    }

    getBLEExpired() {
        return this.bluetoothDeviceExpired
    }
}


