import { Component, Output, EventEmitter, Input, OnChanges, SimpleChanges } from "@angular/core";
import {AppMode} from '@shared/enums/app-mode.enum'
import { FiltersProvider } from '@providers/fiters/filters';

@Component({
    selector: 'booking-time',
    templateUrl: './booking-time.html'
})
export class BookingTime implements OnChanges {
    @Input() timeFrom: any
    @Input() timeTo: any
    @Input() mode: any
    @Output() timeEvent: EventEmitter<any> = new EventEmitter()
    public appMode = AppMode

    ngOnChanges(changes: SimpleChanges): void {
        console.log("onChange");
    }
    constructor(        
        private filtersProvider: FiltersProvider
    ){}

    fireEvent(event) {
        this.timeEvent.emit()
    }

    getStartDate(){
        return this.filtersProvider.getStartDate();
    }

    getEndDate(){
        return this.filtersProvider.getEndDate();
    }

    getStartTime(){
        return this.filtersProvider.getStartTime();
    }

    getEndTime(){
        return this.filtersProvider.getEndTime();
    }
}


