import { Component, OnDestroy } from '@angular/core';
import {  NavController, NavParams, ViewController, Events } from 'ionic-angular';
import { ReservationsProvider } from '@providers/reservations/reservations';
import { LoadingComponent } from '@shared/components/loading/loading';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import * as _ from 'lodash'
import moment from 'moment'
import { CONSTANTS } from "@common/constants";
import { BookCarPayment } from '../../book-car/payment/payment';
import { PayMentType } from '@shared/enums/payment-type.enum';
import { AppEvent } from '@common/appEvent';
import { GoCarToast } from '@shared/components/toast/gocar-toast';
/**
 * Generated class for the ReservationDrivingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-reservation-extend',
  templateUrl: 'reservation-extend.html',
})
export class ReservationExtendPage implements OnDestroy {
  timeExtends = [];
  timeSelect = [];
  data;
  endTime;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public events: Events,
    public gocarToast: GoCarToast,
    public viewCtrl: ViewController,
    private reservationsProvider: ReservationsProvider, 
    private loading: LoadingComponent, 
    public goCarModal: GoCarModal) {
    this.data = this.navParams.get('data');
    this.events.subscribe(AppEvent.ExtendDone, (data) => {
      this.gocarToast.showToast({ message: 'You was successfully extend your trip' })
      this.back(data)
    })
  }

  ionViewDidEnter() {
    this.loading.showLoading();
    this.reservationsProvider.getListExtendTime(this.data.id).subscribe((result) => {
      if (result) {
        this.timeExtends = result.data;
        this.endTime = 'You can extend your reservation until' + '  ' + moment.utc(this.data.end_time).add(this.timeExtends.length, 'h').format(CONSTANTS.BOOKING_TIME_FORMAT)
      }
      this.loading.hideLoading();
    }, err => this.loading.hideLoading())
  }

  selectTime(time) {
    this.timeSelect = [];
    let index = _.findIndex(this.timeSelect, item => {
      return item == time.value;
    })
    if (index != -1) {
      this.timeSelect.splice(index, 1)
    } else {
      this.timeSelect.push(time)
    }

  }

  ischeck(time) {
    return _.find(this.timeSelect, val => {
      return val.value == time.value;
    })
  }

  back(data?) {
    this.viewCtrl.dismiss(data)
  }


  next() {
    // this.loading.showLoading();
    if (this.timeSelect[0]) {
      let body = {
        id: this.data.id,
        extend_time: this.timeSelect[0].value,
        total_price: this.timeSelect[0].total_price
      }
      this.data.extend_time = this.timeSelect[0].value,
        // this.data.end_time = moment.utc(this.data.end_time).add(this.data.extend_time, 'h').format(CONSTANTS.DATE_TIME_FORMAT)
        this.data.pricing = { total_price: this.timeSelect[0].total_price },
        // this.reservationsProvider.updateReservation(body).subscribe((result) => {
        //   this.loading.hideLoading();
        // }, err => this.loading.hideLoading())
        this.goCarModal.showModalWithoutDismiss(BookCarPayment, { total_price: this.data.pricing.total_price, mode: PayMentType.EXTEND_TRIP, reservation: this.data })
        // this.navCtrl.push(BookCarPayment, { total_price: this.data.pricing.total_price, mode: PayMentType.EXTEND_TRIP, reservation: this.data })
    } else {
      this.gocarToast.showToast({ position: 'bottom', duration: 2000, message: 'Please tick extend time' })
    }

  }

  ngOnDestroy() {
    this.events.unsubscribe(AppEvent.ExtendDone)
  }
}



