import { Http } from '@angular/http';
import { Injectable } from '@angular/core';
import { BaseService } from '../base.service';
import { AppConfig } from '@app/app.config';
import { Observable } from 'rxjs';
import { NativeStorageService } from '../storage/native-storage.service';

/*
  Generated class for the CarsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class CarTypesProvider extends BaseService {

  constructor(public http: Http, protected appConfig: AppConfig, private nativeStorageService: NativeStorageService) {
    super(http, appConfig);
  }

  getListTypes(): Observable<any> {
    return Observable.fromPromise(this.cacheCarType()).map((result) => {
      console.log(result.data);
      return result.data
    })
  }

  async cacheCarType() {
    //let exists = await this.nativeStorageService.getKey('cartypes')
    //if (exists) {
    //  let data = await this.nativeStorageService.getData('cartypes')
    //  return JSON.parse(data);
    //}
    let cartypes = await this.getData('cartypes/list').toPromise()
    //await this.nativeStorageService.setData('cartypes', cartypes)
    return cartypes
  }

}



