import { Injectable } from "@angular/core";
import { BLE } from '@ionic-native/ble';
import { Subscription } from "rxjs";
import { ToastController, Events } from "ionic-angular";
import { BluetoothLe } from 'ionic-native-bluetoothle';

import { Platform } from 'ionic-angular';
import { AppEvent } from "@common/appEvent";
import { GoCarToast } from "@shared/components/toast/gocar-toast";
@Injectable()
export class BluetoothService {
    scanSubscription: Subscription;
    connectSubscription: Subscription;
    value;
    data;
    params: {
        "request": true,
        "statusReceiver": false,
        "restoreKey": "bluetoothleplugin"
    };
    cordova: any;

    constructor(private ble: BLE, 
                private toastCtrl: ToastController, 
                public bluetoothle: BluetoothLe,
                public plt: Platform, 
                public events: Events, 
                public gocarToast: GoCarToast,

    ) {
        // this.plt.ready().then((readySource) => {
        //     console.log('Platform ready from', readySource);
        //     this.bluetoothle.initialize().subscribe(ble => {
        //     });

        // });


    }

    enableBluetooth() {
        return this.bluetoothle.isEnabled().then(val => {
            return val
        })
    }

    checkInitialize() {
        return new Promise((resolve, reject) => {

            this.bluetoothle.isInitialized().then(val => {
                console.log('isInitialized', val)
                if (val.isInitialized) {
                    resolve(val);
                } else {
                    //reject(val);
                    this.bluetoothle.initialize().subscribe(res => {
                        console.log(res)
                        if (res.status == 'enabled') {
                            resolve(val);
                        }
                    })
                }
            })
        });

    }


    find(name) {
        console.log("find device => ", name);
        return new Promise((resolve, reject) => {
            let timeout = setTimeout(() => {
                this.bluetoothle.stopScan()
                reject(new Error('Cannot find your car'))
            }, 5000)

            return this.bluetoothle.startScan().subscribe(val => {
                if (val.name == name) {
                    this.bluetoothle.stopScan();
                    this.data = val;
                    clearTimeout(timeout)
                    console.log('found', val);
                    resolve(val)
                }
            }, err => {
                console.log('err', err)
                this.gocarToast.showToast({ position: 'bottom', message: err.message, duration: 2000 })
                this.bluetoothle.stopScan();
                reject(err);
            })
        })
    }


    isConnected(device): Promise<any> {
        return new Promise((resolve, reject) => {
            let params = {
                "address": device.address,
            }
            this.bluetoothle.isConnected(params).then(val => {
                resolve(val);
            }).catch(err => {
                reject(err);
            })
        })

    }

    subscribe(device) {
        let params = {
            "address": device.address,
            "service": 'FFE0',
            "characteristic": 'FFE1',
        }
        return this.bluetoothle.subscribe(params)
    }

    connect2(device) {
        let params = {
            "address": device.address,
        }
        return this.bluetoothle.connect(params)
    }

    discover2(device) {
        return this.bluetoothle.discover({address: device.address})
    }

    connect(device) {
        return new Promise((resolve, reject) => {

            console.log('connect', device)
            let params = {
                "address": device.address,
            }
            let timeout = setTimeout(() => {
                this.bluetoothle.disconnect(params).then(() => {
                    this.presentToast('Cannot connect');
                    reject(false)
                }).catch((err) => {
                    console.log('bluetooth service 130', err)
                    reject(false)
                })
            }, 3000)
            this.bluetoothle.connect(params).subscribe(result => {
                console.log('cconnect result', result)
                this.data = result;
                // this.subscribe(device);

                if (result.status == 'disconnected') { 
                    reject(false)
                }

                if (result.status == 'connected') {
                    clearTimeout(timeout)
                    this.presentToast('Bluetooth Connect successfully');
                                resolve(true)
                    if(this.plt.is('ios')) {
                        this.bluetoothle.services(
                            {
                                "address": device.address,
                                "services": []
                            }
                        ).then((success) => {
                            console.log("services success ", success);
                        }, (err) => {
                            console.log("services success ", err);
                        
                        });
                    } else {
                        this.bluetoothle.isDiscovered(params).then((data) => {
                            console.log('bluetooth service 155', data)
                        }).catch((err) => {
                            console.log('bluetooth service 157', err)
                        })
                        this.bluetoothle.discover({
                            address: device.address,
                        }).then((success) => {
                            console.log("discover success", success);
                            if(success.services.length > 0) {
                                this.presentToast('Bluetooth Connect successfully');
                                resolve(true)
                            } else {
                                this.bluetoothle.disconnect(params).then(() => {
                                    this.presentToast('Connected. But cannot discover the device. Try again');
                                    reject(false)
                                }).catch((err) => {
                                    console.log('bluetooth service 165', err)
                                    reject(false)
                                })
                            }
                            
                        }, (err) => {
                            this.bluetoothle.disconnect(params).then(() => {
                                console.log("discover error", err);
                                this.presentToast('Connected. But cannot discover the device. Try again');
                                reject(false)
                            }).catch((err) => {
                                reject(false)
                            })
                        });
                    }
                }
                
            }, (err => {
                console.log('connect error', err)
                reject(err)
            }))

        })

    }


    reconnected(device) {
        console.log('reconnected', device)
        let params = {
            "address": device.address,
        }
        return this.bluetoothle.reconnect(params)
    }

    async writeLogin(service, characteristic, device) {
        try {
            let encodedString = this.bluetoothle.bytesToEncodedString(new Uint8Array([0x0E, 0x0E, 0x0E]));
            let params = {
                "address": device.address,
                "service": service,
                "characteristic": characteristic,
                "value": encodedString,
                //'type': 'noResponse'
            }
            let writeRes = await this.bluetoothle.write(params)
            console.log("writeLogin : ", writeRes);
            return writeRes
        } catch (error) {
            throw error
        }
    }

    async writeLogout(service, characteristic, device) {
        try {
            let encodedString = this.bluetoothle.bytesToEncodedString(new Uint8Array([0x1b, 0x1b, 0x1b]));
            let params = {
                "address": device.address,
                "service": service,
                "characteristic": characteristic,
                "value": encodedString,
            }
            let writeRes = await this.bluetoothle.write(params);
            console.log("writeLogout :", writeRes);
            return writeRes
        } catch (error) {
            throw error
        }
    }

    async writePassword(service, characteristic, device) {
        try {
            let bytes = this.stringToBytes('677340\r\n');
            console.log("bytes : ", bytes);
            var encodedString = this.bluetoothle.bytesToEncodedString(bytes);
            console.log("encodedString : ", encodedString);

            let params = {
                "address": device.address,
                "service": service,
                "characteristic": characteristic,
                "value": encodedString,
            }
            let writeRes = await this.bluetoothle.write(params);
            console.log("writePassword :", writeRes);
            return writeRes
        } catch (error) {
            throw error
        }
    }

    async writeLock(service, characteristic, device) {
        try {
            let bytes = this.stringToBytes("LCK;1\r\n");
            var encodedString = this.bluetoothle.bytesToEncodedString(bytes);
            console.log(encodedString);

            let params = {
                "address": device.address,
                "service": service,
                "characteristic": characteristic,
                "value": encodedString
            }
            let writeRes = await this.bluetoothle.write(params)
            console.log("writeLock :", writeRes);
            return writeRes
        } catch (error) {
            throw error
        }
    }


    async writeUnlock(service, characteristic, device) {
        try {
            let bytes = this.stringToBytes("LCK;0\r\n");
            var encodedString = this.bluetoothle.bytesToEncodedString(bytes)
            console.log(encodedString);

            let params = {
                "address": device.address,
                "service": service,
                "characteristic": characteristic,
                "value": encodedString
            }
            let writeRes = await this.bluetoothle.write(params)
            console.log("writeLock :", writeRes);
        } catch (error) {
            throw error
        }
    }



    close(device) {
        let params = {
            //"address": this.data.address,
            address: device.address
        }
        if (device != null) {
            return this.bluetoothle.disconnect(params).then(result => {
                console.log('bluetooth service 303 ', result)
                return this.bluetoothle.close(params)
            }).then(() => {
                console.log('Closed connection')
            }).catch(err => {
                console.log('bluetooth service 296 ', err)
                return this.bluetoothle.close(params).then(result => {
                }).catch(err => {
                })
            })
        }
    }



    presentToast(message) {
        let toast = this.toastCtrl.create({
            message,
            duration: 3000,
            position: 'bottom'
        });

        toast.onDidDismiss(() => {
        });

        toast.present();
    }

    stringToBytes(str) {
        var bytes = new ArrayBuffer(str.length * 2);
        var bytesUint16 = new Uint16Array(bytes);
        for (var i = 0; i < str.length; i++) {
            bytesUint16[i] = str.charCodeAt(i);
        }
        return new Uint8Array(bytesUint16);
    }
}


