import { ReservationTabsPage } from './../reservation-tabs/reservation-tabs';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { ReservationsProvider } from '@providers/reservations/reservations';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { GlobalService } from '@providers/global.provider';
import { CallNumber } from '@ionic-native/call-number';

/**
 * Generated class for the ReservationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-reservation',
    templateUrl: 'reservation.html',
})

export class ReservationPage {
    @ViewChild('tabroot') tabroot : NavController
    isHasReservations: boolean = false
    pageType: any
    constructor(public navCtrl: NavController,
        public navParams: NavParams,
        public reservation: ReservationsProvider,
        public alertCtrl: AlertController,
        private callNumber: CallNumber,
        public gocarModal: GoCarModal,
        public global: GlobalService,

    ) {
        this.countReservations()
    }

    ionViewDidEnter() {
        this.getParent();
    }

    getParent() {
        this.pageType = this.navParams.get('pageType')
        this.tabroot.setRoot(ReservationTabsPage, { pageType: this.pageType })
    }
    countReservations() {
        this.reservation.count().subscribe((res) => {
            if (res.data > 0) this.isHasReservations = true
            else this.isHasReservations = false
            this.global.sethasReservations(this.isHasReservations)
        }, err => {
            this.isHasReservations = false
            this.global.sethasReservations(this.isHasReservations)
        })
    }
    call() {

        let alertbox = this.alertCtrl
            .create({
                title: 'GoCar Hotline',
                message: "For emergencies, please call:" +
                          "\n 1-300-88-4622"
                          +"\n60 3 4047 5231",
                buttons: [
                    { text: 'Close' },
                    {
                        text: 'Call Now', handler: () => {

                            this.dial()

                        }
                    }

                ]
            })
        alertbox.present()
    }

    dial() {
        this.callNumber.callNumber("1300884622", false)
            .then(res => console.log('Launched dialer!', res))
            .catch(err => console.log('Error launching dialer', err));
    }


}



