// System import
import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Geolocation } from '@ionic-native/geolocation';
import { FirebaseApp } from 'angularfire2';
import { MenuController } from 'ionic-angular';
import { AppVersion } from '@ionic-native/app-version';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Observable } from 'rxjs';
import { AngularFireDatabase} from 'angularfire2/database'
import _ from 'lodash';

// pages import
import { HomePage } from '@pages/home/home';
import { ProfilePage } from '@pages/profile/profile';
import { UserPage } from '@pages/user/user'
import { RegisterDone } from '@pages/user/register/register-done/register-done';
import { ReservationPage } from '@pages/resevation/reservation/reservation';
import { ReferralPage } from '@pages/referral/referral';
import { WalletPage } from '@pages/wallet/wallet/wallet';
import { PromoPage } from '@pages/promo/promo';

//shared import
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { GoCarToast } from '@shared/components/toast/gocar-toast';
import { AlertBoxComponent } from '@shared/components/alert-box/alert-box';
import { NetWorkStatus } from '@shared/enums/network-status.enum';

//common import
import { AppEvent } from '@common/appEvent';
import { CONSTANTS } from '@common/constants';

// providers import
import { AuthProvider } from '@providers/auth/auth';
import { NetWorkService } from '@providers/network/network.provider';
import { GlobalService } from '@providers/global.provider';
import { BuidingsProvider } from '@providers/building/building';
import { FiltersProvider } from '@providers/fiters/filters';

import { AppConfig } from './app.config';
import { Updater } from '@common/updater';
import { LocationService } from '../../node_modules/@ionic-native/google-maps';
// for deployment

//--------------------------------------

export enum Menu {
  BOOKCAR = 'bookcar',
  RESERVATION = 'reservation',
  WALLET = 'wallet',
  PROMOTIONS = 'promotions',
  PROFILE = 'profile',
  REFERRAL = 'referral'
}

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = HomePage;
  currentMenu: Menu = Menu.BOOKCAR
  pagesRequiredLogin = CONSTANTS.PAGE_REQUIRED_LOGIN;
  isHasReservations: any
  pages: Array<{ title: string, component: any }>;
  disconnectSubscription;
  onconnectSubscription;
  title = "No Internet Connection";
  message = "You are currently in offline mode. Please reconnect and try again.";
  counter = 0;
  versionNumber;
  constructor(public menuCtrl: MenuController,
    public platform: Platform,
    public statusBar: StatusBar,
    public events: Events,
    public gocarToast: GoCarToast,
    public splashScreen: SplashScreen,
    public gocarModal: GoCarModal,
    public authService: AuthProvider,
    public netWorkService: NetWorkService,
    public firebase: FirebaseApp,
    public firebaseDB: AngularFireDatabase,
    public geoLocation: Geolocation,
    public global: GlobalService,
    public appConfig: AppConfig,
    public alertBoxComponent: AlertBoxComponent,
    public buidingsProvider: BuidingsProvider,
    public filtersProvider: FiltersProvider,
    private appVersion: AppVersion,
    public iab: InAppBrowser) {
    // translateService.setDefaultLang('en');
    // this.translateService.use('en');
    this.initializeApp();
    // this.initializeUpdate()
    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Book A Car', component: HomePage },
      { title: 'My Reservation', component: ReservationPage },
      { title: 'MyWallet', component: WalletPage },
      { title: 'Promo & Deals', component: PromoPage },
      { title: 'Profile', component: ProfilePage }

      // { title: 'Refer & earn', component: ReferralPage }
    ];
    this.isHasReservations = this.global.gethasReservations()
  }

  initializeApp() {
    console.log("initialieApp", navigator.onLine);
    this.platform.ready().then(() => {
      console.log("platform ready");
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      // this.rootPage = HomePage
      //  this.statusBar.styleDefault();
      this.appVersion.getVersionNumber().then((version) => {
        console.log(version);
        this.versionNumber = version
      })
      this.statusBar.backgroundColorByHexString('#37BC9B');
      this.statusBar.overlaysWebView(false)
      this.splashScreen.hide();
      // check internet are available

      this.events.subscribe(AppEvent.AppGoOffline, () => {
        let alertNetwork = this.alertBoxComponent.createInfoAlert(this.title, null, this.message);
        alertNetwork.present();
      })
      LocationService.getMyLocation().then((pos) => {
           this.global.setUserLocation(pos.latLng.lat, pos.latLng.lng)

        this.buidingsProvider.getNearestBuilding(pos.latLng.lat, pos.latLng.lng).subscribe((building) => {
          this.filtersProvider.setCities([building.data[0].city]);
        })
      })
      // this.geoLocation.getCurrentPosition().then(pos => {
      //   this.global.setUserLocation(pos.coords.latitude, pos.coords.longitude)

      //   this.buidingsProvider.getNearestBuilding(pos.coords.latitude, pos.coords.longitude).subscribe((building) => {
      //     this.filtersProvider.setCities([building.data[0].city]);
      //   })

      // })

      this.platform.registerBackButtonAction(() => {
        console.log("back button pressed");
        let currentPage = this.nav.getActive().component.name
        console.log(currentPage)
        if(currentPage == 'HomePage') {
          if (this.counter == 0) {
            this.counter++;
            //this.presentToast();
            this.gocarToast.showToast({ message: 'Double tap to exit.' })
            setTimeout(() => { this.counter = 0 }, 3000);
          } else {
            // console.log("exitapp");
            this.platform.exitApp();
          }
        }
      }, 0)


    });
    this.events.subscribe(AppEvent.RegisterEnd, (user) => {
      console.log('ended');
      this.nav.setRoot(RegisterDone, { user })
      this.currentMenu = Menu.PROFILE
    })

    this.events.subscribe(AppEvent.AppLoggedOut, () => {
      this.nav.setRoot(HomePage)
      this.currentMenu = Menu.BOOKCAR
    })

    this.events.subscribe(AppEvent.PaymentDone, (user) => {
      console.log('ended');
      this.gocarToast.showToast({ message: 'Youve successfully booked a GoCar. Thank you, and enjoy your trip!' })
      this.nav.setRoot(ReservationPage, { user })
      this.currentMenu = Menu.RESERVATION
    })

    this.events.subscribe(AppEvent.PaymentFailed, (user) => {
      console.log('ended');
      this.gocarToast.showToast({ message: 'Payment failed. Please try again.' , showCloseButton:true })
      //this.nav.setRoot(ReservationPage, { user })
    })

    this.events.subscribe(AppEvent.GoToReservation, () => {
      if (this.authService.isAuthenticated) {
        this.authService.getUserDatasByToken().subscribe((result) => {
          let user = result.data
          this.nav.setRoot(ReservationPage, { user })
          this.currentMenu = Menu.RESERVATION
        })
      }
    })
  }

  initializeUpdate() {
    let ref = this.firebaseDB.database.ref('/configs/isUpdate')
    ref.on('value', (resp) => {
      let isUpdate = resp.val()
      if(isUpdate) {
        let updater = new Updater()
    // updater.checkChannel()
        updater.toggleBeta()
      }
      console.log(resp.val())
    })
  }

  logout() {
    this.authService.doLogOut().then(() => {
      this.openLogin()
    })
  }

  openLogin() {
    this.menuCtrl.toggle('main-menu')
    this.gocarModal.showModal(UserPage)
  }

  get isAuthenticate() {
    return this.authService.isAuthenticated
  }

  openPage(page) {
    console.log(page);
    if (_.includes(this.pagesRequiredLogin, page)) {
      let token = this.authService.getCredentials();

      if (_.isEmpty(token)) {
        return this.openLogin();
      } else {
        console.log(page);
        if (this.global.netWorkStatus === NetWorkStatus.OFFLINE && (page === 'ReservationPage' || page === 'PromoPage')) {
          return this.setPage(page, null);
        }
        this.authService.getUserDatasByToken().subscribe((result) => {
          let user = result.data;
          if (user && !_.isEmpty(user)) {
            if (user.status === 'Registered') {
              return this.setPage(page, user);
            } else {
              return this.nav.setRoot(RegisterDone, { user });
            }
          }
        }, err => {
          console.log(err);
        });
      }
    } else {
      console.log("setPage");
      this.setPage(page)
    }
  }

  setPage(page, user?) {
    switch (page) {
      case 'HomePage':
        this.nav.setRoot(HomePage, { user });
        this.currentMenu = Menu.BOOKCAR
        break;
      case 'PromoPage':
        this.nav.setRoot(PromoPage, { user });
        this.currentMenu = Menu.PROMOTIONS
        break
      case 'ReservationPage':
        this.nav.setRoot(ReservationPage, { user });
        this.currentMenu = Menu.RESERVATION
        break;
      case 'WalletPage':
        this.nav.setRoot(WalletPage, { user });
        this.currentMenu = Menu.WALLET
        break;
      case 'ProfilePage':
        this.nav.setRoot(ProfilePage, { user });
        this.currentMenu = Menu.PROFILE
        break;
      case 'ReferralPage':
        this.nav.setRoot(ReferralPage, { user });
        this.currentMenu = Menu.REFERRAL
        break;
    }
  }

  ngOnDestroy() {
    this.events.unsubscribe(AppEvent.RegisterEnd);
    this.events.unsubscribe(AppEvent.PaymentDone);
    this.events.unsubscribe(AppEvent.GoToReservation);
    this.events.unsubscribe(AppEvent.AppLoggedOut)
    // this.disconnectSubscription.unsubscribe();
  }

  ionViewWillEnter() {
    this.addKeyboardListeners();
    this.filtersProvider.validateTime()

  }

  ionViewWillLeave() {
    this.removeKeyboardListeners();
  }


  private addKeyboardListeners() {
    Observable.fromEvent(window, 'keyboardWillShow').subscribe(e => {
      // Code here
      console.log("keyboardWillShow");
      document.body.classList.add('keyboard-is-open');
    });

    Observable.fromEvent(window, 'keyboardWillHide').subscribe(e => {
      // Code here
      console.log("keyboardWillHide");
      document.body.classList.remove('keyboard-is-open');


    });
  }

  private removeKeyboardListeners() {
    //if (this.keybaordShowSub) this.keybaordShowSub.unsubscribe();
    //if (this.keyboardHideSub) this.keyboardHideSub.unsubscribe();
  }

  help() {
    let browser = this.iab.create('https://gocarhelp.zendesk.com/hc/en-us/categories/200199507-Frequently-Asked-Questions', '_blank');
  }

}



