import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { ReservationPage } from './reservation';
import { ReservationDetailPage } from '../reservation-detail/reservation-detail';
import { ReservationDrivingPage } from '../reservation-driving/reservation-driving';
import { ReservationReturnedPage } from '../reservation-returned/reservation-returned';
import { ReservationExtendPage } from '../reservation-extend/reservation-extend';
import { ReservationTabsPage } from '../reservation-tabs/reservation-tabs';
import { ComponentsModule } from '@shared/components/components.module';
import { BluetoothService } from '@providers/bluetooth/bluetooth.service';
import { MbscModule } from '../../../lib/mobiscroll/js/mobiscroll.angular.min';
import { ConfirmChangeReservation } from '@pages/resevation/reservation-driving/confirm-change-reservation/confirm-change-reservation';
import { ReservationExpiredPage } from "@pages/resevation/reservation-expired/reservation-expired";
import { BLE } from '@ionic-native/ble';

@NgModule({
  declarations: [
    ReservationPage,
    ReservationDetailPage,
    ReservationDrivingPage,
    ReservationReturnedPage,
    ReservationExtendPage,
    ReservationTabsPage,
    ReservationExpiredPage,
    ConfirmChangeReservation
  ],
  imports: [
    IonicPageModule.forChild(ReservationPage),
    ComponentsModule,
    // DatePipe,
    MbscModule
  ],
  entryComponents: [
    ReservationPage,
    ReservationDetailPage,
    ReservationDrivingPage,
    ReservationReturnedPage,
    ReservationExtendPage,
    ReservationTabsPage,
    ReservationExpiredPage,
    ConfirmChangeReservation
  ],
  providers:  [
    BluetoothService,
    BLE,
  ]
})
export class ReservationPageModule {}



