import { Component, Input, Output, EventEmitter, OnChanges, SimpleChanges } from "@angular/core";
import { CarTypeImageEnum } from "../../enums/car-type-image.enum";

@Component({
  selector: 'card-filter',
  templateUrl: './card-filter.html'
})
export class CardFilterComponent implements OnChanges {
  @Input() type: string
  @Input() iconDir: string
  @Input() text: string
  @Input() selected: boolean
  @Output() selectEvent: EventEmitter<any> = new EventEmitter()

  public imgSrc: string = './assets/imgs/car_type/'

  constructor() {

  }

  ngOnChanges(changes: SimpleChanges) {

  }

  onSelect() {
    this.selectEvent.emit(this.type)
  }

  get image() {
    console.log(this.type);
    switch (this.type) {
      // case "Almera":
      //   return this.iconDir
      // case 'Captur':
      //   return this.iconDir
      // case 'Captur Promo':
      //   return this.iconDir
      // case 'X-Gear':
      //   return this.iconDir
      // case 'Bezza':
      //   return this.iconDir
      // case 'Grand Livina':
      //   return this.iconDir
      // case 'Koleos':
      //   return this.iconDir
      // case 'Navara':
      //   return this.iconDir
      case 'All':
        return `${this.imgSrc}${CarTypeImageEnum.All}`
      default:
        return this.iconDir
    }
    // switch (this.type) {
    //     case CarTypeEnum.MPV:
    //         return `${this.imgSrc}${CarTypeImageEnum.MPV}`
    //     case CarTypeEnum.SEDAN:
    //         return `${this.imgSrc}${CarTypeImageEnum.SEDAN}`
    //     case CarTypeEnum.SUV:
    //         return `${this.imgSrc}${CarTypeImageEnum.SUV}`
    //     default:
    //         return `${this.imgSrc}${CarTypeImageEnum.SEDAN}`
    // }
  }

}


