import { Http, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { BaseService } from '../base.service';
import { AppConfig } from '@app/app.config';
import { Observable } from 'rxjs';
/*
  Generated class for the CarsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class Hub2hubProvider extends BaseService {

  constructor(public http: Http, protected appConfig: AppConfig) {
    super(http, appConfig);
  }

  listHub2Hub(pickupId): Observable<any> {
    return this.getData(`hub2hub/list/hub2hub/${pickupId}`)
      .catch((err) => {
        console.log(err);
        return Observable.throw(false);
      });
  }
}



