import { Component, OnDestroy, ViewChild } from '@angular/core';
import { NavController, NavParams, Events, PopoverController, Platform } from 'ionic-angular';
import { ReservationsProvider } from '@providers/reservations/reservations';
import { LoadingComponent } from '@shared/components/loading/loading';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { ViewInfoComponent } from '@shared/components/view-info/view-info';
import { ReservationExtendPage } from '../reservation-extend/reservation-extend';
import { RentalDetailPage } from '../../rental-detail/rental-detail';
import { AuthProvider } from '@providers/auth/auth';
import { BluetoothService } from '@providers/bluetooth/bluetooth.service';
import { SmsService } from '@providers/sms/sms.provider';
import { TimeService } from '@providers/time.service';
import * as _ from 'lodash'
import { AppEvent } from '@common/appEvent';
import { GlobalService } from '@providers/global.provider';
import { UserPage } from '../../user/user';
import { GoCarStorageService } from '@providers/storage/localstorage.service';
import { CONSTANTS } from '@common/constants';
import { NetWorkStatus } from '@shared/enums/network-status.enum';
import { NetWorkService } from '@providers/network/network.provider';
import { MbscRangeOptions, mobiscroll } from '@lib/mobiscroll/js/mobiscroll.angular.min';
import { Resevervation } from '@models/reservation';
import moment from 'moment'
import { ConfirmChangeReservation } from '@pages/resevation/reservation-driving/confirm-change-reservation/confirm-change-reservation';
import { BookCarPayment } from '@pages/book-car/payment/payment';
import { PayMentType } from '@shared/enums/payment-type.enum';
import { GoCarToast } from '@shared/components/toast/gocar-toast';
import { BluetoothLe } from 'ionic-native-bluetoothle';
import { BleDevice, GocarDevice, DeviceState } from '@providers/bluetooth/ble-device.service';
import { Subscription, Observable } from 'rxjs';
import { LocalNotifications } from '@ionic-native/local-notifications';
import { ReservationMode } from '@shared/enums/reservation-mode.enum';
import { PricingProvider } from '@providers/pricing/pricing.provider';
/**
 * Generated class for the ReservationDrivingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
mobiscroll.settings = {
  theme: 'mobiscroll'
};
@Component({
  selector: 'page-reservation-driving',
  templateUrl: 'reservation-driving.html',
})
export class ReservationDrivingPage implements OnDestroy {
  @ViewChild('externalRange') externalRange: any
  listReservation: any[];
  listExpired = [];
  listComing = [];
  showEmpty = false;
  listNotifi = []
  isBluetooth = false;
  deviceName: BleDevice = null;
  connectionState: DeviceState
  data;
  isSubcribedStateChange: boolean = false
  stateSubcription: Subscription
  initStatus: string
  osType: 'ios' | 'android' | 'other'
  scanStatus: string = 'Scan stopped'
  range: Array<Date>;
  currentReservation: Resevervation
  externalSettings: MbscRangeOptions = {
    controls: ['calendar', 'time'],
    // returnFormat: 'moment',
    steps: {
      minute: 60,
    },
    min: new Date(),
    timeFormat: 'HH:00 A',
    // timeFormat: 'HH',
    onBeforeShow: (event, inst) => {
      inst.setVal([moment(this.currentReservation.start_time), moment(this.currentReservation.end_time)])
    },
    onSet: (event, inst) => {
      this.onSet()
    },
    onBeforeClose: (event, inst) => {
      if(event.button == 'cancel') {
        return true
      }
      let range = inst.getVal(true) // temp (Optional): Boolean - If true, temporary selected date is returned (prior clicking the Set button).
      if (moment().startOf('hour').isAfter(range[0])) {
        this.gocarToast.showToast({ message: 'Start time must be more than current.' })
        return false
      }
      return true;
    }
  };
  networkStatus: boolean
  NetWorkStatusEnum = NetWorkStatus
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private reservationsProvider: ReservationsProvider,
    private loading: LoadingComponent,
    private gocarModal: GoCarModal,
    private gocarToast: GoCarToast,
    private authProvider: AuthProvider,
    private events: Events,
    private pricingProvider: PricingProvider,
    private gocarStorage: GoCarStorageService,
    public bluetoothService: BluetoothService,
    private ble: BluetoothLe,
    public smsService: SmsService,
    public platform: Platform,
    public timeService: TimeService,
    public popoverCtrl: PopoverController,
    public network: NetWorkService,
    // private localNotifications: LocalNotifications,
    private globalService: GlobalService, ) {
    this.listReservation = []
    this.listExpired = []
    this.listNotifi = []
    this.networkStatus = navigator.onLine
    if (platform.is('ios')) {
      this.osType = 'ios'
    } else if (platform.is('android')) {
      this.osType = 'android'
    } else {
      this.osType = 'other'
    }
    this.events.subscribe(AppEvent.AppGoOffline, () => {
      this.networkStatus = false
    })
    this.events.subscribe(AppEvent.AppGoOnline, () => {
      this.networkStatus = true
    })
    // this.localNotifications.on('click').subscribe((result) => {
    //   this.gocarModal.showModalV2(RentalDetailPage, { data: result.data, service: this })
    // })
  }

  initDataNotification() {
    _.forEach(this.listReservation, (val, key) => {
      if (val.status == 'DURING_TRIP') {
        let time = moment(val.end_time).utc().subtract(30, 'minute').format(CONSTANTS.TIME_FORMAT)
        let data = {
          'id': key,
          'title': 'IMPORTANT',
          'text': 'Your reservation ends in 30 minutes.Please return the car on time, or extend your current reservation.',
          'trigger': { at: new Date(time) },
          'data': val
        }
        this.listNotifi.push(data)
      }
    })
    // this.localNotifications.schedule(this.listNotifi)
  }

  ionViewDidEnter() {
    console.log('reservation ionViewDidEnter')
    this.initData()
  }

  initData() {
    this.deviceName = this.globalService.getBLE()
    if (this.deviceName && !this.isSubcribedStateChange) {
      this.subcribeStateChange()
    }
    this.listExpired = this.globalService.getReservation() || []
    this.loadData();
    this.globalService.resetReservation();
  }

  loadData() {
    this.listReservation = [];
    if (!navigator.onLine) {
      this.listReservation = JSON.parse(this.gocarStorage.getData(CONSTANTS.RESERVATION_KEY) || '[]')
      return true
    }
    if (this.authProvider.isAuthenticated) {
      this.loading.showLoading();
      this.reservationsProvider.getListReservations(this.authProvider.userId).subscribe((result) => {
        this.loading.hideLoading();
        this.listReservation = result.data;
        this.initDataNotification();
        this.listReservation.map((item) => {
          if (this.currentReservation && item.id == this.currentReservation.id) {
            item.isBluetoothOpen = this.deviceName && this.deviceName.currentState == DeviceState.DISCOVERED
          }
          item.disabledBluetooth = false
        })
        this.countExpired(result.data)
        this.listReservationComing(result.data)
        this.gocarStorage.setData(CONSTANTS.RESERVATION_KEY, JSON.stringify(this.listReservation))
      }, err => {
        this.loading.hideLoading()
        this.listReservation = JSON.parse(this.gocarStorage.getData(CONSTANTS.RESERVATION_KEY) || '[]')
      })
    } else {
      this.gocarModal.showModal(UserPage)
    }
  }

  showInfo(event) {
    this.gocarModal.showModal(ViewInfoComponent, { data: event })
  }

  extend(event) {
    this.gocarModal.showModal(ReservationExtendPage, { data: event })
    this.gocarModal.currentModal.onDidDismiss((isExtended) => {
      if (isExtended) {
        this.initData()
      }
    })
  }

  rental(reservation) {
    this.currentReservation = reservation
    this.gocarModal.showModalV2(RentalDetailPage, { data: reservation, service: this, mode: ReservationMode.DRIVING })
  }

  eventLock(reservation) {
    if (this.deviceName && this.deviceName.currentState == DeviceState.DISCOVERED) {
      this.deviceName.unlockDevice()
    } else {
      this.smsService.lockCar(reservation.id).subscribe(val => {
        console.log(val)
      }, err => {
        console.log(err)
      })
    }
  }

  eventUnlock(reservation) {
    if (this.deviceName && this.deviceName.currentState == DeviceState.DISCOVERED) {
      this.deviceName.lockDevice()
    } else {
      this.smsService.unlockCar(reservation.id).subscribe(val => {
        console.log(val)
      }, err => {
        console.log(err)
      })
    }
  }

  initialize() {
    this.ble.initialize().take(1).subscribe(result => {
      console.log(`bluetooth status: ${result.status}`)
      this.initStatus = result.status
      this.scan()
    })
  }

  scan() {
    let scanSubscription = this.ble.startScan().subscribe(result => {
      console.log(JSON.stringify(result))
      if (result.status === 'scanResult') {
        if (result.name == this.currentReservation.bluetooth_address) {
          clearTimeout(scanTimeout)
          this.ble.stopScan().then(() => {
            console.log('scanStopped')
            let device: GocarDevice = {
              address: result.address,
              characteristic: this.currentReservation.bluetooth_characteristic,
              service: this.currentReservation.bluetooth_service
            }
            this.connect2(device)
            this.scanStatus = 'scanStopped'
            scanSubscription.unsubscribe()
          })
        }
      } else if (result.status === 'scanStarted') {
        console.log('scan started')
        this.scanStatus = 'scanStarted'
      } else {
        // TODO handle error
        this.currentReservation.disabledBluetooth = false;
        console.log('start scan error: ' + JSON.stringify(result))
      }
    }, err => {
      console.log('start scan error 1', err)
      this.gocarToast.showToast({ message: err.message })
      this.currentReservation.disabledBluetooth = false;
      this.rollbackBluetooth(this.currentReservation.id, false)
      setTimeout(() => {
        this.isBluetooth = false
      })
      clearTimeout(scanTimeout)
    })
    let scanTimeout = setTimeout(() => {
      this.ble.stopScan().then(() => {
        console.log('scanStopped')
        this.smsService.resetCar(this.currentReservation.id).subscribe((data) => {
          console.log('Car is unlocked', data)
        }, err => {
          console.log('cannot unlocked', err)
        })
        this.scanStatus = 'scanStopped'
        scanSubscription.unsubscribe()
        this.currentReservation.disabledBluetooth = false;
        this.gocarToast.showToast({ message: 'Cannot find your car' })
        this.rollbackBluetooth(this.currentReservation.id, false)
        setTimeout(() => {
          this.isBluetooth = false
        })
      })
    }, 5000)
  }

  eventBluetooth(reservation) {
    if (!this.isBluetooth) {
      this.currentReservation = reservation
      this.isBluetooth = true // to prevent event onchange from check bluetooth
      reservation.disabledBluetooth = true;
      if (reservation.isBluetoothOpen) {
        this.initialize()
      } else {
        this.disconnect2()
      }
    }
  }

  disconnect2() {
    return this.deviceName.disconnect().then(() => {
    }).catch((error) => {
      //console.log(11123123, JSON.stringify(error))
      this.isBluetooth = false
      this.currentReservation.disabledBluetooth = false
    })
  }

  connect2(device: GocarDevice) {
    this.deviceName = new BleDevice(this.ble, this.osType, device)
    this.globalService.setBLE(this.deviceName)
    this.subcribeStateChange()
    this.deviceName.isconnected().then((result) => {
      //console.log('reservation-driving isconnected.then', result)
      if (result.isConnected == false) {
        this.deviceName.reconnect()
      }
    }).catch((err) => {
      //console.log('reservation-driving isconnected.catch', err)
      this.deviceName.connect()
    })
  }

  unsubcribeStateChange() {
    // this.deviceName.stateChangeEvent.unsubscribe()
    if (this.stateSubcription) {
      this.stateSubcription.unsubscribe()
    }
  }

  subcribeStateChange() {
    this.isSubcribedStateChange = true
    this.stateSubcription = this.deviceName.stateChangeEvent.subscribe((data) => {
      console.log('state changed', data)
      this.connectionState = data.newState
      if (data.newState == DeviceState.DISCOVERED) {
        this.gocarToast.showToast({ message: 'Connected' })
        this.isBluetooth = false
        this.currentReservation.disabledBluetooth = false;
      } else if (data.newState == DeviceState.DISCONNECTED) {
        this.gocarToast.showToast({ message: 'Disconnected' })
        console.log('Disconnected 1', this.isBluetooth)
        if (this.isBluetooth) {
          this.currentReservation.disabledBluetooth = false;
          this.rollbackBluetooth(this.currentReservation.id, false)
          setTimeout(() => {
            this.isBluetooth = false
          })
        }
      } else if (data.oldState != DeviceState.DISCONNECTED && data.newState == DeviceState.CLOSED) {
        this.gocarToast.showToast({ message: 'Disconnected' })
        this.currentReservation.disabledBluetooth = false;
        if (this.isBluetooth) {
          this.rollbackBluetooth(this.currentReservation.id, false)
          setTimeout(() => {
            this.isBluetooth = false
          })
        }
      }
    }, (err) => {
      console.log('state changed err', err)
      this.isSubcribedStateChange = false
    })
  }

  rollbackBluetooth(resId, val) {
    let reservation = _.find(this.listReservation, (item) => {
      return item.id == resId
    })
    reservation.isBluetoothOpen = val
  }

  countExpired(listReservation) {
    this.listExpired = _.filter(listReservation, (o) => {
      return o.expired;
    })
  }

  listReservationComing(listReservation) {
    this.listComing = _.filter(listReservation, (o) => {
      return !o.expired;
    })
    if (this.listComing.length == 0) {
      this.showEmpty = true;
    }
  }


  eventExpired(event) {
    this.getListExpired();
  }

  buildParams() {
    let startTime = moment(this.range[0]).format(CONSTANTS.DATE_TIME_FORMAT).toString()
    let endTime = moment(this.range[1]).seconds(0).format(CONSTANTS.DATE_TIME_FORMAT).toString()
    let params = {
      carId: this.currentReservation.carId,
      userId: this.currentReservation.userId,
      startTime: this.currentReservation.start_time,
      endTime: this.currentReservation.end_time,
      newStartTime: startTime,
      newEndTime: endTime,
      buildingId: this.currentReservation.buildingId
    }
    return params
  }

  onSet() {
    console.log(this.range)
    this.loading.showLoading()
    this.pricingProvider.getChangeReservationPrice(this.buildParams()).subscribe((data) => {
      this.loading.hideLoading()
      let confirm = this.popoverCtrl.create(ConfirmChangeReservation, { startTime: this.range[0], endTime: this.range[1], data }, { cssClass: 'popover-confirm' })
      confirm.present()
      confirm.onDidDismiss((data) => {
        if (data && data.confirmed) {
          if (data.price && data.price > 0) {
            this.proceedPayment(data)
          } else {
            this.proceedChangeReservation()
          }
        }
      })
    }, (err) => {
      this.loading.hideLoading()
    })
  }

  changeReservation(reservation) {
    if (reservation.status == 'BEFORE_TRIP') {
      this.currentReservation = reservation
      this.externalRange.instance.show()
    }
  }

  proceedChangeReservation(): any {
    this.loading.showLoading()
    this.reservationsProvider.changeReservation(this.buildChangeReservation()).subscribe(() => {
      this.initData()
      this.loading.hideLoading()
    }, err => this.loading.hideLoading())
  }

  proceedPayment(data?): any {
    let startTime = moment(this.range[0]).format(CONSTANTS.DATE_TIME_FORMAT).toString()
    let endTime = moment(this.range[1]).seconds(0).format(CONSTANTS.DATE_TIME_FORMAT).toString()
    this.currentReservation.newStartDate = startTime
    this.currentReservation.newEndDate = endTime
    this.gocarModal.showModal(BookCarPayment, { params: this.buildParams(), mode: PayMentType.CHANGE_RESERVATION, reservation: this.currentReservation })

    this.gocarModal.currentModal.onDidDismiss((isPaid) => {
      if (isPaid) {
        this.initData()
      }
    })
  }

  buildChangeReservation() {
    let startTime = moment(this.range[0]).format(CONSTANTS.DATE_TIME_FORMAT).toString()
    let endTime = moment(this.range[1]).seconds(0).format(CONSTANTS.DATE_TIME_FORMAT).toString()
    return {
      userId: this.currentReservation.userId,
      carId: this.currentReservation.carId,
      buildingId: this.currentReservation.buildingId,
      oldStartTime: this.currentReservation.start_time,
      oldEndTime: this.currentReservation.end_time,
      startTime,
      endTime,
      reservationId: this.currentReservation.id,
      useGocarCredit: false
    }
  }

  getListExpired() {
    if (this.authProvider.isAuthenticated) {
      this.reservationsProvider.getListExpired(this.authProvider.userId).subscribe((result) => {
        if (result.data) {
          this.listReservation = result.data
          this.listExpired = result.data
        }
      }, err =>
          console.log(err)
      )
    }
  }

  ngOnDestroy() {
    console.log('reservation destroyed')
    this.unsubcribeStateChange()
    this.events.unsubscribe(AppEvent.ExtendDone)
    this.events.unsubscribe(AppEvent.AppGoOnline)
    this.events.unsubscribe(AppEvent.AppGoOffline)
  }
}



