import { Http, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { BaseService } from '../base.service';
import { AppConfig } from '@app/app.config';
import { Observable } from 'rxjs';
import { GlobalService } from '../global.provider';
import { FiltersProvider } from '../fiters/filters';
import { Car } from '@models/car';
import { AuthProvider } from '../auth/auth';
import { UtilsService } from '../utils.service';
import * as _ from 'lodash'
import { AlertBoxComponent } from '@shared/components/alert-box/alert-box';
import { Events } from 'ionic-angular';
import * as moment from "moment"
import { TimeService } from '@providers/time.service';
/*
  Generated class for the CarsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class CarsProvider extends BaseService {

  constructor(public http: Http,
    public global: GlobalService,
    public auth: AuthProvider,
    public utilService: UtilsService,
    public events: Events,
    public alertBox: AlertBoxComponent,
    public filterProviders: FiltersProvider,
    protected appConfig: AppConfig,
    private timeService: TimeService) {
    super(http, appConfig, alertBox, events, global);
    console.log('Hello CarsProvider Provider');
  }

  getCars(pageIndex?: any, filter?: any, buildingId?: any, limit?: any): Observable<any> {
    let startTime = this.filterProviders.getBookingTimeStart()
    let endTime = this.filterProviders.getBookingTimeEnd()
    let timeStart = moment(startTime)
    if (timeStart.isBefore(this.timeService.getNow())) {
      this.filterProviders.validateTime()
      startTime = this.filterProviders.getBookingTimeStart()
      endTime = this.filterProviders.getBookingTimeEnd()
    }
    let location = this.global.getUserLocation()
    pageIndex = pageIndex || 0
    let body = {
      buildingId,
      startTime,
      endTime,
      limit,
      pageIndex,
      filter,
      location
    }
    return this.postData(`cars/list`, body)
      .map((res: Response) => {
        let retData = res.json()
        let cars: Car[] = []
        let data = retData.data
        let availbleCars = data.availbleCars
        let unavailbleCars = data.unavailbleCars
        this.utilService.sortCars(availbleCars)
        this.utilService.sortCars(unavailbleCars)
        let unsortCars = _.concat(availbleCars, unavailbleCars)
        unsortCars.forEach(element => {
          cars.push(new Car(element))
        });
        data.cars = cars
        return data
      })
      .catch((err) => {
        console.log(err);
        return Observable.throw(err);
      });
  }

  pickCar(buildingId, carTypeId) {
    let startTime = this.filterProviders.getBookingTimeStart()
    let endTime = this.filterProviders.getBookingTimeEnd()
    let userId = this.auth.userId
    return this.postData('cars/pick', { buildingId, carTypeId, startTime, endTime, userId })
      .map((res: Response) => {
        let retData = res.json()
        let car = retData.data
        return car
      })
      .catch(err => {
        return Observable.throw(err)
      })
  }
}



