
import { Component } from "@angular/core";
import { FiltersProvider } from '@providers/fiters/filters';
import { Events } from 'ionic-angular';
import moment from 'moment'
import { CONSTANTS } from "@common/constants";

@Component({
    selector: 'select-date',
    templateUrl: 'select-date.html'
})

export class SelectDatePage {
    constructor(
        private filtersProvider: FiltersProvider,
        public events: Events
    ) {
        this.events.publish('calendar:date');
    }


    dateRange: Array<any>;

    rangeSettings: any = {
        min: new Date(),
        display: 'inline',
        calendarScroll: 'vertical',
        layout: 'liquid',
        calendarHeight: '400',
        rows: 8
    };

    ngOnInit(): void {
        //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
        console.log('set default range');
        this.dateRange = [new Date(this.filtersProvider.getStartDate()), new Date(this.filtersProvider.getEndDate())]
    }

    setRange(event) {
        console.log("Set Range");
        console.log("set range", event.inst.getVal());

        if (event.inst.getVal()[0] !== null) {
            let startDate = event.inst.getVal()[0]
            this.filtersProvider.setStartDate(moment(startDate).format(CONSTANTS.BOOKING_DATE_FORMAT));
            this.filtersProvider.setEndDate(moment(startDate).format(CONSTANTS.BOOKING_DATE_FORMAT));
        }
        if (event.inst.getVal()[1] !== null) {
            let endDate = event.inst.getVal()[1]
            this.filtersProvider.setEndDate(moment(endDate).format(CONSTANTS.BOOKING_DATE_FORMAT));
        }

    }

    ionViewDidLoad() {
        // this.filtersProvider.setStartDate();
        console.log('ionViewDidLoad', 'Select date running')
    }



}


