import { Component, ViewChild } from "@angular/core";
import { ViewController, NavController } from "ionic-angular";
import { SelectDatePage } from "./select-date/select-date";
import { SelectTimePage } from "./select-time/select-time";
import { FiltersProvider } from '@providers/fiters/filters';
import { Events } from 'ionic-angular';
import moment from 'moment'
import { GoCarToast } from "../toast/gocar-toast";
@Component({
    selector: 'gocar-calendar',
    templateUrl: './gocar-calendar.html',

})
export class GocarCalendar {
    @ViewChild('nav') nav: NavController
    // main: any = SelectDatePage
     state : any ='date';
    originDate:any = {}
    constructor(
        public viewCtrl: ViewController,
        public filterProvider: FiltersProvider,
        public gocarToast: GoCarToast,
        public events: Events
    ) {
        events.subscribe('calendar:date', () =>{
            console.log("state calendar:date");
            this.state = 'date'
        })

        events.subscribe('calendar:time', () =>{
            console.log("state calendar:date");
            this.state = 'time'
        })
    }

    ngOnInit(): void {
        //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
        //Add 'implements OnInit' to the class.
        this.state = 'date'
        this.originDate['startDate'] = this.filterProvider.getStartDate()
        this.originDate['startTime'] = this.filterProvider.getStartTime()
        this.originDate['endDate'] = this.filterProvider.getEndDate()
        this.originDate['endTime'] = this.filterProvider.getEndTime()
    }
    close(endType?) {
        if(endType != 'manual') {
            let start = this.filterProvider.getBookingTimeStart()
            let end = this.filterProvider.getBookingTimeEnd()
            if(moment(start).isSame(end) || moment(start).isAfter(end)) {
                return this.gocarToast.showToast({duration: 5000, message: 'Invalid DateTime'})
            }
            this.viewCtrl.dismiss(true)
        } else {
            this.filterProvider.setStartDate(this.originDate['startDate'])
            this.filterProvider.setEndDate(this.originDate['endDate'])
            this.filterProvider.setStartTime(this.originDate['startTime'])
            this.filterProvider.setEndTime(this.originDate['endTime'])
            this.viewCtrl.dismiss(false)
        }
        
    }

    ionViewDidLoad() {
        this.nav.setRoot(SelectDatePage)
    }

    gotoSelectDate(){
        this.nav.push(SelectDatePage)
    }

    gotoSelectTime() {
        this.nav.push(SelectTimePage)
    }

  
}


