import { Component, ViewChild } from "@angular/core";
import { Nav, Events, ViewController } from "ionic-angular"
import { LoginPage } from "./login/login";
import { AppEvent } from "@common/appEvent";

@Component({
    selector: 'user',
    templateUrl: './user.html'
})

export class UserPage {
    @ViewChild(Nav) userNav: Nav
    constructor(public events: Events, public viewCtrl: ViewController) {
        this.events.subscribe(AppEvent.UserDismiss, () => {
            this.viewCtrl.dismiss()
        })
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad', 'UserPage')
        this.userNav.setRoot(LoginPage)
    }
}


