import { DropLocationPage } from './../../../pages/drop-location/drop-location';
import { Component, Output, EventEmitter, Input, ViewChild, SimpleChanges } from "@angular/core";
import { ViewController, Nav } from "ionic-angular";
// import { Nav, Platform, Events } from 'ionic-angular';
import { OnChanges } from "@lib/mobiscroll/src/js/frameworks/angular";
import { AppMode } from "../../enums/app-mode.enum";
import { AppConfig } from "@app/app.config";
import { GlobalService } from "@providers/global.provider";
import { UtilsService } from "@providers/utils.service";

@Component({
    selector: 'gocar-card',
    templateUrl: './gocar-card.html'
})
export class GoCarCard implements OnChanges {
    // @ViewChild(Nav) nav: Nav;
    @Input() car
    @Input() mode: any
    @Output() event: EventEmitter<any> = new EventEmitter()
    @Output() eventBuilding: EventEmitter<any> = new EventEmitter()
    private appMode = AppMode
    private imageUrl: string
    private userLocation

    constructor(public viewCtrl: ViewController,
        public nav:Nav,
        public appConfig: AppConfig,
        public global: GlobalService,
        public utilsService: UtilsService,
       ) {
        this.userLocation = global.getUserLocation()
    }

    get distance() {
        if (this.userLocation) {
            let point1 = { lat: this.car.building_lat, lng: this.car.building_lng }
            let point2 = { lat: this.userLocation.lat, lng: this.userLocation.lng }
            let distance = this.utilsService.getDistanceFromPointToPoint(point1, point2)
            return distance / 1000
        }
        return 0
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.car && changes.car.currentValue) {
            this.imageUrl = `${this.appConfig.carImageUrl}/${this.car.carTypeId}/${this.car.photo}`
        }
    }

    get duration() {
        if (this.car.pricing) {
            let month = this.car.pricing.month > 0 ? (this.car.pricing.month < 2 ? `${this.car.pricing.month} month` : `${this.car.pricing.month} months`) : ''
            let day = this.car.pricing.day > 0 ? (this.car.pricing.day < 2 ? `${this.car.pricing.day} day` : `${this.car.pricing.day} days`) : ''
            let hour = this.car.pricing.hour > 0 ? (this.car.pricing.hour < 2 ? `${this.car.pricing.hour} hour` : `${this.car.pricing.hour} hours`) : ''
            return `${month} ${day} ${hour}`
        }
        return ''
    }

    fireEvent(event) {
        this.event.emit(this.car)
    }
    showBuilding(event) {
        this.eventBuilding.emit(this.car)
    }
    dropOff(){
        this.nav.push(DropLocationPage)
    }
}


