import { Component, Input, SimpleChanges } from '@angular/core';
import { GoCarPopover } from '../popover/gocar-popover';
import { UtilsService } from '@providers/utils.service';
import { CONSTANTS } from '@common/constants';
import { ImageType } from '../../enums/image-type.enum';
import { SelectImage } from '../select-image/select-image';
import { AppConfig } from "@app/app.config";
import { UploadImage } from '@providers/upload/upload';
import { AuthProvider } from '@providers/auth/auth';
import { LoadingComponent } from '../loading/loading';
import { AlertBoxComponent } from '../alert-box/alert-box';

/**
 * Generated class for the RegisterNotApplicableComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'register-not-applicable',
  templateUrl: 'register-not-applicable.html'
})
export class RegisterNotApplicableComponent {
  public passport: any
  public imagesProfile: any[] = []
  public licence: any
  public uploadUrl: any = this.appConfig.apiUrl + '/user/upload-profile'
  public isPending = false;
  @Input() userData: any;
  constructor(private popover: GoCarPopover,
    private utilsService: UtilsService,
    private appConfig: AppConfig,
    private uploadImage: UploadImage,
    private gocarLoading: LoadingComponent,
    private alert: AlertBoxComponent,
    private authProvider: AuthProvider
  ) {
  }

  ngOnChanges(changes: SimpleChanges): void {

  }

  private attachLicence() {
    this.popover.show(SelectImage).then(data => {
      if (data) {
        this.utilsService.thumbnailify(data, CONSTANTS.THUMBNAIL_SIZE, (thumbnail) => {
          this.licence = thumbnail
        })
        let item = { image: data, type: ImageType.LICENCE }
        this.utilsService.addItemInArray(this.imagesProfile, item, 'type')
      }
    })
  }

  private attachPassport() {
    this.popover.show(SelectImage).then(data => {
      if (data) {
        this.utilsService.thumbnailify(data, CONSTANTS.THUMBNAIL_SIZE, (thumbnail) => {
          this.passport = thumbnail
        })
        let item = { image: data, type: ImageType.PASSPORT }
        this.utilsService.addItemInArray(this.imagesProfile, item, 'type')
      }
    })
  }

  private get isUploadedImages() {
    return this.passport && this.licence
  }

  submit() {
    if(!this.isUploadedImages) {
      let alert = this.alert.createInfoAlert('Invalid Info', null, 'Please attach your license and passport')
      alert.present()
      return false;
    }
    if (this.imagesProfile && this.imagesProfile.length > 1) {
      this.gocarLoading.showLoading('Upload image...')
      this.uploadImage.doUpload(this.imagesProfile, this.uploadUrl, { userId: this.userData.id }).then((data) => {
        this.gocarLoading.hideLoading()
        this.imagesProfile = []
        this.authProvider.updateRequestDoccument({ id: this.userData.id }).subscribe(val => {
          this.userData = val.data;
          this.isPending = true;
        })
      }).catch(err => {
        this.imagesProfile = []
        this.licence = null;
        this.passport = null;
        this.gocarLoading.hideLoading()
      })
    }
  }
}



