import { Component, ViewChild, OnInit, Renderer, OnDestroy } from '@angular/core';
import { NavController, Events, Content, App } from 'ionic-angular';
import { LoadingComponent } from '@shared/components/loading/loading';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { CarFilterModal } from '../car-filter/car-filter';
import { NetWorkService } from '@providers/network/network.provider';
import { AlertBoxComponent } from '@shared/components/alert-box/alert-box';
import { BuidingsProvider } from '@providers/building/building';
import { CarsProvider } from '@providers/cars/cars';
import { AppEvent } from '@common/appEvent';
import { UtilsService } from '@providers/utils.service';
import { CarFilterMode } from '@shared/enums/car-filter-mode.enum'
import { BookCarPage } from '../book-car/book-car';
import { UserPage } from '../user/user';
import { GoCarToast } from '@shared/components/toast/gocar-toast';
import { AuthProvider } from '@providers/auth/auth';
import { ViewInfoComponent } from '@shared/components/view-info/view-info';
import { ReservationsProvider } from '@providers/reservations/reservations';
import { GlobalService } from '@providers/global.provider';
import { ReservationPage } from '../resevation/reservation/reservation';
import { FiltersProvider } from '@providers/fiters/filters';

@Component({
  selector: 'map-page',
  templateUrl: 'map-page.html'
})
export class MapPage implements OnInit, OnDestroy {
  @ViewChild(Content) content: Content;
  @ViewChild('mapview') mapView: any
  @ViewChild('list') list: any

  public isOpen: boolean = false
  public isLoadingCars = false
  public listBuilding = [];
  public cars = [];
  public currentBuildingName: string = ''
  public currentBuildingId: string = ''
  public pageInfo: any = {
    pagIndex: 0,
    itemsPerPage: 4
  }
  listExpired = []
  position : any
  constructor(public navCtrl: NavController,
    public events: Events,
    public netWorkService: NetWorkService,
    public loading: LoadingComponent,
    public alert: AlertBoxComponent,
    public utilService: UtilsService,
    public renderer: Renderer,
    public gocarToast: GoCarToast,
    public authService: AuthProvider,
    public gocarModal: GoCarModal,
    public buidingsProvider: BuidingsProvider,
    private carsProvider: CarsProvider,
    private reservationsProvider: ReservationsProvider,
    private app: App,
    private filtersProvider: FiltersProvider,
    private globalService: GlobalService) {
    // netWorkService.onDisconnect.subscribe(() => {
    //   let network = this.alert.createInfoAlert("No network connections")
    //   network.present()
    // })
    events.subscribe(AppEvent.TimeChanged, () => {
      this.loadBuilding()
      this.loadCars(this.currentBuildingId, 0, (this.pageInfo + 1) * this.pageInfo.itemsPerPage)
    })

    this.position =  {lat: Number(3.172322), lng:Number(101.70818)};
  }

  ngOnInit(){
    this.filtersProvider.validateTime();
  }

  private bookNow(car) {
    if (!this.utilService.isValidBookingTime()) {
      this.gocarToast.showToast({ duration: 2000, message: 'Invalid DateTime' })
    } else if (this.authService.isAuthenticated) {
      this.carsProvider.pickCar(car.building_id, car.carTypeId).subscribe((picked) => {
        this.navCtrl.push(BookCarPage, { car: picked });
      }, err => this.gocarToast.showToast({ message: err.message }))
    } else {
      this.gocarModal.showModal(UserPage)
    }

  }

  filter() {
    this.gocarModal.showModal(CarFilterModal, { mode: CarFilterMode.MAP })
  }

  ionViewDidEnter() {
    this.getListExpired();
    this.loadBuilding();
  }

  loadBuilding() {
    this.loading.showLoading();
    this.buidingsProvider.getListBuilding().subscribe((result) => {
      this.loading.hideLoading();
      this.listBuilding = result.data;
    }, err => {
      console.log(err)
      this.loading.hideLoading()
    })
  }

  onMarkerClick(building) {
    let buildingId = building.building_id
    this.currentBuildingName = building.name
    this.currentBuildingId = buildingId
    if (!this.isOpen) {
      this.isOpen = true
      this.renderer.setElementClass(this.list.nativeElement, 'show', true)
      this.renderer.setElementClass(this.list.nativeElement, 'hide', false)
      this.renderer.setElementStyle(this.list.nativeElement, 'height', '50%')
      this.renderer.setElementStyle(this.mapView.nativeElement, 'height', '50%')
    }
    this.loadCars(buildingId, 0)
  }

  private loadCars(buildingId, pagIndex?, limit?) {
    this.isLoadingCars = true
    this.cars = []
    this.carsProvider.getCars(null, null, buildingId).subscribe((result) => {
      this.cars = result.cars
      this.isLoadingCars = false
    }, err => {
      this.isLoadingCars = false
      console.log(err)
    })
  }

  eventBuilding(car) {
    this.gocarModal.showModal(ViewInfoComponent, { data: car })
  }

  onMapClick() {
    this.closeList()
  }

  closeList() {
    if (this.isOpen) {
      this.isOpen = false
      this.renderer.setElementClass(this.list.nativeElement, 'hide', true)
      this.renderer.setElementClass(this.list.nativeElement, 'show', false)
      this.renderer.setElementStyle(this.list.nativeElement, 'height', '0px')
      this.renderer.setElementStyle(this.mapView.nativeElement, 'height', '100%')
    }
  }

  ngOnDestroy(): void {
    this.events.unsubscribe(AppEvent.TimeChanged)
  }

  getListExpired() {
    if (this.authService.isAuthenticated) {
      this.reservationsProvider.getListExpired(this.authService.userId).subscribe((result) => {
        if (result.data) {
          this.listExpired = result.data
        }
      }, err =>
          console.log(err)
      )
    }
  }

  eventExpired() {
    this.globalService.setReservation(this.listExpired);
    this.app.getRootNav().setRoot(ReservationPage)
  }

}



