import { Component, ViewChild } from "@angular/core";
import { Nav, Events, ViewController, NavParams, IonicPage, NavController} from "ionic-angular"
import { BeforeTrip } from "./before-trip/before-trip";
import { DuringTrip } from "./during-trip/during-trip";
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { RentalDetailProvider } from '@providers/rental-detail/rental-detail';
import { LoadingComponent } from "@shared/components/loading/loading";
import { ReservationMode } from "@shared/enums/reservation-mode.enum";

@Component({
    selector: 'rentail-detail',
    templateUrl: 'rentail-detail.html'
})

export class RentalDetailPage {
    @ViewChild(Nav) userNav: Nav
    reservation: any
    service: any
    device: any
    mode: ReservationMode
    constructor(public events: Events,
                public viewCtrl: ViewController,
                public navParams: NavParams,
                public navCtrl: NavController,
                public goCarModal: GoCarModal,
                public gocarLoading: LoadingComponent,
                public rentalDetailProvider: RentalDetailProvider) {
        this.reservation = navParams.get('data')
        this.service = navParams.get('service')
        this.device = navParams.get('device')
        this.mode = navParams.get('mode')
    }

    ionViewDidEnter() {
        console.log('ionViewDidLoad', 'UserPage')
        this.gocarLoading.showLoading()
        this.rentalDetailProvider.getRentalDetailById(this.reservation.id).subscribe(rs => {
            this.gocarLoading.hideLoading()
            this.reservation = rs.data;
            if (this.reservation.status == 'DURING_TRIP' || this.reservation.status == 'END_TRIP') {
                this.goCarModal.showModal(DuringTrip, {data: this.reservation, service: this.service, mode: this.mode})
            } else {
                this.goCarModal.showModal(BeforeTrip, {data: this.reservation, service: this.service})
            }
        }, err => {
            this.gocarLoading.hideLoading()
        })  

    }

    
}


