import { Injectable } from "@angular/core";
import { TIMEVALUE } from "@shared/enums/time.enum";
import * as _ from 'lodash'

@Injectable()
export class ExtendTimeService {
    timeExtends = [];
    timeValue = TIMEVALUE;
    constructor() {
        this.timeExtends = [];
    }

    initExtendTime(time, priceHour, priceDay, priceMonth) {
        this.timeExtends = [];
        let i;
        for (i = 1; i <= time; i++) {
            let month = 0;
            let day = 0;
            let hour = 0;
            let totalPrice = 0;
            if ((i / this.timeValue.DAY) < 30) {
                day = _.toInteger(i / this.timeValue.DAY);
                hour = i % this.timeValue.DAY;
                totalPrice = day * priceDay + hour * priceHour;
            } else if ((i / this.timeValue.DAY) == 30) {
                month = _.toInteger(i / this.timeValue.MONTH);
                day = _.toInteger((i % this.timeValue.MONTH) / this.timeValue.DAY);
                hour = (i - (month * this.timeValue.MONTH) - (day * this.timeValue.DAY));
                totalPrice = month * priceMonth + day * priceDay + hour * priceHour;
            }
            let title = ' + ';
            if (month != 0) {
                if (month > 1) {
                    title += month + ' Months '
                } else {
                    title += month + ' Month '
                }
            }
            if (day != 0) {
                if (day > 1) {
                    title += day + ' Days '
                } else {
                    title += day + ' Day '
                }
            }
            if (hour != 0) {
                if (hour > 1) {
                    title += hour + ' Hours '
                } else {
                    title += hour + ' Hour '
                }
            }
            title += ' - ' + 'RM' + totalPrice.toFixed(2)
            let data = {
                title: title,
                value: i,
                status: 0,
                total_price: totalPrice
            }
            this.timeExtends.push(data)
        }
        return this.timeExtends;
    }
}


