import { Headers, Http, Response, RequestOptions } from '@angular/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { Observable, Observer } from 'rxjs/Rx';
import * as _ from 'lodash';
import { AppConfig } from '@app/app.config';
import { CONSTANTS } from '@common/constants';
import { AlertBoxComponent } from '@shared/components/alert-box/alert-box';
import { Events } from 'ionic-angular';
import { GlobalService } from './global.provider';
import { AppEvent } from '@common/appEvent';
import { GoCarToast } from '@shared/components/toast/gocar-toast';

@Injectable()
export class BaseService {

    OBJECT_ERROR = { "code": 400, "message": "No internet connection detected. Please check and try again." };

    constructor(protected http: Http, 
                protected config: AppConfig, 
                protected alertBox?: AlertBoxComponent,
                protected events?: Events,
                protected global?: GlobalService,
                protected toast?: GoCarToast
            ) { }

    protected getData(path: string): Observable<any> {
        return this.getToken()
            .flatMap((token) => this.getHeaders(token))
            .flatMap((options) => this.http.get(`${this.config.apiUrl}/${path}`, options))
            .map((res: Response) => res.json())
            .catch((err) => {
                return this.getError(err);
            });
    }

    protected postData(path: string, body?: any, headersPairs?: any): Observable<any> {
        return this.getToken()
            .flatMap((token) => this.getHeaders(token, headersPairs))
            .flatMap((options) => this.http.post(`${this.config.apiUrl}/${path}`, body, options))
            .catch((err) => {
                return this.getError(err);
            });
    }

    protected putData(path: string, body: any): Observable<any> {
        return this.getToken()
            .flatMap((token) => this.getHeaders(token))
            .flatMap((options) => this.http.put(`${this.config.apiUrl}/${path}`, body, options))
            .catch((err) => {
                console.error(`Put api error`);
                return this.getError(err);
            });
    }

    protected getError(err) {
        if(err.status == 403) {
           this.blockAccount()
           return Observable.throw(false)
        }

        // Invalid login
        if(err.status == 500) {
            this.invalidLogin(err)
            return Observable.throw(false)
        }

        if (!err.json().message) {
            return Observable.throw(this.OBJECT_ERROR)
        }
        return Observable.throw(err.json());
    }

    private getToken(): Observable<string> {
        return Observable.create((observer: Observer<string>) => {
            let token = localStorage.getItem(CONSTANTS.GOCAR_TOKEN);
            if (token) {
                observer.next(token);
                observer.complete();
            }
            observer.next(null);
            observer.complete();

        })
    }

    private invalidLogin(err) {
        this.toast.showToast({message: err.message})
        localStorage.removeItem(CONSTANTS.GOCAR_TOKEN)
        this.global.sethasReservations(false)
        this.events.publish(AppEvent.AppLoggedOut)
    }

    private blockAccount() {
        let alert = this.alertBox.createInfoAlert('Blocked', 'Your account is suspended. ', 'Please email hello@gocar.my for more details.')
        alert.present()
       alert.onDidDismiss(() => {
        localStorage.removeItem(CONSTANTS.GOCAR_TOKEN)
        this.global.sethasReservations(false)
        this.events.publish(AppEvent.AppLoggedOut)
       })
    }

    private getHeaders(token?: string, headersPairs?: any): Observable<RequestOptions> {
        return Observable.create((observer: Observer<RequestOptions>) => {
            const headers = new Headers();
            if (token) {
                headers.append(CONSTANTS.GOCAR_TOKEN, token);
            }
            headers.append('Content-Type', 'application/json');
            if (headersPairs) {
                _.forEach(headersPairs, (value, key) => {
                    headers.append(key, value);
                })
            }
            let options = new RequestOptions({ headers });
            observer.next(options);
            observer.complete();
        })
    }
}



