
import { Component, ViewChild, ElementRef } from "@angular/core";
import { Events } from 'ionic-angular';
import { FiltersProvider } from '@providers/fiters/filters';
import * as moment from 'moment';
import { CONSTANTS } from '@common/constants';
@Component({
    selector: 'select-time',
    templateUrl: 'select-time.html'
})

export class SelectTimePage {

    //hours:string =[];
    public hours: Array<any> = new Array();
    public hoursEnd: Array<any> = new Array();
    private filter: { startTime: any, endTime: any }

    @ViewChild('startTimeCol', { read: ElementRef }) public startTimeCol: ElementRef;
    @ViewChild('endTimeCol', { read: ElementRef }) public endTimeCol: ElementRef;

    constructor(
        private filtersProvider: FiltersProvider,
        public events: Events
    ) {
        this.events.publish('calendar:time');

        this.filter = {
            startTime: 0,
            endTime: 0
        }

    }

    ngOnInit(): void {
        this.generateHourList();
    }

    ngAfterViewInit() {
        let now = moment().minute(0).second(0)
        let startTime = moment(this.filtersProvider.getBookingTimeStart()).format(CONSTANTS.TIME_FORMAT);
        let timeNow = moment(now.format(CONSTANTS.TIME_FORMAT))
        let startTimeFormat = moment(startTime)
        if (startTimeFormat.isBefore(timeNow)) {
            this.filtersProvider.setStartTime(now.format(CONSTANTS.BOOKING_HOUR_FORMAT))
        }
        let startHour = this.filtersProvider.getStartTime();
        this.setStartTime(startHour);
        this.gotoStart(startHour);

        let endTime = moment(this.filtersProvider.getBookingTimeEnd()).format(CONSTANTS.TIME_FORMAT);
        let endTimeFormat = moment(endTime)
        if (endTimeFormat.isBefore(timeNow)) {
            this.filtersProvider.setEndTime(now.format(CONSTANTS.BOOKING_HOUR_FORMAT))
        }
        let endHour = this.filtersProvider.getEndTime();
        this.setEndTime(endHour);
        this.gotoEnd(endHour);
    }

    generateHourList() {

        let startDate = moment(this.filtersProvider.getStartDate());
        let endDate = moment(this.filtersProvider.getEndDate());
        let today = moment();
        var i = 0

        if (today.isSame(startDate, 'day')) {
            for (i = parseInt(today.format('H')); i < 24; i++) {
                if (i < 10)
                    this.hours.push({ "value": "0" + i + ":00", 'text': i + ":00" });
                else
                    this.hours.push({ "value": i + ":00", 'text': i + ":00" });
            }

        }
        else {
            for (i = 0; i < 24; i++) {
                if (i < 10)
                    this.hours.push({ "value": "0" + i + ":00", 'text': i + ":00" });
                else
                    this.hours.push({ "value": i + ":00", 'text': i + ":00" });
            }
        }

        if (today.isSame(endDate, 'day')) {
            for (i = parseInt(today.format('H')); i < 24; i++) {
                if (i < 10)
                    this.hoursEnd.push({ "value": "0" + i + ":00", 'text': i + ":00" });
                else
                    this.hoursEnd.push({ "value": i + ":00", 'text': i + ":00" });
            }
        }
        else {
            for (i = 0; i < 24; i++) {
                if (i < 10)
                    this.hoursEnd.push({ "value": "0" + i + ":00", 'text': i + ":00" });
                else
                    this.hoursEnd.push({ "value": i + ":00", 'text': i + ":00" });
            }
        }

    }

    setStartTime(h) {
        if (h != null) {
            this.filter.startTime = h;
            this.filtersProvider.setStartTime(h);
        }
    }

    setEndTime(h) {
        if (h != null) {
            this.filter.endTime = h;
            this.filtersProvider.setEndTime(h);
        }
    }

    gotoStart(h) {
        console.log(h);
        console.log(document.getElementById('start_' + h));
        let yOffset = document.getElementById('start_' + h).offsetTop;
        this.startTimeCol.nativeElement.scrollTop += (yOffset);
    }

    gotoEnd(h) {
        console.log(h);
        console.log(document.getElementById('end_' + h));
        let yOffset = document.getElementById('end_' + h).offsetTop;
        this.endTimeCol.nativeElement.scrollTop += (yOffset);
    }


    ionViewDidLoad() {
    }
}


