import { Injectable } from "@angular/core";
import { NativeStorage } from "@ionic-native/native-storage";
import _ from 'lodash';

@Injectable()
export class NativeStorageService {
    constructor(private nativeStorage: NativeStorage) {

    }

    public setData(key, data) {
        return this.nativeStorage.setItem(key, JSON.stringify(data)).then(data => {
            return data;
        }).catch(err => {
            console.error('Error storing item', err)
        })
    }

    public getData(key) {
        return this.nativeStorage.getItem(key).then(data => {
            return data;
        }).catch(err => {
            console.error('Error storing item', err)
        })
    }

    async getKey(key) {
        let keys = await this.nativeStorage.keys()
        if (keys.length > 0) {
            let val = _.find(keys, (o) => {
                return o === key;
            })
            return val
        } else {
            return null
        }

    }
}


