import { Http, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { BaseService } from '../base.service';
import { AppConfig } from '@app/app.config';
import { Observable } from 'rxjs';
import { GlobalService } from '../global.provider';
import { AuthProvider } from '../auth/auth';
import { AlertBoxComponent } from '@shared/components/alert-box/alert-box';
import { Events } from 'ionic-angular';

export abstract class PromoCodeOptions {
  code?: any
  userId?: any
  buildingId?: any
  startTime?: any
  endTime?: any
  totalPrice?: any
  buildingCity?: any
  buildingName?: any
  useGocarCredit?: boolean
}

/*
  Generated class for the ReservationsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ReservationsProvider extends BaseService {

  constructor(public http: Http,
    public global: GlobalService,
    public auth: AuthProvider,
    public events: Events,
    public alertBox: AlertBoxComponent,
    protected appConfig: AppConfig) {
    super(http, appConfig, alertBox, events, global);
    console.log('Hello ReservationsProvider Provider');
  }

  getListReservations(userId): Observable<any> {
    return this.getData(`reservations/list-reservation/${userId}`)
      .catch((err) => {
        console.log(err);
        return Observable.throw(false);
      });
  }
  getListReservationsReturn(userId): Observable<any> {
    return this.getData(`reservations/list-reservation-return/${userId}`)
      .catch((err) => {
        console.log(err);
        return Observable.throw(false);
      });
  }
  getListExtendTime(id): Observable<any> {
    return this.getData(`reservations/list-extend-time/${id}`)
      .catch((err) => {
        console.log(err);
        return Observable.throw(false);
      });
  }
  updateReservation(body): Observable<any> {
    return this.postData(`reservations/extend-time`, body)
      .map((res: Response) => res.json())
      .catch((err) => {
        console.log(err);
        return Observable.throw(err);
      });
  }

  changeReservation(body): Observable<any> {
    return this.postData(`reservations/change`, body)
      .map((res: Response) => res.json())
      .catch((err) => {
        console.log(err);
        return Observable.throw(err);
      });
  }

  saveReservation(body): Observable<any> {
    return this.postData(`reservations/save`, body)
      .map((res: Response) => res.json())
      .catch((err) => {
        console.log(err)
        return Observable.throw(err)
      })
  }

  checkValidPromoCode(body: PromoCodeOptions): Observable<any> {
    return this.postData('promo-code/verify', body)
      .map((res: Response) => res.json())
      .catch((err) => {
        return Observable.throw(err)
      })
  }

  getListExpired(userId): Observable<any> {
    return this.getData(`reservations/list-expired/${userId}`)
      .catch((err) => {
        console.log(err);
        return Observable.throw(false);
      });
  }

  count(): Observable<any> {
    if(!this.auth.isAuthenticated) return Observable.throw(new Error('Unauthorize'))
    let userId = this.auth.userId
    return this.getData(`reservations/count/${userId}`).catch(err => Observable.throw(err))
  }

  holdReservation(body) : Observable<any>{
    return this.postData(`hold_reservations/hold`, body)
      .map((res: Response) => res.json())
      .catch((err) => {
        console.log(err)
        return Observable.throw(err)
      })
  }

}



