import { Component } from '@angular/core';
import { Events, ViewController, NavController, NavParams, AlertController } from 'ionic-angular';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { ReportDamagePage } from '../report-damage/report-damage';
import { RentalDetailProvider } from '@providers/rental-detail/rental-detail';
import { ViewInfoComponent } from '@shared/components/view-info/view-info';
import { SmsService } from '@providers/sms/sms.provider';
import * as moment from 'moment';
import { CONSTANTS } from "@common/constants";

@Component({
  selector: 'before-trip',
  templateUrl: 'before-trip.html',
})
export class BeforeTrip {
  position: any
  reservation: any
  buildingName: any
  step: any = 1;
  duaration: any = ''
  registration: any = ''
  service: any
  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private goCarModal: GoCarModal,
              public events: Events,
              public viewCtrl: ViewController,
              public rentalDetailProvider: RentalDetailProvider,
              public smsService: SmsService,
              private alertCtrl: AlertController,) {
    this.step = 1;
  }

  ionViewDidLoad() {
    this.reservation = this.navParams.get('data')
    this.service = this.navParams.get('service')
    this.calculateTime();
    this.position = {lat: Number(this.reservation.building_lat), 
                      lng: Number(this.reservation.building_lng)}
    this.buildingName = this.reservation.building_name;
    this.registration = this.reservation.registration;
  }

  startTrip() {
    let now = new Date().getTime();
    let start = new Date(moment.utc(this.reservation.start_time).format(CONSTANTS.RENTALDETAIL_TIME_FORMAT)).getTime();
    let timeRangeAllow = 10 * 60 * 1000;
    if (this.reservation.status == 'BEFORE_TRIP' && (start - now > timeRangeAllow)) {
      this.alertTimeError();
    } else {
      this.unlockSms();
      this.goCarModal.showModalV2(ReportDamagePage, { data: this.reservation, service: this.service})
    }
  }

  unlockSms(){
    this.smsService.unlockCar(this.reservation.id).subscribe(val => {
      console.log(val)
    }, err => {
      console.log(err)
    })
  }

  // showBuilding(event) {
  //   this.goCarModal.showModalV2(ViewInfoComponent, { data: event, service: this.service})
  //   this.goCarModal.currentModal.onDidDismiss(data => {
  //     this.navCtrl.setRoot(BeforeTrip, { data: this.reservation })
  //   })
  // }

  back() {
    this.viewCtrl.dismiss(this.reservation);
  }
  
  calculateTime() {
    let dateStart = new Date(this.reservation.start_time);
    let dateEnd = new Date(this.reservation.end_time);
    let remain = dateEnd.getTime() - dateStart.getTime()
    let day = 0;
    let hour = 0;
    let minute = 0;
    let sec = 0;
    if (remain >= 86400000) {
        day = Math.floor(remain / 86400000)
        this.duaration += day + ' day ';
    }
    remain = remain - day * 86400000;
    if (remain >= 3600000) {
        hour = Math.floor(remain / 3600000)
        this.duaration += hour + ' hour ';
    }
    remain = remain - hour * 3600000;
    if (remain >= 60000) {
        minute = Math.floor(remain / 60000)
        this.duaration += minute + ' minute ';
    }
  }

  showInfor () {
    this.goCarModal.showModalV2(ViewInfoComponent, {data: this.reservation, componentPrevious: 'BeforeTrip', service: this.service})
  }

  alertTimeError() {
    let alert = this.alertCtrl.create({
      title: 'Unable to Start',
      message: 'You can only start your trip 10 minutes before the stipulated start time.',
      buttons: [
        {
          text: 'OK',
          role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }
}



