import { Http } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { BaseService } from '../base.service';
import { AppConfig } from '@app/app.config';

/*
  Generated class for the WalletProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class WalletProvider extends BaseService {

  constructor(public http: Http, protected appConfig: AppConfig) {
    super(http, appConfig);
    console.log('WalletProvider Provider');
  }

  getWalletInfoByUser() {
    return this.getData(`wallet/list`)
      .catch((err) => {
        console.log(err);
        return Observable.throw(false);
      });
  }

  getWalletTransactionInfoByUser() {
    return this.getData(`wallet/transaction`)
      .catch((err) => {
        console.log(err);
        return Observable.throw(false);
      });
  }

  public qrScanner(data): Observable<any> {
    return this.postData(`wallet/qrScanner`, data)
               .map((res: Response) => res.json())
               .catch(err => Observable.throw(err))
  }

}



