import { Component } from '@angular/core';
import { Events, ViewController, NavController, NavParams, AlertController } from 'ionic-angular';
import { GoCarModal } from '@shared/components/modal/gocar-modal';
import { RentalDetailProvider } from '@providers/rental-detail/rental-detail';
import { ViewInfoComponent } from '@shared/components/view-info/view-info';
import { SmsService } from '@providers/sms/sms.provider';
import { GoCarToast } from '@shared/components/toast/gocar-toast';

@Component({
  selector: 'after-trip',
  templateUrl: 'after-trip.html',
})
export class AfterTrip {
  position: any
  reservation: any
  buildingName: any
  rate: 0;
  pinCode: any
  service: any
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private goCarModal: GoCarModal,
    public events: Events,
    public viewCtrl: ViewController,
    public rentalDetailProvider: RentalDetailProvider,
    private alertCtrl: AlertController,
    private smsService: SmsService,
    public gocarToast: GoCarToast, ) {
    this.reservation = navParams.get('data')
    this.service = this.navParams.get('service')
    this.position = {
      lat: Number(this.reservation.building_lat),
      lng: Number(this.reservation.building_lng)
    }
    this.buildingName = this.reservation.building_name;
    this.pinCode = this.reservation.unlock_code_end_id;

  }

  ionViewDidLoad() {
  }

  back() {
    this.viewCtrl.dismiss(this.reservation)
  }

  close() {
    this.reservation.rate = this.rate
    this.presentConfirm();

  }

  showInfor() {

    this.goCarModal.showModalV2(ViewInfoComponent, { data: this.reservation, componentPrevious: 'AfterTrip', service: this.service })
  }

  presentConfirm() {
    let alert = this.alertCtrl.create({
      title: 'End Trip',
      cssClass: 'call',
      message: 'You are about to end the reservation. Please take note of the following: <ul><li>The car will lock once you tap End Trip.</li><li> Make sure to take all your belongings.</li><li> Switch off the cars headlights and interior lights.</li></ul>',
      buttons: [
        {
          text: 'Cancel',
          cssClass: 'alert-cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'End Trip',
          handler: () => {
            this.lockSms();
            this.rentalDetailProvider.updateStatusRentalDetail(this.reservation, 'END_TRIP').subscribe(rs => {
              this.rentalDetailProvider.getRentalDetailById(this.reservation.id).subscribe(rs => {
                this.service.initData();
                this.gocarToast.showToast({ message: 'Thank you for using GoCar.' })
                this.viewCtrl.dismiss(this.reservation)
              })
            })
          }
        }
      ]
    });
    alert.present();
  }

  lockSms() {
    this.smsService.lockCar(this.reservation.id).subscribe(val => {
      this.smsService.resetCar(this.reservation.id).subscribe(res => {
      })
    }, err => {
      console.log(err)
    })
  }
}



